<template lang="pug">
  section
    tk-header-admin
    .content
      .container
        .menu
          .row
            .col-sm-6.text-left
              .menu-content
                .menu-body
                  h5  Step 2 of 3
                  h2 Your Online Shop
                  p Setting the basic preferrences of your shop
            .col-sm-6.text-right
              .menu-content
                .menu-body
                  button(@click='skip').action Skip this step
                  tk-primary-button(name='next step - team', @click='validateBeforeSubmit')
        main
          .shop-identity
            .row
              .col-sm-8.col-sm-offset-2
                .shop-content
                  h4 Shop Identity
                  p Set up the online identity of your shop
                  .row
                    .col-sm-5.col-sm-offset-1
                      p(style='margin-top: 40px;') * Shop name
                    .col-sm-6
                      span.count {{ info.name.length }} / 50
                      tk-input-type-text(name='name', placeholder='name', data-vv-value-path='text', style='margin-bottom: 24px;', maxlength='50', v-model='info.name', v-validate="'required|alpha_spaces'", :has-error="errors.has('name')", data-vv-validate-on="none")
                      p(v-if="errors.has('name')").error Shop name is required
                      button.action.action-main(v-if='!show', @click='shop') I have a shop logo
                      button.action.action-main(v-if='show', @click='shop') I don't have a shop logo
                      transition(name='slide-fade')
                        .logo-content(v-if='show')
                          .logo-body
                            img.doc-types(src='/static/doc_types.svg')
                            h4 Drag And Drop
                            p 
                              strong Shop logo 
                              | min 170x50 px
                            tk-secondary-button(name='drag and drop')
                      // input(type='checkbox', name='logo', id="shop-logo", v-model='info.several_shops')
                          label(for="shop-logo") I will be setting up several shops
                  .row
                    .col-sm-5.col-sm-offset-1
                      p.shop-domain * Shop Domain
                    .col-sm-6
                      .domain-length
                        span {{ info.domain.length }} / 24
                      tk-input-type-text-domain(name='domain', data-vv-value-path='text', style='margin-bottom: 24px;', v-model='info.sub_domain', placeholder='my domain name', v-validate="'required|alpha_num'", :has-error="errors.has('domain')", data-vv-validate-on="none")
                      p(v-if="errors.has('domain')").error Shop Domain is required
                      // button(style='margin-bottom: 0;').action.action-main I have my own domain
          .shop-settings
            .row
              .col-sm-8.col-sm-offset-2
                h4 Shop Settings
                p Adjust your shop settings according to what your customers prefferences
                .row
                  .col-sm-5.col-sm-offset-1
                    p.top Shop Default Language
                  .col-sm-6
                    tk-custom-select-string(:options='languages', style='margin-bottom: 24px;', v-model='info.default_language')
                    tk-modal-shop-languages(style='margin-bottom: 24px;')
                    span.pull-right 3 Selected
                .row
                  .col-sm-5.col-sm-offset-1
                    p.top Shop Default Currency
                  .col-sm-6
                    tk-custom-select-string(:options='currencies', style='margin-bottom: 24px;', v-model='info.default_currency')
                    tk-modal-shop-currencies(style='margin-bottom: 32px;')
                    span.pull-right 3 Selected
                p(style='margin-bottom: 36px;') Select all payment methods you need on your shop
                .row
                  .col-sm-5.col-sm-offset-1
                    p Payment Gateways
                  .col-sm-6
                    form
                      template(v-for='details in merchant_details.allowed_gateways')
                        input(type='checkbox', name='allowed_gateways', :id='details.id', :value='details.id', v-model='info.allowed_gateways')
                        label(:for='details.id')
                          img(:src='"static/" + details.translations.logo.en')
                          | {{ details.code }}
                .calculator
                  h4 Example Event Cauculator
                  p Estimate based on the selected payment gateways
                  .row
                    .col-sm-2
                      tk-input-type-text(placeholder='100', style='margin-bottom: 8px;', v-on:keypress="isNumber")
                    .col-sm-5
                      p(style='margin-top: 12px; font-size: 18px;') tickets sold at average price of
                    .col-sm-2
                      tk-input-type-text-admin-currency(placeholder='50.00', style='margin-bottom: 8px;', v-on:keypress="isNumber")
                    .col-sm-3
                      p(style='margin-top: 12px; font-size: 18px;') results in a 
                  p.height(style='margin-bottom: 0px;') bruto event income of 
                    strong 5000$. 
                    | Of this amount the payment gateway fees average out to 
                    strong 94$*
                    | , Ticketyl fees are 
                    strong 100$ 
                    | (total fees - 
                    strong 3.8%
                    | ) which means your actual earnings are 
                    strong 4305$.
                .row
                  .col-sm-11.col-sm-offset-1
                    p(style='font-size: 12px;') * We assume that all payment gateways will process an equal amount income
    tk-footer-admin-white(:active='2', name='next step - team', @click='validateBeforeSubmit')
</template>

<script>
  import * as types from '@/store/mutation-types'
  import store from '@/store'
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        info: {
          name: '',
          logo: '',
          several_shops: '',
          sub_domain: '',
          domain: 'ticketyl.com',
          default_language: 'eng',
          default_currency: 'eur',
          allowed_gateways: []
        },
        show: false,
        currencies: [ 'Euro', 'USD' ],
        languages: [ 'English', 'Latvian' ]
      }
    },
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    },
    methods: {
      isNumber (e) {
        var iKeyCode = (e.which) ? e.which : e.keyCode
        if (iKeyCode !== 46 && iKeyCode > 31 && (iKeyCode < 48 || iKeyCode > 57)) {
          e.preventDefault()
          return false
        } else {
          this.customGiftCard.name = 'Custom Gift Card'
          this.customGiftCard.amount = 1
          this.customGiftCard.code = 'eur'
          return true
        }
      },
      validateBeforeSubmit () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            this.$store.commit(types.ADD_SHOP, this.info)
            this.$router.push({name: 'Team'})
            return
          }
        })
      },
      skip () {
        this.$router.push({name: 'Team'})
      },
      shop () {
        if (this.show === false) {
          this.show = true
        } else {
          this.show = false
        }
      }
    },
    mounted () {
      this.$store.dispatch('fetch_merchant', this.token)
    },
    computed: {
      ...mapGetters({
        merchant_details: 'merchant_details',
        token: 'logInAdminToken'
      })
    }
  }
</script>

<style lang="less" scoped>
.shop-domain{
  margin-top: 40px;
}
.domain-length{
  font-size: 12px;
  text-align: right;
  margin-bottom: 8px;
}
.top{
  margin-top: 14px;
}
.count{
  font-size: 12px;
  margin-bottom: 8px;
  float: right;
}
.shop-content{
  padding-bottom: 41px;
  margin-bottom: 27px;
  border-bottom: 1px solid #979797;
}
.logo-content{
  margin-bottom: 37px;
}
.action-main{
  margin-bottom: 16px;
}
h4{
  margin-top: 0;
  +p{
    margin-bottom: 20px;
  }
}
.height{
  font-size: 18px;
  line-height: 40px;
}
.content{
  padding-bottom: 120px;
}
form{
  margin-bottom: 40px;
}
.calculator{
  padding: 24px 32px;
  border: 1px solid #979797;
  margin-bottom: 27px;
  h4{
    margin: 0 0 18px 0;
    +p{
      margin-bottom: 24px;
    }
  }
}
form{
  .doc-types{
    height: 56px;
    width: auto;
    margin-bottom: 24px;
  }
  img{
    height: 22px;
    width: 22px;
    margin-right: 8px;
  }
}
.logo-content{
  padding: 4px;
  text-align: center;
  color: #9a9a9a;
  background-color: #fff;
  display: table;
  height: 356px;
  width: 100%;
  .logo-body{
    border: 4px dotted #f7f7f7;
    display: table-cell;
    vertical-align: middle;
  }
}
button{
  outline: 0;
}
.action{
  padding: 0;
  border: none;
  text-transform: uppercase;
  text-decoration: underline;
  background-color: transparent;
}
.menu{
    margin-bottom: 20px;
    border-bottom: 1px solid #979797;
  .menu-content{
      display: table;
      height: 150px;
      width: 100%;
    .menu-body{
      display: table-cell;
      vertical-align: middle;
      h5{
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 8px;
      }
      h2{
        margin: 0 0 16px 0;
      }
      p{
        font-weight: bold;
        margin: 0;
      }
      button{
        margin-left: 48px;
        &:first-child{
          margin: 0;
        }
      }
    }
  }              
}
.error{
  font-size: 12px;
  margin-top: -24px !important;
  font-weight: bold;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s ease;
}
.slide-fade-enter, .slide-fade-leave-to{
  transform: translateX(10px);
  opacity: 0;
}
</style>



