<template lang="pug">
  main
    .loading-screen(v-if='!series || this.$route.name !== "EventList"')
      .container
        .loading
          .spinner
            .rect1
            .rect2
            .rect3
            .rect4
            .rect5
    section(v-else)
      //- .container
          .row
            .col-sm-4(v-for = '(e, index) in series')
              .small-piles
                .small-piles-content(v-bind:style="{ 'background-image': 'url(static/' + e.translations.image.en + ')' }")
                  .small-piles-body
                    .small-event
                      span Salzburg 2017
                      span Dec 16
      .divs(v-for = '(serie, index) in series')
        .event-list
          router-link(:to="routerData(serie)")
            .event-list-wrapper(v-bind:style="{ 'background-image': 'url(/static/' + serie.translations.logo.en + ')' }")
              .event-list-body
                .event
                  span {{ serie.translations.name | tktranslate(lang) }}
                  span {{ formatDate(serie.acts_date_range.start.date ) }} | {{ serie.acts_price_range.min | tkprice }} +
        //- .gift-card(v-if="index === Math.floor(series.length/2)")
          .container
            .row
              .col-sm-5.col-sm-push-7
                .gift-card-wrapper
                  .gift-card-body
                    h2 {{ translations.gift_card_title[defaultLang] | translate }}
                    p.hidden-xs {{ translations.gift_card_paragraph[defaultLang] | translate }}
                    p.hidden-sm.hidden-md.hidden-lg {{ translations.gift_card_mob_paragraph[defaultLang] | translate }}
                    img(:src='translations.gift_card_image[defaultLang] | translate', :alt='translations.gift_card_image_alt[defaultLang] | translate').img-responsive.margin-bottom.hidden-sm.hidden-md.hidden-lg
                    tk-modal-gift-card(:name="translate(translations.gift_card_button[defaultLang])", :translations='translations', @toCheckout='checkout')       
              .col-sm-7.col-sm-pull-5.hidden-xs
                .gift-card-wrapper
                  .gift-card-body
                    img(:src='translations.gift_card_image[defaultLang] | translate', :alt='translations.gift_card_image_alt[defaultLang] | translate').img-responsive
</template>

<script>
  import moment from 'moment'
  import { mapGetters } from 'vuex'

  export default {
    props: ['translations'],
    computed: {
      ...mapGetters({
        lang: 'selectedLanguage',
        series: 'series'
      })
    },
    methods: {
      checkout () {
        this.$router.push({name: 'Checkout'})
      },
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      routerData (e) {
        return {name: 'Event', params: {title: e.translations.name.en, id: e.id}}
      },
      formatDate (date) {
        return moment(date).format('MMM D')
      }
    }
  }
</script>

<style lang="less" scoped>
.small-piles{
  text-align: center;
  .small-piles-content{
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: table;
    height: 240px;
    width: 100%;
    margin-bottom: 31px;
    .small-piles-body{
      background: rgba(0, 0, 0, 0.3);
      display: table-cell;
      vertical-align: middle;
      .small-event{
        width: 80%;
        margin: auto;
        border: 3px solid #fff;
        padding: 24px 8px;
        span{
          color: #fff;
          display: block;
          &:first-child{
            font-size: 24px;
            font-weight: bold;
          }
          &:last-child{
            font-size: 14px;
            font-weight: bold;
            letter-spacing: 1px;
          }
        }
      }
    }
  }           
}
  .margin-bottom{
    margin-bottom: 24px;
  }
  .loading{
    position: relative;
    background-color: #f7f7f7;
    height: 80vh;
    width: 100%;
    margin: auto;
    .spinner{
      position: absolute;
      top: 15%;
      display: table-cell;
      vertical-align: middle;
    }
    img{
      height: 32px;
      position: absolute;
      bottom: 40px;
      left: 0;
    }
  }
  .gift-card{
    background-color: #fff;
    margin-bottom: 64px;
    border-top: 1px solid #979797;
    border-bottom: 1px solid #979797;
    .gift-card-wrapper{
      display: table;
      height: 500px;
      width: 100%;
      padding: 24px 0;
      .gift-card-body{
        display: table-cell;
        vertical-align: middle;
        h2{
          width: 300px;
          margin-top: 0;
          margin-bottom: 24px;
        }
        p{
          width: 347px;
          margin-bottom: 32px;
        }
      }
    }       
  }
  .event-list{
    .event-list-wrapper{
      position: relative;
      display: table;
      height: 552px;
      width: 100%;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      margin-bottom: 64px;
      .event-list-body{
        background: rgba(0, 0, 0, 0.3);
        display: table-cell;
        vertical-align: middle;
        text-align: center;
        padding: 20px;
        .event{
          padding: 16px 24px;
          color: #fff;
          min-width: 40%;
          border: none;
          background-color: transparent;
          border: 3px solid #fff;
          text-decoration: none;
          display: inline-block;
          span{
            display: block;
            &:first-child{
              text-transform: capitalize;
              font-size: 56px;
              font-weight: bold;
            }
            &:last-child{
              font-size: 14px;
              font-weight: bold;
              letter-spacing: 1px;
            }
          }
        }
      }
    }
  }
  .spinner {
    position: relative;
    margin: 100px auto;
    width: 100%;
    height: 80px;
    text-align: center;
    font-size: 10px;
    img{
      height: 40px;
      position: absolute;
      left: 40px;
      bottom: 40px;
    }
  }
.spinner > div {
  background-color: #444;
  height: 100%;
  width: 8px;
  margin-right: 4px;
  display: inline-block;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}
.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.spinner .rect3 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
.spinner .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.spinner .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
@-webkit-keyframes sk-stretchdelay {
  0%, 40%, 100% { -webkit-transform: scaleY(0.4) }  
  20% { -webkit-transform: scaleY(1.0) }
}
@keyframes sk-stretchdelay {
  0%, 40%, 100% { 
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }  20% { 
    transform: scaleY(1.0);
    -webkit-transform: scaleY(1.0);
  }
}
/* Media Queries */
/* Extra Small */
@media(min-width:0px) and (max-width:767px){
  .event-list{
    .event-list-wrapper{
      margin-bottom: 24px;
      height: 240px;
      padding: 0;
      .event-list-body{
        padding: 56px 15px;
        .event{
          width: 100% !important;
          padding: 8px 0;
          span{
            display: block;
            &:first-child{
              font-size: 32px !important;
            }
            &:last-child{
              font-size: 14px !important;
            }
          }
        }
      }
    }
  }
  .gift-card{
    padding: 0 0 24px 0;
    margin-bottom: 24px;
    .gift-card-wrapper{
      display: inherit;
      height: auto;
      .gift-card-body{
        display: inherit;
        img{
          display: block;
          height: 150px;
          margin: 0 auto;
          padding: 0 32px;
          margin: 24px auto 18px auto;
        }
        h2{
          font-size: 20px;
          margin-bottom: 8px;
        }
        p{
          width: auto;
          margin-bottom: 24px;
        }
        button{
          width: 100%;
        }
      }
    }
  }
   .loading{
    height: 70vh;
    img{
      bottom: 32px;
    }
  }
}
/* Small */
@media(min-width:768px) and (max-width:991px){
  .event-list .event-list-wrapper .event-list-body a {
    width: 80%;
  }
  .event-list .event-list-wrapper {
    height: 300px;
  }
  .gift-card .gift-card-wrapper .gift-card-body img {
    display: block;
    margin: 0 auto;
    height: 200px;
  }
  .gift-card .gift-card-wrapper .gift-card-body p {
    width: 250px;
  }
  .gift-card .gift-card-wrapper {
    height: 358px;
  }
}
/* Medium */
@media(min-width:992px) and (max-width:1199px){
  .gift-card .gift-card-wrapper .gift-card-body img {
    display: block;
    margin: 0 auto;
    height: 200px;
  }
}
/* Large */
@media(min-width:1200px){
  .gift-card .gift-card-wrapper .gift-card-body img {
    display: block;
    margin: 0 auto;
    height: 250px;
  }
}
</style>



