import * as types from '../mutation-types'

const state = {
  all_events: [],
  tickets: [],
  editable_ticket: null,
  subevents: [],
  special_pricing: []
}
const mutations = {
  [types.UPDATE_SUBEVENT_MAP] (state, data) {
    state.subevents[data.index].show_map = data.checked
  },
  [types.UPDATE_SUBEVENT_LOCATION] (state, data) {
    state.subevents[data.index].location = data.value
  },
  [types.ADD_SUBEVENT] (state, e) {
    state.subevents.push(e)
  },
  [types.DELETE_SUBEVENT] (state, i) {
    state.subevents.splice(i, 1)
  },
  [types.SET_TICKET_EDITABLE] (state, i) {
    state.editable_ticket = state.tickets[i]
  },
  [types.ADD_EVENT] (state, s) {
    state.all_events.push(s)
  },
  [types.ADD_EVENT_TICKET] (state, s) {
    state.tickets.push(s)
  },
  [types.DELETE_ADMIN_EVENT] (state, index) {
    state.events.splice(index, 1)
  },
  [types.DUPLICATE_ADMIN_EVENT] (state, index) {
    var currentEvent = state.events[index]
    var duplEvent = Object.assign({}, currentEvent)
    state.events.push(duplEvent)
  },
  [types.DELETE_ADMIN_TICKETS] (state, index) {
    state.tickets.splice(index, 1)
  },
  [types.DUPLICATE_ADMIN_TICKET] (state, index) {
    var findEvent = state.tickets[index]
    var duplicateEvent = Object.assign({}, findEvent)
    state.tickets.push(duplicateEvent)
  },
  [types.UPDATE_EVENT_TICKET_NAME] (state, data) {
    state.tickets[data.index].name = data.value
  },
  [types.UPDATE_EVENT_TICKET_QUANTITY] (state, data) {
    state.tickets[data.index].quantity = data.value
  },
  [types.UPDATE_EVENT_TICKET_PRICE] (state, data) {
    state.tickets[data.index].price = data.value
  }
}
const getters = {
  edit_ticket: state => {
    return state.editable_ticket
  },
  all_events: state => {
    return state.all_events
  },
  all_tickets: state => {
    return state.tickets
  },
  subevents: state => {
    return state.subevents
  }

}
export default {
  state,
  getters,
  mutations
}



