import * as types from '../mutation-types'

const state = {
  shop_preferences: {},
  shop: null,
  serie: null,
  series: null,
  packages: null,
  package: null,
  routes: null,
  order: null
}
const mutations = {
  [types.ADD_SHOP_PREFERENCES] (state, s) {
    state.shop_preferences = s
  },
  [types.SET_SHOP] (state, item) { state.shop = item },
  [types.SET_SERIE] (state, item) { state.serie = item },
  [types.SET_PACKAGE] (state, item) { state.package = item },
  [types.SET_ORDER] (state, item) { state.order = item },
  [types.SET_ROUTES] (state, items) { state.routes = items },
  [types.SET_SERIES] (state, items) { state.series = items },
  [types.SET_PACKAGES] (state, items) { state.packages = items }
}
const getters = {
  shop: state => { return state.shop },
  serie: state => { return state.serie },
  package: state => { return state.package },
  routes: state => { return state.routes },
  packages: state => { return state.packages },
  series: state => { return state.series },
  order: state => { return state.order },
  shop_data: state => {
    return {
      'name': state.shop_preferences.name,
      'logo': state.shop_preferences.logo,
      'domain': state.shop_preferences.domain,
      'default_language': state.shop_preferences.default_language,
      'default_currency': state.shop_preferences.default_currency,
      'allowed_gateways': state.shop_preferences.allowed_gateways,
      'layout': state.shop_preferences.layout,
      'giftcards_enabled': state.shop_preferences.giftcards_enabled,
      'giftcard_banner': state.shop_preferences.giftcard_banner,
      'charges': state.shop_preferences.charges
    }
  }
}
export default {
  state,
  mutations,
  getters
}



