<template lang="pug">
  section
    .container
      .row
        .col-xs-12.col-sm-4.col-sm-push-8
          #aside
            nav
              p {{ translations.privacy_menu_title[defaultLang] | translate }}
              ul
                li
                  router-link(:to="{'name': 'Privacy'}").active {{ translations.privacy_menu_link_1[defaultLang] | translate }}
                li
                  router-link(:to="{'name': 'Imprint'}") {{ translations.privacy_menu_link_2[defaultLang] | translate }}
                li 
                  router-link(:to="{'name': 'Contact'}") {{ translations.privacy_menu_link_3[defaultLang] | translate }}
                li 
                  router-link(:to="{'name': 'Terms'}") {{ translations.privacy_menu_link_4[defaultLang] | translate }}
                li
                  router-link(:to="{'name': 'About'}") {{ translations.privacy_menu_link_6[defaultLang] | translate }}
        .col-xs-12.col-sm-8.col-sm-pull-4
          h1 {{ translations.privacy_title[defaultLang] | translate }}
          p(style="white-space: pre-line") {{ translations.privacy_paragraph_1[defaultLang] | translate }}
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: ['translations'],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')
      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '72') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    }
  }
</script>

<style lang="less" scoped>
.router-link-exact-active{
  font-weight: bold !important;
}
.active{
  font-weight: bold !important;
}
section{
  margin-top: 32px;
  h1{
    margin-bottom: 32px;
  }
  h4{
    margin-bottom: 24px;
    text-transform: uppercase;
  }
  p:last-child{
    margin-bottom: 80px;
  }
  nav{
    background-color: #f7f7f7;
    padding: 24px 16px;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    p{
      text-transform: uppercase;
      color: #9a9a9a;
      margin-bottom: 24px;
    }
    ul{
      text-decoration: none;
      padding: 0;
      list-style-type: none;
      li{
        margin-bottom: 16px;
        &:last-child{
          margin: 0;
        }
      }
      a{
        cursor: pointer;
        text-transform: capitalize;
        text-decoration: none;
        &:hover{
          color: #292929;
        }
      }
    }
  }
}
.fixed{
  z-index: 9999;
  position: fixed;
  width: 360px;
  top: 32px;
}
@media(min-width:0px) and (max-width:767px){
  section{
    margin-top: 16px;
    nav{
      margin-bottom: 16px;
    }
    h1{
      margin-bottom: 16px;
    }
  }
}
</style>



