import * as types from '../mutation-types'
import moment from 'moment'

const state = {
  series: [],
  seriesId: 0,
  events: [],
  selectedEvent: 0
}
const mutations = {
  [types.SET_SERIES] (state, s) {
    state.series = s
  },
  [types.SET_SERIESID] (state, s) {
    state.seriesId = s
  },
  [types.SET_EVENTS] (state, e) {
    state.events = e
  },
  [types.GET_SELECTEDEVENT] (state, e) {
    state.selectedEvent = e
  }
}
const getters = {
  getSeries: state => {
    return state.series
  },
  getSeriesId: state => {
    return state.seriesId
  },
  getEventDates: state => {
    return state.events.map(function (event) {
      return moment(event.datetime.date)
    })
  },
  getEvents: state => {
    return state.events
  }
}
export default {
  state,
  mutations,
  getters
}



