<template lang="pug">
  section
    tk-header-admin
    tk-modal-ticket-settings
    .wrapper
      .container
        #setup
          .row
            .col-sm-6
              .setup-content
                .setup-body
                  p Event Setup
                  h2(v-if='!name') Main Details - New Event
                  h2.title-filled(v-if='name') {{ name }}
                    tk-tools(style='margin-left: 16px;')
                  span.status draft
            .col-sm-6.text-right
              .setup-content
                .setup-body
                  tk-secondary-button(name='save as draft')
                  tk-primary-button(@click='goTo', name='next step')
        #content
          .row
            .col-sm-10.col-sm-offset-1
              #basic-details
                .row
                  .col-sm-8
                    h3 1. Basic Details
                    p All crucial information about this event
                    span * Event title
                    span.count(v-if="name") {{ name.length }} / 50
                    span.count(v-if="!name") 0 / 50
                    tk-input-type-text.select(name='title', data-vv-value-path='text', placeholder='a short and recognizable title', v-validate="'required|alpha_spaces'", maxlength='50', v-model='name', :has-error="errors.has('title')", data-vv-validate-on="none")
                    p(v-if="errors.has('title')").error.error-title Event title is required
                    button.action(v-if='!has_subevents', @click='set_has_subevents') Create Sub-Events
                    button.action(v-if='has_subevents', @click='set_no_subevents', style='margin-bottom: 24px;') No Sub-Events
                section(v-if='has_subevents')
                  .row
                    .col-sm-8
                      #sub-event
                        span Sub event name
                        tk-send(placeholder='sub-event title', style='margin-bottom: 24px;', v-model="new_subevent_name", @click='add_subevent')
                  .row
                    .col-sm-12
                      #all-sub-events
                        span(style='display: block;') All sub-events
                        span.sub-event(v-for='(s, index) in subevents',
                            @click='set_active_subevent(index)',
                            :class="{'active-subevent': index === current_subevent}") {{ s.name }}
                          button(@click='delete_subevent(index)')
                            i.material-icons close
              section(v-if='has_subevents && subevents[current_subevent]')
                #location
                  h3 {{ subevents[current_subevent].name }}
                    tk-tools-second(style='margin-left: 16px;')
                  span * Location
                  .row
                    .col-sm-8
                      tk-input-type-text.select(placeholder='where will the event take place', v-model='new_subevent_location', @input='updateSubeventLocation')
                      form(action='')
                        input(type='checkbox', name='map', id="map", v-model='new_subevent_map', @change='updateSubeventMap')
                        label(for="map", style='display: inline-block;') Show map on event page
                      br
                    .col-sm-4
                      tk-map(v-if='subevents[current_subevent].show_map && subevents[current_subevent].location', style='height: 224px; margin-bottom: 0;')
                #event-date
                  .row
                    .col-sm-8
                      h3 {{ subevents[current_subevent].name }} dates
                      p Specify when will this event take place or create an event series
                  .row
                    .col-sm-12
                      .event-date-nav
                        button(@click='showTable()', :class="{'active-nav': show_table === true}") Table
                        button(@click='showCalendar()', :class="{'active-nav': show_calendar === true}") Cart
                  
                    #calendar(v-if='!show_table && show_calendar')
                      .row
                        .col-sm-12
                          .calendar-start-end
                            .row
                              .col-sm-2
                                p Start
                                tk-input-type-text
                              .col-sm-2
                                p End
                                tk-input-type-text
                      .row
                        .col-sm-4
                          tk-calendar
                        .col-sm-4
                          tk-calendar
                      .row
                        .col-sm-12
                          .calendar-sub-menu
                            .row
                              .col-sm-6
                                button(style='margin-right: 32px; margin-top: 12px;').action only one event
                                button.action duplicate day
                              .col-sm-6
                                tk-secondary-button(name='add dates')
                      .events-on-this-date
                        .row.events-sub-menu
                          .col-sm-6
                            p.events-title Events on this date
                          .col-sm-6.text-right
                            p.total Total events: 
                              strong 1
                        .row(v-for="(event, index) in all_events")
                          .col-sm-12
                            tk-tools(style='margin-right: 14px; margin-top: -8px;')
                            span Time: 
                              strong 16:00 - 22:00 
                              | | 
                              strong Location: House of Blackheads, Riga, Latvia
                            br
                            tk-tools(style='margin-right: 14px; margin-top: -8px;')
                            span Time: 
                              strong 16:00 - 22:00 
                              | | 
                              strong Location: House of Blackheads, Riga, Latvia
                            br
                            tk-tools(style='margin-right: 14px; margin-top: -8px;')
                            span Time: 
                              strong 16:00 - 22:00 
                              | | 
                              strong Location: House of Blackheads, Riga, Latvia
                  
                    .events-table(v-if='!show_calendar && show_table')
                      .row.margin
                        .col-sm-2
                          label.start(for="start-date") Start
                          tk-input-type-text(name='start_date', data-vv-value-path='text', placeholder="yyyy-mm-dd", v-validate="'required|date_format:YYYY-MM-DD'", v-model='new_date.start_date', :has-error="errors.has('start_date')", data-vv-validate-on="none")
                          p(v-if="errors.has('start_date')").error Invalid date format
                        .col-sm-2
                          label(for="start-time") &nbsp;
                          tk-input-type-text(name='start_time', data-vv-value-path='text', placeholder="hh-mm", v-validate="'required|date_format:HH-mm'", v-model='new_date.start_time', :has-error="errors.has('start_time')", data-vv-validate-on="none")
                          p(v-if="errors.has('start_time')").error Invalid time format
                        .col-sm-2
                          label.end(for="end-date") End
                          tk-input-type-text(name='end_date', data-vv-value-path='text', placeholder="yyyy-mm-dd", v-validate="'required|date_format:YYYY-MM-DD'", v-model='new_date.end_date', :has-error="errors.has('end_date')", data-vv-validate-on="none")
                          p(v-if="errors.has('end_date')").error Invalid date format
                        .col-sm-2
                          label(for="end-time") &nbsp;
                          tk-input-type-text(name='end_time', data-vv-value-path='text', placeholder="hh-mm", v-validate="'required|date_format:HH-mm'", v-model='new_date.end_time', :has-error="errors.has('end_time')", data-vv-validate-on="none")
                          p(v-if="errors.has('end_time')").error Invalid time format
                      .row
                        .col-sm-6.text-left
                          //button.action(style='margin-top: 18px;') multiple events
                        .col-sm-2.text-right
                          tk-secondary-button(name='add date', style='width: 100%;', @click='addDate')
                      .row
                        .col-sm-12
                          tk-table-event-overview(v-if='all_events.length')
                #tickets-and-seating
                  .row
                    .col-sm-8
                      h3 3. Tickets and Seating
                      h4 Seating 
                      p Choose a ticket allocation model for this event
                      form(action='')
                        input#open(type='radio', name='open', value='open', v-model='seating')
                        label(for="open") Open Admittance
                        //  input#reserved(type='radio', name='open', value='reserved')
                            label(for="reserved") Reserved Seating
                            input#mixed(type='radio', name='open', value='mixed')
                            label(for="mixed") Mixed Seating
                  .row
                    .col-sm-12
                      h4 Tickets
                      p Create and organize all ticket types of this event
                      tk-table-ticket-seatings(v-if='all_tickets.length > 0', style='margin-bottom: 24px;', @ticketSettings='ticketSettings')
                  .row
                    .col-sm-9
                      tk-secondary-button(name='add paid ticket', @click='addTicket("paid")')
                      tk-secondary-button(name='add free ticket', @click='addTicket("free")')
                      tk-secondary-button(name='add donation ticket', @click='addTicket("donation")')
                    .col-sm-3.text-right
                      p.center TOTAL TICKETS: 
                        strong {{ all_tickets.length }}
                #special-pricing
                  .row
                    .col-sm-12
                      h3 4. Special Pricing
                      p Set special prices for handicapped, elderly, students, VIP or any other attendee group
                      br
                      form(action='')
                        input#nospecial(type='radio', name='pricing', value='nospecial')
                        label(for="nospecial") No special pricing
                        input#special(type='radio', name='pricing', value='special')
                        label(for="special") Allow special pricing
                  .row
                    .col-sm-8.col-sm-offset-1
                      form(action='')
                        input(type='checkbox', name='pricing', id="handicapped")
                        label(for="handicapped") Handicapped
                        .discount
                          .row
                            .col-sm-2.col-sm-offset-2
                              tk-input-type-text-admin-currency
                            .col-sm-3
                              tk-input-type-text(placeholder='amount')
                        input(type='checkbox', name='pricing', id="senior")
                        label(for="senior") Senior
                        input(type='checkbox', name='pricing', id="student")
                        label(for="student") Student
    tk-footer-admin
</template>

<script>
import map from '@/components/single-event-page/map'
import * as types from '@/store/mutation-types'
import Api from '@/services/ApiService'
import { mapGetters } from 'vuex'
import Calendar from '@/components/single-event-page/Calendar'

export default {
  data () {
    return {
      name: null,
      location: null,
      show_map: null,
      seating: null,
      new_subevent_name: {},
      new_date: {
        end_time: '12-00',
        start_time: '10-00',
        end_date: '2018-01-05',
        start_date: '2018-01-05'
      },
      current_subevent: null,
      show_table: true,
      show_calendar: false,
      has_subevents: false,
      new_subevent_map: false,
      new_subevent_location: ''
    }
  },
  methods: {
    sendEvent () {
      var data = {
        'name': 'Riga 2017',
        'location': '',
        'show_map': true,
        'seating': 'open',
        'status': 'draft',
        'default_language': 'en',
        'default_currency': 'eur',
        'timezone': 'UTC+3',
        'vat_rate': 21,
        'sales_tax': 5,
        'special_pricing': [
          {
            'name': 'Handicapped1ss1',
            'discount': 2500,
            'currency': 'eur'
          },
          {
            'name': 'Senior1ss1',
            'discount': 50,
            'currency': '%'
          }
        ],
        'tickets': [
          {
            'name': 'Early Bird',
            'quantity': 20,
            'sales_channels': [],
            'ticket_fees': [],
            'sales_start_date': '2017-12-01 00:00:00',
            'sales_end_date': '2018-01-05 12:00:00',
            'minimum_ticket_count': 1,
            'maximum_ticket_count': 10,
            'description': 'Description of the ticket',
            'price': {
              'amount': 35000,
              'currency': 'eur'
            }
          }
        ],
        'subevents': [
          {
            'name': 'Dinner in the sky',
            'location': 'Riga',
            'show_map': true
          }
        ],
        'events': [
          {
            'date_start': '2018-01-05 00:00:00',
            'date_end': '2018-01-05 00:00:00',
            'name': 'Dinner In The Sky',
            'subevent': 'Dinner in the sky'
          },
          {
            'date_start': '2018-01-05 00:00:00',
            'date_end': '2018-01-05 00:00:00',
            'name': 'Dinner In The Sky',
            'subevent': 'Dinner in the sky'
          }
        ]
      }
      var api = new Api()
      api
        .token(this.token)
        .admin().event().create(data)
        .then(response => { this.$router.push({name: 'VisualEditor'}) })
    },
    showTable () {
      this.show_table = true
      this.show_calendar = false
    },
    showCalendar () {
      this.show_table = false
      this.show_calendar = true
    },
    updateSubeventMap (value) {
      var checked = value.srcElement.checked
      var index = this.current_subevent
      this.$store.commit(types.UPDATE_SUBEVENT_MAP, {checked, index})
    },
    updateSubeventLocation (value) {
      var index = this.current_subevent
      this.$store.commit(types.UPDATE_SUBEVENT_LOCATION, {value, index})
    },
    set_active_subevent (index) {
      this.current_subevent = index
      if (this.subevents[this.current_subevent]) {
        this.new_subevent_map = this.subevents[this.current_subevent].show_map
        this.new_subevent_location = this.subevents[this.current_subevent].location
      }
    },
    ticketSettings () {
      window.jQuery('#ticket-settings').modal('show')
    },
    addTicket (type) {
      var ticket = {
        type: type,
        name: '',
        quantity: '',
        price: 0
      }
      this.$store.commit(types.ADD_EVENT_TICKET, ticket)
    },
    addDate () {
      this.$validator.validateAll().then((result) => {
        result = true
        if (result) {
          this.$store.commit(types.ADD_EVENT, {
            date_start: this.new_date.start_date + ' ' + this.new_date.start_time + ':00',
            date_end: this.new_date.end_date + ' ' + this.new_date.end_time + ':00',
            subevent: this.subevents[this.current_subevent].name
          })
          this.new_date = {
            end_time: '',
            start_time: '',
            end_date: '',
            start_date: ''
          }
        }
      })
    },
    goTo () {
      this.sendEvent()
    },
    set_has_subevents () {
      this.has_subevents = true
      this.current_subevent = 0
    },
    set_no_subevents () {
      this.has_subevents = false
      this.current_subevent = 0
    },

    add_subevent () {
      this.current_subevent = this.subevents.length - 1
      this.$store.commit(types.ADD_SUBEVENT, {
        name: this.new_subevent_name,
        location: '',
        show_map: false
      })
    },
    delete_subevent (index) {
      var temp = this.subevents.length - 2
      if (temp < 0) { temp = null }
      this.current_subevent = temp
      // remove events and tickets
      this.$store.commit(types.DELETE_SUBEVENT, index)
    },
    isNumber (e) {
      var iKeyCode = (e.which) ? e.which : e.keyCode
      if (iKeyCode !== 46 && iKeyCode > 31 && (iKeyCode < 48 || iKeyCode > 57)) {
        e.preventDefault()
        return false
      } else {
        /* this.customGiftCard.name = 'Custom Gift Card'
        this.customGiftCard.amount = 1
        this.customGiftCard.code = 'eur' */
        return true
      }
    }
  },
  computed: {
    ...mapGetters({
      all_events: 'all_events',
      all_tickets: 'all_tickets',
      subevents: 'subevents',
      token: 'logInAdminToken'
    })
  },
  components: {
    tkMap: map,
    tkCalendar: Calendar
  }
}
</script>

<style lang="less" scoped>
  .active-subevent{
    border: 2px solid #292929 !important;
  }
  .wrapper{
    margin-bottom: 120px;
    .select{
      margin-bottom: 24px;
    }
    .count{
      float: right;
      font-size: 12px;
      margin-top: 3px;
    }
    .discount{
      margin-bottom: 16px;
    }
    .title-filled{
      margin-right: 16px;
    }
    .center{
      margin: 0;
      margin-top: 12px;
    }
    .sub-event{
      cursor: pointer;
      font-size: 18px;
      display: inline-block;
      padding: 8px;
      border: 1px solid #292929;
      margin-right: 24px;
      margin-bottom: 16px;
      &:hover{
        background-color: #fff;
      }
      button{
        padding: 0;
        background-color: transparent;
        border: none;
        outline: 0;
        .material-icons{
          padding-left: 8px;
          vertical-align: middle;
          font-size: 18px;
        }
      }
    }
    .margin{
      margin-bottom: 24px;
    }
    .action{
      outline: 0;
      text-transform: uppercase;
      padding: 0;
      background-color: transparent;
      text-decoration: underline;
      border: none;
    }
    #setup{
      border-bottom: 1px solid #979797;
      margin-bottom: 32px;
    }
    #location{
      label{
        margin: 0;
      }
    }
    #event-date{
      label{
        margin-bottom: 10px;
      }
    }
    #basic-details, #event-date, #tickets-and-seating, #location{
      padding-bottom: 24px;
      margin-bottom: 24px;
      border-bottom: 1px solid #979797;
    }
    #tickets-and-seating{
      form{
        margin-bottom: 24px;
      }
      button{
        margin-right: 32px;
      }
    }
    .setup-content{
      display: table;
      height: 152px;
      width: 100%;
      .setup-body{
        display: table-cell;
        vertical-align: middle;
        p{
          font-size: 18px;
          font-weight: bold;
          margin: 0;
        }
        h2{
          max-width: 700px;
          word-wrap: break-word;
          margin: 0 0 16px 0;
        }
        .status{
          font-weight: bold;
          border: 1px solid #292929;
          text-transform: uppercase;
          margin: 0;
          padding: 4px;
        }
        button{
          margin-left: 24px;
        }
      }
    }
    h3{
      margin: 0 0 14px 0;
      &+p{
        font-size: 18px;
        margin-bottom: 18px;
      }
    }
    h4{
      margin-bottom: 16px;
      & + p{
        font-size: 18px;
        margin-bottom: 24px;
      }
    }
    span{
      display: inline-block;
      margin-bottom: 8px;
    }   
  }
  .error {
    font-size: 12px;
    font-weight: bold;
  }
  .error-title{
    margin-top: -24px;
  }
  .event-date-nav{
    border-bottom: 1px solid #979797;
    padding-bottom: 27px;
    margin-bottom: 21px;
    button{
      font-size: 18px;
      padding: 0;
      outline: 0;
      background-color: transparent;
      border: none;
      margin-right: 61px;
    }
    .active-nav{
      position: relative;
      -webkit-text-stroke: 1px #292929;
      &:after{
        content: '';
        position: absolute;
        width: 8px;
        height: 1px;
        background-color: #292929;
        left: 0;
        right: 0;
        margin: auto;
        top: 32px;
      }
    }
  }
  .calendar-start-end{
    border-bottom: 1px solid #979797;
    padding-bottom: 24px;
    margin-bottom: 24px;
  }
  .events-on-this-date{
    border-top: 1px solid #979797;
    margin-top: 24px;
    padding-top: 24px;
    .events-sub-menu{
      margin-bottom: 14px;
    }
    .total{
      text-transform: uppercase;
    }
    .events-title{
      text-transform: uppercase;
      color: #9a9a9a;
    }
    span{
      font-size: 18px;
      margin-bottom: 8px;
    }
  }
</style>



