import * as types from '../mutation-types'
import ApiService from '@/services/ApiService'
const api = new ApiService()

const state = {
  customGiftCard: null,
  giftCards: [],
  allCards: [],
  /* Gift Card Voucher Amount */
  gift_card_voucher_amount: []
}
const mutations = {
  [types.ADD_CUSTOMGIFTCARD] (state, g) {
    state.customGiftCard = g
  },
  [types.ADD_GIFTCARD] (state, data) {
    const c = state.giftCards.find(card => card.id === data.giftCard.id)
    if (!c) {
      data.giftCard.giftCount = data.count
      state.giftCards.push(data.giftCard)
    } else {
      c.giftCount = data.count
    }
  },
  [types.SET_GIFTCARDS] (state, gs) {
    state.allCards = gs
  },
  [types.SET_GIFTCARDSVOUCHERS] (state, v) {
    state.gift_card_voucher_amount = v
  },
  [types.CLEAR_GIFTCARDS] (state) {
    state.giftCards = []
    state.customGiftCard = null
    state.gift_card_voucher_amount = []
  }
}
const getters = {
  totalSelectedgiftCards: state => {
    var total = 0
    for (var i = 0; i < state.giftCards.length; i++) {
      total += state.giftCards[i].giftCount
    }
    if (state.customGiftCard !== null) {
      return total + 1
    }
    return total
  },
  /* Get Voucher Amount */
  get_voucher_amount: state => {
    if (state.gift_card_voucher_amount.length > 0) {
      return state.gift_card_voucher_amount[0].discount.amount
    }
    return 0
  },
  get_voucher_code: state => {
    if (state.gift_card_voucher_amount.length > 0) {
      return state.gift_card_voucher_amount[0].code
    }
    return 0
  },
  totalGiftCardPrice: state => {
    var total = 0
    for (var i = 0; i < state.giftCards.length; i++) {
      total += state.giftCards[i].price.amount * state.giftCards[i].giftCount
    }
    if (state.customGiftCard === null) {
      return Math.round(total) + getters.get_voucher_amount(state)
    }
    return total + (state.customGiftCard.price * 100) + getters.get_voucher_amount(state)
  },
  allCards: state => {
    return state.allCards
  },
  allSelectedgiftCards: state => {
    return state.giftCards
  },
  customGift: state => {
    return state.customGiftCard
  }
}
const actions = {
  fetch_giftCards (context) {
    api.getShopgiftCards().then(response => {
      for (var i = 0; i < response.data[0].length; i++) {
        response.data[0][i].giftCount = 0
        response.data[0][i].group = false
      }
      context.commit(types.SET_GIFTCARDS, response.data[0])
      context.commit(types.CLEAR_GIFTCARDS)
    })
  }
}
export default {
  state,
  mutations,
  getters,
  actions
}



