<template lang="pug">
  section
    .section-content
      .section-body
        .container
          .row
            .col-sm-8.col-sm-offset-2
              h1 {{ translations.reset_password_title[defaultLang] | translate }}
              p {{ translations.reset_password_paragraph[defaultLang] | translate }}
              .row
                .col-sm-6
                  p {{ translations.reset_password_current[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text-password(v-model='user.current')
                .col-sm-6
                  p {{ translations.reset_password_new[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text-password#new(v-model='user.new')
                .col-sm-6
                  p {{ translations.reset_password_repeat[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text-password#newRepeat(v-model='user.new_repeat')
                  .error(v-if='!match')
                    p New password did not match!
                  .success(v-if='changed')
                    p Your password has been changed successfully! Thank you
                .col-sm-6
                .col-sm-6
                  tk-secondary-button(v-if='!user.current || !user.new || !user.new_repeat', name="Change Password")
                  tk-primary-button(v-if='user.current && user.new && user.new_repeat', name="Change Password", @click='change')
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        match: true,
        changed: false,
        user: {
          current: '',
          new: '',
          new_repeat: ''
        }
      }
    },
    methods: {
      change () {
        if (this.user.new !== this.user.new_repeat) {
          this.match = false
          document.getElementById('new').classList.add('wrongInput')
          document.getElementById('newRepeat').classList.add('wrongInput')
        } else {
          document.getElementById('new').classList.remove('wrongInput')
          document.getElementById('newRepeat').classList.remove('wrongInput')
          this.match = true
          this.changed = true
        }
      }
    },
    props: ['translations'],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    }
  }
</script>

<style lang="less" scoped>
section{
  .section-content{
    text-align: center;
    display: table;
    width: 100%;
    min-height: 80vh;
    .section-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
  h1 + p{
    margin-bottom: 24px;
    text-align: center;
    margin-top: 0;
    border-bottom: 1px solid #979797;
    padding-bottom: 25px;
  }
  .line{
    margin-bottom: 24px;
  }
  p{
    text-align: left;
    margin-top: 14px;
  }
  input[type='text']{
    margin-bottom: 24px;
  }
  .col-sm-6:last-child{
    text-align: left;
  }
  button{
    margin-bottom: 96px;
  }
}
.wrongInput{
  border: 3px solid #292929 !important;
}
.error, .success{
  text-align: left;
  margin-top: -16px;
  margin-bottom: 14px;
  p{
    font-size: 12px;
    font-weight: bold;
    margin: 0;
  }
}
/* Media Queries */
/* Extra Small */
@media(min-width:0px) and (max-width:767px){
  section{
    text-align: left;
    margin-top: 16px;
    h1, p, h1 + p{
      text-align: left;
    }
    h1 + p{
      padding-bottom: 16px;
      margin-bottom: 21px;
    }
    p{
      margin-top: 0;
    }
    input[type='text']{
      margin-bottom: 16px;
    }
    button{
      width: 100%;
      margin-bottom: 32px;
    }
  }
}
</style>



