<template lang="pug">
  section
    button.termsButton(type='button', name='Terms and Conditions', data-toggle='modal', data-target='#Terms') {{ translations.checkout_terms[defaultLang] | translate }}
    #Terms.modal(role='dialog')
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close
            i.material-icons clear
        .modal-content
          .wrapper
            .modal-header
              h1 {{ translations.checkout_modal_terms_title | tktranslate(lang) }}
            .modal-body
              h2 {{ translations.checkout_modal_terms_sub_title | tktranslate(lang) }}
              h4 {{ translations.checkout_modal_terms_paragraph | tktranslate(lang) }}
              p(style="white-space: pre-line") {{ translations.checkout_modal_terms_paragraph_1 | tktranslate(lang) }}
                // .row
                  .col-sm-7
                  .col-sm-5
                    form(action='')
                      input(type='checkbox', name='terms', value='term', id="tac", v-model="checked")
                      label(for="tac") {{ translations.checkout_modal_terms_accept | tktranslate(lang) }}
          // .modal-footer
            tk-disabled-button(:name="translations.checkout_modal_terms_button | tktranslate(lang)", v-if='!checked')
            tk-primary-button(:name="translations.checkout_modal_terms_button | tktranslate(lang)", v-else, data-dismiss="modal")
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    computed: {
      ...mapGetters({
        lang: 'selectedLanguage',
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {},
    data () {
      return {
        checked: ''
      }
    }
  }
</script>

<style lang="less" scoped>
section{
  display: inline-block;
  margin: 0;
  padding: 0;
  box-shadow: none;
  button{
    outline: none;
  }
  .termsButton{
    background-color: transparent;
    color: #292929;
    text-transform: none;
    font-size: 14px;
    padding: 0;
    border: 0;
    padding-left: 4px;
    outline: none !important;
    text-decoration: underline;
    &:hover{
      background-color: transparent;

    }
  }
  h1{
    margin: 24px 0 32px 0;
  }
  h2{
    margin-bottom: 16px;
  }
  h4{
    margin-bottom: 24px;
  }
  ul{
    padding: 0;
    list-style-type: none;
    li{
      padding-bottom: 16px;
      ul{
        padding-top: 16px;
      }
    }
  }
  .modal-footer{
    background-color: #fff;
    padding: 20px 32px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  }
  .modal-dialog{
    width: 770px;
  }
  .modal-content{
    height: 80vh;
    overflow-y: scroll;
    padding-bottom: 60px;
  }
  .modal-body{
    text-align: left;
    padding: 0;
  }
  .modal-content{
    background-color: #f7f7f7;
    border-radius: 0;
    border: none;
  }
  .wrapper{
    padding: 0 25px 25px 25px;
  }
  .modal-header{
    text-align: left;
    border-bottom: 1px solid #979797;
    padding: 0;
  }
  .modal-close{
    padding: 10px 0;
    text-align: right;
    button{
      padding: 0;
      font-size: 14px;
      text-transform: uppercase;
      color: #fff;
      border: none;
      background-color: transparent;
      .material-icons{
        font-size: 18px;
        vertical-align: text-bottom;
        padding-left: 4px;
      }
    }
  }
}
@media(min-width:0px) and (max-width:767px){
  .modal-content{
    height: 100vh !important;
  }
  .modal-dialog{
    height: 100vh;
    overflow-y: scroll;
    width: auto !important;
  }
  .modal-body{
    padding-bottom: 40px !important;
  }
  .modal-footer{
    button{
      width: 100%;
    }
  }
  section{
    h1{
      margin: 0;
      margin-bottom: 16px !important;
    }
    h2{
      font-size: 24px;
    }
    h4{
      font-size: 14px;
      margin-bottom: 16px;
    }
    .wrapper{
      padding: 16px;
      padding-bottom: 0;
    }
    ul{
      margin-bottom: 0;
      padding: 0;
      list-style-type: none;
    }
    .modal-close{
      box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
      display: block;
      position: absolute;
      bottom: 0;
      width: 100%;
      background-color: #fff;
      text-align: center;
      z-index: 1;
      padding: 20px;
      button{
        color: #292929;
        border: none;
        background-color: transparent;
        padding: 0;
        text-transform: uppercase;
      }
    }
  }
}
</style>



