<template lang="pug">
  section
    .container
      #empty-history(v-if='!totalPrice && !totalGiftCardPrices')
        .history-content
          .row
            .col-sm-8.col-sm-offset-2
              img(src='static/calendar_emptystate.svg', alt='')
              h2 {{ translations.history_empty_title[defaultLang] | translate }}
              p {{ translations.history_empty_paragraph[defaultLang] | translate }}
      .row(v-if='totalPrice || totalGiftCardPrices')
        .col-sm-8.col-sm-offset-2
          h1 {{ translations.history_title[defaultLang] | translate }}
          p {{ translations.history_paragraph[defaultLang] | translate }}
      #tickets(v-if='totalPrice')
        .row(v-for="(ticket,index) in ticketsSelected")
          .col-sm-8.col-sm-offset-2
            #history
              h3 {{ ticket.seat.code }}
              p {{ ticket.seat.price.currency.created_at }}
              .row
                .col-xs-6.col-sm-5
                  h5 {{ translations.history_date[defaultLang] | translate }}
                  p {{ ticket.seat.price.currency.created_at }}
                .col-xs-6.col-sm-5
                  h5 {{ translations.history_items[defaultLang] | translate }}
                  p {{ ticket.count }}
                .col-xs-6.col-sm-2
                  h5 {{ translations.history_total[defaultLang] | translate }}
                  p {{ (ticket.seat.price.amount * ticket.count) / 100 | currency('') }} {{ tickets[0].price.currency.code }}
                .col-xs-12.col-sm-5
                  tk-primary-button(:name="translate(translations.history_pdf_button[defaultLang])")
                .col-xs-12.col-sm-5
                  tk-secondary-button(:name="translate(translations.history_invoice_button[defaultLang])")
                .col-sm-2
      #giftCards(v-if='giftCards')
        .row(v-for="(g ,index) in giftCards")
          .col-sm-8.col-sm-offset-2
            #history-gift
              h3 {{ g.translations.name.en }}
              p {{ g.price.currency.created_at }}
              .row
                .col-xs-6.col-sm-5
                  h5 {{ translations.history_date[defaultLang] | translate }}
                  p {{ g.price.currency.created_at }}
                .col-xs-6.col-sm-5
                  h5 {{ translations.history_items[defaultLang] | translate }}
                  p {{ g.giftCount }}
                .col-xs-6.col-sm-2
                  h5 {{ translations.history_total[defaultLang] | translate }}
                  p {{ (g.giftCount * g.price.amount) / 100 | currency('') }} {{ g.price.currency.code }}
                .col-xs-12.col-sm-12
                  tk-primary-button(:name="translate(translations.history_documents_button[defaultLang])")
        .row(v-if='customGift !== null')
          .col-sm-8.col-sm-offset-2
            .custom
              h3 {{ customGift.name }}
              p 2017-12-03 11:09:12
              .row
                .col-xs-6.col-sm-5
                  h5 {{ translations.history_date[defaultLang] | translate }}
                  p 2017-12-03 11:09:12
                .col-xs-6.col-sm-5
                  h5 {{ translations.history_items[defaultLang] | translate }}
                  p {{ customGift.amount }}
                .col-xs-6.col-sm-2
                  h5 {{ translations.history_total[defaultLang] | translate }}
                  p {{ customGift.price | currency('') }}
                .col-xs-12.col-sm-12
                  tk-primary-button(:name="translate(translations.history_documents_button[defaultLang])")
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  props: ['translations'],
  methods: {
    translate (translate) {
      return this.$options.filters.translate(translate)
    }
  },
  computed: {
    ...mapGetters({
      tickets: 'getTickets',
      totalPrice: 'getTotalPrice',
      discount: 'totalDiscount',
      totalTickets: 'getTicketsSelected',
      allSeats: 'getAllSeats',
      defaultLang: 'defaultLanguage',
      giftCards: 'allSelectedgiftCards',
      customGift: 'customGift',
      totalGiftCardPrices: 'totalGiftCardPrice'
    }),
    ticketsSelected () {
      return this.allSeats.map(ticketType => {
        var ts = this.tickets.filter(function (ticket) {
          return ticket.id === ticketType.id
        })

        return {
          seat: ticketType,
          count: ts.length
        }
      }).filter(type => {
        return type.count > 0
      })
    }
  }
}
</script>

<style lang="less" scoped>
#history, #history-gift, .custom{
  padding-bottom: 24px;
  border-bottom: 1px solid #979797;
}
#empty-history{
  display: table;
  min-height: 70vh;
  width: 100%;
  text-align: center;
  .history-content{
    display: table-cell;
    vertical-align: middle;
  }
}
h3{
  text-transform: capitalize;
  margin: 0;
  margin-top: 13px;
  margin-bottom: 8px;
}
h3 + p{
  margin-bottom: 16px;
}
section{
  h1 + p{
    padding-bottom: 24px;
    margin: 0;
    border-bottom: 1px solid #979797;
  }
  min-height: 50vh;
  .container{
    margin-bottom: 64px;
  }
  h5{
    font-size: 14px;
    color: #9a9a9a;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 4px;
  }
  h5 + p{
    margin-bottom: 24px;
  }
}
@media(min-width:0px) and (max-width:767px){
  #history, #history-gift, .custom{
    padding: 0;
  }
  section{
    min-height: 60vh;
    margin-bottom: 0;
    .container{
      margin-bottom: 38px;
    }
    .col-xs-6{
      margin-bottom: 24px;
      p{
        margin: 0;
      }
    }
    button{
      width: 100%;
      margin-bottom: 24px;
    }
    h1 + p{
      padding-bottom: 16px;
    }
  }
}
@media(min-width:768px) and (max-width:991px){
  section{
    min-height: 30vh;
  }
}
@media(min-width:992px) and (max-width:1199px){
  section{
    min-height: 70vh;
  }
}
</style>



