<template lang="pug">
  section
    tk-header-admin
    .settings
      .container
        #settings
          .row
            .col-sm-4
              .settings-content
                .settings-body
                  h2 Main Settings
            .col-sm-8.text-right
              .settings-content
                .settings-body
                  tk-secondary-button(name='revert to saved')
                  tk-primary-button(name='save changes')
    .container
      .navigation
        .row
          .col-sm-12
            .menu-content
              .menu-body
                router-link(:to="{name: 'Events'}") My Account
                router-link.current(:to="{name: 'Events', query: {filter: 'upcoming'}}") Shop Preferences
                router-link(:to="{name: 'Events', query: {filter: 'public'}}") Manage Team
                router-link(:to="{name: 'Events', query: {filter: 'draft'}}") Notifications
    .content
      .container
        .row
          .col-sm-4#js-scrollspy 
            nav#aside
              p Shop Preferences
              ul.js-scrollspy-nav
                li#manageLink(@click='manageSpy') 1. Manage Shops
                li#shopLink(@click='shopSpy') 2. Shop Settings
                li#paymentLink(@click='paymentSpy') 3. Payment Gateways
                li#landingLink(@click='landingSpy') 4. Landing page Layout
                li#giftLink(@click='giftSpy') 5. Gift Cards
          .col-sm-8#internal-content
            div#manage-scroll
              h3 1. Manage Shops
              p Create new and manage your existing shops.
              #manage.section.scrollspy
                .row
                  .col-sm-4.col-sm-offset-2
                    p.center Create Shop
                  .col-sm-6
                    tk-send(v-model='info.name').last
            #shop.section.scrollspy
              h3 2. Shop Settings - TED Latvia
              p Adjust your prefferences regarding your shopping experience
              .row
                .col-sm-4.col-sm-offset-2
                  p.center Shop Domain
                .col-sm-6
                  tk-input-type-text-domain(v-model='info.domain').margin-bottom
                .col-sm-4.col-sm-offset-2
                  p.center Shop Default Language
                .col-sm-6
                  tk-custom-select-string(:options='languages', v-model='info.default_language').margin-bottom
                  tk-modal-shop-languages.margin-bottom
                .col-sm-4.col-sm-offset-2
                  p.center Shop Default Currency
                .col-sm-6
                  tk-custom-select-string(:options='currencies', v-model='info.default_currency').margin-bottom
                  tk-modal-shop-currencies.last
            #payment.section.scrollspy
              h3 3. Payment Gateways
              p Select all payment methods you need on your shop
              .row
                .col-sm-4.col-sm-offset-2
                  p Payment Gateways
                .col-sm-6
                  form(action='')
                    input(type='checkbox', name='card', id="visa", value='visa', v-model='info.allowed_gateways')
                    label(for="visa")
                      img(src='static/visa.png')
                      | Visa, MasterCard (1.4% + 0.24$)
                    input(type='checkbox', name='card', id="american", value='american', v-model='info.allowed_gateways')
                    label(for="american")
                      img(src='static/american_express.png')
                      | American Express (fixed 0.50$)
                    input(type='checkbox', name='card', id="paypal", value='paypal', v-model='info.allowed_gateways')
                    label(for="paypal") 
                      img(src='static/paypal.png') 
                      | PayPal (1% + 0.45$)
                    input(type='checkbox', name='card', id="sofort", value='sofort', v-model='info.allowed_gateways')
                    label(for="sofort") 
                      img(src='static/sofort.png')
                      | Sofort (3%)
                    input(type='checkbox', name='card', id="bitcoin", value='bitcoin', v-model='info.allowed_gateways')
                    label(for="bitcoin")
                      img(src='static/bitcoin.png')
                      | Bitcoin (free)
            #landing.section.scrollspy
              h3 4. Landing page Layout
              p Select the way your events and products are presented on the Landing page
              .row
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                  form(action='')
                    input#big(type='radio', name='tiles', value='big', v-model='info.layout')
                    label(for="big") Big, full width tiles
                    input#small(type='radio', name='tiles', value='small', v-model='info.layout')
                    label(for="small") Small tiles arranged in 3 columns
            #gift.section.scrollspy
              h3 5. Gift Cards
              p Control the availability and appearance of the Gift Cards for this shop
              .row
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                  .gift-card-content
                    .gift-card-body
                      h4 Upload New
                      p 
                        strong Gift Card image 
                        | min 435x200 px
                      tk-secondary-button(name='browse images')
                  button.last.action Do not sell gift cards in this shop
            .row
              .col-sm-8
                tk-secondary-button.right(name='revert to saved')
                tk-primary-button(@click='save', name='save changes')
    tk-footer-admin
</template>

<script>
  import Api from '@/services/ApiService'
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'
  import store from '@/store'

  export default {
    data () {
      return {
        info: {
          name: '',
          logo: '',
          domain: '',
          default_language: '',
          default_currency: '',
          allowed_gateways: [],
          layout: '',
          giftcards_enabled: '',
          giftcard_banner: ''
        },
        visibility: ['Public', 'Private'],
        currency: ['Euro', 'USD'],
        timezone: ['UTC+3', 'GMT'],
        currencies: [ 'Euro', 'USD' ],
        languages: [ 'English', 'Latvian' ]
      }
    },
    computed: {
      ...mapGetters({
        shop_data: 'shop_data',
        token: 'logInAdminToken'
      })
    },
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    },
    methods: {
      save () {
        this.$store.commit(types.ADD_SHOP_PREFERENCES, this.info)
        new Api().postShop(this.token, {shop_data: this.shop_data}).then(response => {
        })
      },
      manageSpy () {
        document.getElementById('manage').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      shopSpy () {
        document.getElementById('shop').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      paymentSpy () {
        document.getElementById('payment').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      landingSpy () {
        document.getElementById('landing').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      giftSpy () {
        document.getElementById('gift').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      }
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')

      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '350') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    }
  }
</script>

<style lang="less" scoped>
#manage, #shop, #payment, #landing, #gift{
  margin-bottom: 32px;
  border-bottom: 1px solid #979797;
}
.navigation{
  border-bottom: 1px solid #979797;
  margin-bottom: 32px;
  .menu-content{
    display: table;
    height: 88px;
    width: 100%;
    .menu-body{
      display: table-cell;
      vertical-align: middle;
      a{
        text-transform: none;
        position: relative;
        text-decoration: none;
        margin-right: 50px;
        color: #292929;
        font-size: 18px;
        .router-link-exact-active{
          position: relative;
          width: 10px;
          height: 2px;
          -webkit-text-stroke: 1px #292929;
          &:after{
            content: "";
            position: absolute;
            width: 10px;
            height: 1px;
            background-color: #292929;
            left: 0;
            right: 0;
            bottom: -10px;
            margin: 0 auto;
          }
        }
      }
    }
  }
}               
.settings{
  background-color: #f7f7f7;
  #settings{
    border-bottom: 1px solid #979797;
  }
  .settings-content{
    display: table;
    height: 116px;
    width: 100%;
    .settings-body{
      display: table-cell;
      vertical-align: middle;
      h2{
        margin: 0;
      }
      button{
        margin-left: 24px;
      }
      .back{
        text-transform: uppercase;
        padding: 0;
        border: none;
        outline: 0;
        text-decoration: underline;
        background-color: transparent;
      }
    }
  }        
}
.content{
  margin-bottom: 120px;
}
nav{
  padding: 24px;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
  p{
    color: #9a9a9a;
    text-transform: uppercase;
    margin-bottom: 24px;
  }
  ul{
    text-decoration: none;
    padding: 0;
    list-style-type: none;
    li{
      margin-bottom: 16px;
      cursor: pointer;
      &:last-child{
        margin: 0;
      }
    }
    a{
      cursor: pointer;
      text-transform: capitalize;
      text-decoration: none;
      &:hover{
        color: #292929;
      }
    }
  }
}
.active{
  font-weight: bold;
}
.center{
  margin: 0;
  margin-top: 12px;
}
.current{
  position: relative;
  -webkit-text-stroke: 1px #292929;
  &:after{
    position: absolute;
    content: '';
    width: 14px;
    height: 1px;
    background-color: #292929;
    bottom: -11px;
    left: 0;
    right: 0;
    margin: auto;
  }
}
.fixed{
  position: fixed;
  background-color: #f7f7f7;
  z-index: 1;
  width: 360px;
  top: 32px;
}
input[type='text']{
  margin-bottom: 24px;
}
.margin-bottom{
  margin-bottom: 24px;
}
.last{
  margin-bottom: 32px !important;
}
.action{
  outline: 0;
  text-transform: uppercase;
  padding: 0;
  background-color: transparent;
  text-decoration: underline;
  border: none;
}
.right{
  margin-right: 32px;
}
h3{
  margin-top: 0;
  +p{
    margin-bottom: 32px;
  }
}
h4{
  margin-bottom: 24px;
}
form{
  margin-bottom: 32px;
  img{
    height: 22px;
    width: 22px;
    margin-right: 8px;
  }
}
.gift-card-content{
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0.2) 100%), url('/static/giftcard_bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: table;
  height: 184px;
  width: 100%;
  margin-bottom: 32px;
  .gift-card-body{
    color: #fff;
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    p{
      margin-bottom: 16px;
    }
  }
}
</style>



