import axios from 'axios'
import modal from '@/services/Modal/Modal'

class Promise {
  successcallback = null;
  errorcallback = null;
  constructor (promise) {
    promise.then(response => {
      if (response.data.error) {
        if (this.errorcallback) {
          this.errorcallback(response.data.error)
        } else {
          this.error(response.data.error)
        }
      } else if (this.successcallback) { this.successcallback(response.data) }
    }).catch((error) => {
      if (!error.data) {
        alert(error)
      } else {
        if (this.errorcallback) {
          this.errorcallback(error.data.error)
        } else {
          this.error(error.data.error)
        }
      }
    })
  }
  then (func) {
    this.successcallback = func
    return this
  }
  catch (func) {
    this.errorcallback = func
    return this
  }
  error (error) {
    modal.warn(error.code, this.translate(error.translations.message))
  }
  translate (object) {
    var lang = 'en'
    if (!object) { return 'column.404' }
    if (object[lang]) { return object[lang] }
    return 'translation.404'
  }
}

class TicketylOS {
  constructor () {
    this.error_object = null
    this.default = {}
    this.calls = []

    this.api_url = 'https://os.ticketyl.com'
    // this.api_url = 'https://api.ticketyl.dev'
    this.front_url = 'https://danceworld.ticketyl.com'
    this.axios = axios.create({
      baseURL: this.api_url
    })
  }
  activeRequest (request) {
    if (request) {
      this.request = request
      return this
    }
    return this.request
  }
  doRequest (promise) {
    var promiseobj = new Promise(promise)
    this.calls.push(promiseobj)
    return promiseobj
  }
  then (func) {
    this.success_callback = func
    return this
  }
  catch (func) {
    this.error_callback = func
    return this
  }

  get (endpoint, data) {
    this.success_callback = null
    this.error_callback = null
    return this.doRequest(this.axios.get(endpoint, { params: this.mergedParams(data) }))
  }
  post (endpoint, data) {
    this.success_callback = null
    this.error_callback = null
    return this.doRequest(this.axios.post(endpoint, this.mergedParams(data)))
  }
  delete (endpoint, data) {
    // x
  }
  mergedParams (data) {
    data = data || {}
    if (this.token) { data.token = this.session_token }
    var p = Object.assign({}, this.default, data)
    return p
  }
  token (token) {
    if (token) { this.session_token = token; return this }
    return this.session_token
  }
  admin () {
    return {
      api: this,
      user: function (id) {
        return {
          api: this.api,
          login: function (data) { return this.api.post('admin/login', data) },
          register: function (data) { return this.api.post('admin/register', data) },
          password: function () {
            return {
              api: this.api,
              new: function (data) { return this.api.post('admin/user/password/new', data) },
              set: function (data) { return this.api.post('admin/user/password/set', data) },
              reset: function (data) { return this.api.post('admin/user/password/reset', data) }
            }
          }
        }
      },
      merchant: function (id) {
        return {
          api: this.api,
          get: function () { return this.api.axios.get('/admin/merchant/' + id) },
          create: function (data) { return this.api.post('admin/merchant', data) },
          update: function (data) { return this.api.post('admin/merchant/' + id, data) },
          series: function (data) { return this.api.get('/admin/series', data) },
          events: function (data) { return this.api.get('/admin/events', data) },
          orders: function (data) { return this.api.get('/admin/orders', data) },
          shops: function (data) { return this.api.get('/admin/shops', data) },
          subscriptions: function (data) { return this.api.get('/admin/subscriptions', data) },
          members: function (data) { return this.api.get('/admin/members', data) },
          upload: function (data) { return this.api.post('/admin/upload', data) }
        }
      },
      member: function (id) {
        return {
          api: this.api,
          get: function () { return this.api.get('/admin/member/' + id) },
          create: function (data) { return this.api.post('admin/merchant', data) },
          update: function (data) { return this.api.post('admin/merchant/' + id, data) }
        }
      },
      shop: function (id) {
        return {
          api: this.api,
          get: function () { return this.api.get('/admin/shop/' + id) },
          create: function (data) { return this.api.post('admin/shop', data) },
          update: function (data) { return this.api.post('admin/shop/' + id, data) },
          special_pricing: function (id) {
            return {
              api: this.api,
              get: function () { return this.api.get('/admin/package/' + id) },
              create: function (data) { return this.api.post('admin/package', data) },
              update: function (data) { return this.api.post('admin/package/' + id, data) },
              delete: function (data) { console.log(this) }
            }
          }
        }
      },
      event: function (id) {
        return {
          api: this.api,
          get: function () { return this.api.get('/admin/event/' + id) },
          create: function (data) { return this.api.post('admin/event', data) },
          update: function (data) { return this.api.post('admin/event/' + id, data) },
          layout: function (data) { return this.api.post('admin/event/' + id + '/layout', data) },
          delete: function () { console.log(this) },
          subevent: function (id) {
            return {
              api: this.api,
              get: function () { return this.api.get('/admin/package' + id) },
              create: function (data) { return this.api.post('admin/package', data) },
              update: function (data) { return this.api.post('admin/package/' + id, data) },
              delete: function (data) { console.log(this) }
            }
          },
          date: function (id) {
            return {
              api: this.api,
              get: function () { return this.api.get('/admin/act' + id) },
              create: function (data) { return this.api.post('admin/act', data) },
              update: function (data) { return this.api.post('admin/act/' + id, data) },
              delete: function (data) { console.log(this) }
            }
          }
        }
      },
      subscription: function (id) {
        return {
          api: this.api,
          get: function () { return this.api.get('/admin/subscription/' + id) },
          create: function (data) { return this.api.post('admin/subscription', data) },
          update: function (data) { return this.api.post('admin/subscription/' + id, data) }
        }
      },
      order: function (id) {
        return {
          api: this.api,
          get: function () { return this.api.get('/admin/order/' + id) }
        }
      }
    }
  }
  shop () {
    return {
      api: this,
      get: (data) => { return this.get('/shop', data) },
      routes: (data) => { return this.get('/shop/routes', data) },
      series: (params) => { return this.get('/shop/series', params) },
      route: (id) => {
        return {
          layout: () => { return this.get('/shop/route/' + id + '/layout') }
        }
      },
      newsletter (email) { return this.axios.post('/shop/newsletter', { email: email }) },
      serie: (id) => { return this.get('/shop/serie/' + id) },
      packages: (serie) => { return this.get('/shop/serie/' + serie + '/packages') },
      events () {
        return {
          api: this.api,
          filter (filter) { return this.api.get('/shop/events', filter.merge({})) }
        }
      },
      seats: function (eventId) { return this.api.get('/shop/event/' + eventId + '/seats') },
      subscriptions: function () { return this.api.get('/shop/subscriptions') }
    }
  }
  gateway () {
    return {
      pay: (code, data) => {
        var url = this.api_url + '/gateway/' + code + '/pay?' + Object.keys(data).map(function (key) { return key + '=' + data[key] }).join('&')
        document.location.href = url
      }
    }
  }
  order (code) {
    return {
      add: (data) => { return this.post('/order/add', data) },
      remove: (data) => { return this.post('/order/remove', data) },
      update: (data) => { return this.post('/order/update', data) },
      reserve: (data) => { return this.post('/order', data) },
      get: (data) => { return this.get('/order/' + code, data) },
      finalize: (data) => { return this.post('/order/' + code + '/finalize', data) }
    }
  }
  filter () {
    return {
      filter_search: null,
      filter_categories: [],
      filter_page: 1,
      filter_limit: 50,
      filter_status: null,
      search: function (term) { this.filter_search = term; return this },
      category: function (id) { this.filter_categories.push(id); return this },
      page: function (page) { this.filter_page = page; return this },
      limit: function (limit) { this.filter_limit = limit; return this },
      status: function (status) { this.filter_status = status; return this },
      merge: function (data) {
        var filter = {}
        if (this.filter_search) { filter['filter.search'] = [this.filter_search] }
        if (this.filter_page) { filter['filter.page'] = this.filter_page }
        if (this.filter_limit) { filter['filter.limit'] = this.filter_limit }
        if (this.filter_status) { filter['filter.status'] = this.filter_status }
        if (this.filter_categories) { filter['filter.categories'] = this.filter_categories }
        return Object.assign({}, filter, data)
      }
    }
  }
}
export default new TicketylOS()



