import * as types from '../mutation-types'

const state = {
  seats: []
}
const mutations = {
  [types.SET_SEATS] (state, s) {
    state.seats = s
  }
}
const getters = {
  getSeats: state => {
    return state.seats
  }
}
export default {
  state,
  mutations,
  getters
}



