<template lang="pug">
  section
    .section-content
      .section-body
        .container
          .row
            .col-sm-8.col-sm-offset-2
              h1 {{ translations.forgot_password_title[defaultLang] | translate }}
              p {{ translations.forgot_password_paragraph[defaultLang] | translate }}
          .row
            .col-sm-4.col-sm-offset-4
              tk-send#emails(placeholder='Email Address', v-model='user_email', @click='password')
              .send(v-if='emailSent')
                p Email has been sent!
              .wrong(v-if='wrongEmail')
                p Wrong email address!
              router-link(:to="{'name': 'EventList'}") {{ translations.forgot_password_button[defaultLang] | translate }}
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    data () {
      return {
        emailSent: false,
        wrongEmail: false,
        user_email: ''
      }
    },
    props: ['translations'],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    mounted () {
      window.jQuery('#LogIn').modal('hide')
    },
    methods: {
      password () {
        var mailformat = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i
        if (this.user_email.match(mailformat)) {
          document.getElementById('emails').classList.remove('wrongInput')
          this.emailSent = true
          this.wrongEmail = false
        } else {
          document.getElementById('emails').classList.add('wrongInput')
          document.getElementById('emails').focus()
          this.wrongEmail = true
          this.emailSent = false
        }
      }
    }
  }
</script>

<style lang="less" scoped>
section{
  .section-content{
    text-align: center;
    display: table;
    width: 100%;
    min-height: 60vh;
    .section-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
  h1 + p{
    padding-bottom: 26px;
    margin-bottom: 26px;
    border-bottom: 1px solid #979797;
  }
  .input-group{
    margin-bottom: 40px;
  }
}
.send, .wrong{
  text-align: left;
  margin-top: -32px;
  margin-bottom: 14px;
  p{
    font-size: 12px;
    font-weight: bold;
    margin: 0;
  }
}
.wrongInput{
  border: 3px solid #292929 !important;
}
/* Media Queries */
/* Extra Small */
@media(min-width:0px) and (max-width:767px){
  section{
    text-align: left;
    margin-top: 16px;
    min-height: 80vh;
  }
  a{
    display: block;
    text-align: center;
  }
  h1, p, h1 + p{
    text-align: left;
  }
  h1 + p{
    padding-bottom: 16px;
    margin-bottom: 21px;
  }
}
@media(min-width:768px) and (max-width:991px){
  section{
    min-height: 60vh;
  }
}
@media(min-width:992px) and (max-width:1199px){
  section{
    min-height: 70vh;
  }
}
</style>



