<template lang="pug">
  .div
    button.cart(type='button', data-toggle='modal', :data-target="'#shoppingCartXs' + _uid")
      i(v-if='cart.length > 0').material-icons.md-24 add_shopping_cart
      i(v-else).material-icons.md-24 shopping_cart
    .modal(role='dialog', :id="'shoppingCartXs' + _uid")
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-content-wrapper
            .modal-header
              h3 {{ translations.shopping_cart_title | tktranslate(lang) }}
            .modal-body
              tk-table-basket.hidden-xs(v-if='cart.length > 0' :translations='translations')
              tk-table-basket-xs.hidden-sm.hidden-md.hidden-lg(v-if='cart.length > 0' :translations='translations')
              .empty(v-else)
                h4 Cart Empty
                p Select tickets an they will appear here
                //.tickets
                  tk-Modal(:translations='translations', @closeBasket='hideShoppingCart')
          .modal-footer
            .row
              .col-sm-4.hidden-xs
                .footer-wrapper
                  .footer-body
              .col-sm-4.hidden-xs
                .footer-wrapper
                  .footer-body
              .col-sm-4.col-xs-12
                .footer-wrapper
                  .footer-body
                    tk-primary-button(v-if='this.$route.name === "Checkout"' name='continue shopping' data-dismiss="modal")
                    tk-primary-button.addToBasket(v-if='cart.length > 0 && this.$route.name !== "Checkout"' @click='toCheckout', :name="translations.shopping_cart_to_checkout | tktranslate(lang)")
                    tk-disabled-button.addToBasket(v-if='!cart.length > 0 && this.$route.name !== "Checkout"' :name="translations.shopping_cart_to_checkout | tktranslate(lang)")
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage',
        lang: 'selectedLanguage',
        cart: 'get_cart'
      })
    },
    methods: {
      hideShoppingCart () {
        console.log('hide')
        window.jQuery('#shoppingCartXs' + this._uid).modal('hide')
      },
      toCheckout () {
        window.jQuery('#tickets' + this._uid).modal('hide')
        this.$router.push({name: 'Checkout'})
      },
      translate (translate) {
        return this.$options.filters.translate(translate)
      }
    }
  }
</script>

<style lang="less" scoped>
.empty{
  text-align: center;
  margin-top: 80px;
  img{
    height: 120px;
    margin-bottom: 16px;
  }
}
.div{
  display: inline-block;
}
.cart{
  position: relative;
  font-size: 14px;
  outline: 0;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  .material-icons{
    font-size: 20px;
    vertical-align: middle;
    margin-top: -4px;
    margin-left: 4px;
  }
  span{
    position: absolute;
    font-size: 10px;
    vertical-align: super;
    background-color: #292929;
    color: #fff;
    top: 3px;
    right: 15px;
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
  }
}
.modal-dialog{
  width: 770px;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  button{
    outline: 0;
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    border: none;
    background-color: transparent;
  }
}
.modal-body{
  padding: 40px 0;
  p{
    margin-bottom: 8px;
  }
}
.modal-content-wrapper{
  padding: 0 25px;
}
.modal-header{
  text-align: center;
  border-bottom: 1px solid #979797;
  padding: 20px 0;
  h3{
    margin: 4px 0;
  }
}
.modal-content{
  background-color: #f7f7f7;
  border-radius: 0;
  height: 100vh;
  overflow-y: scroll;
  padding-bottom: 60px;
}
.modal-footer{
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  padding: 0 25px;
  .footer-wrapper{
    display: table;
    height: 75px;
    width: 100%;
    .footer-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
}
@media(min-width:0px) and (max-width:767px){
  .modal-footer{
    position: fixed;
    bottom: 60px;
    width: 100%;
    button{
      width: 100%;
    }
  }
  .modal-body{
    margin-bottom: 80px;
  }
  .modal-dialog{
    height: 100vh;
    overflow-y: scroll;
    width: auto !important;
  }
  .modal-close{
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    border-top: 1px solid #979797;
    background-color: #fff;
    text-align: center;
    z-index: 1;
    padding: 20px;
    button{
      color: #292929;
      border: none;
      background-color: transparent;
      padding: 0;
      text-transform: uppercase;
    }
  }
}
</style>



