<template lang="pug">
  .log-in
    button#logInButton(type='button', data-toggle='modal', data-target='#LogIn') LogIn
    #LogIn.modal.fade(role='dialog')
      transition(name="component-fade" mode="out-in")
        component(v-bind:is="view", @createAccount='showSignUp', @logIn='showLogIn', :translations='translations')
</template>

<script>
  import LogIn from '@/components/modal/LogIn'
  import SignUp from '@/components/modal/SignUp'
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    data () {
      return {
        logIn: true,
        view: 'v-a'
      }
    },
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    components: {
      'v-a': LogIn,
      'v-b': SignUp
    },
    methods: {
      showSignUp () {
        this.view = 'v-b'
      },
      showLogIn () {
        this.view = 'v-a'
      }
    }
  }
</script>

<style lang="less" scoped>
  .log-in{
    display: inline-block;
    margin-left: 16px;
  }
   button{
      border: none;
      text-align: center;
      text-transform: uppercase;
      padding: 0;
      transition: .3s;
      border-radius: 0;
      background-color: transparent;
      outline: none !important;
      &:hover {
        cursor: pointer;
      }
    }
  .modal-dialog{
    max-width: 402px;
  }
  .modal-body{
    text-align: left;
  }
  .component-fade-enter-active, .component-fade-leave-active {
    transition: opacity .3s ease;
  }
  .component-fade-enter, .component-fade-leave-to
  /* .component-fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
   /* Small */
  @media(min-width:768px) and (max-width:991px){
    button{
      font-size: 12px;
      padding: 0;
    }
    .log-in{
      margin-left: 16px;
    }
  }
</style>



