<template lang="pug">
  .dropdown.account
    button#dLabel(type='button', data-toggle='dropdown', aria-haspopup='true', aria-expanded='false') admin
      i.material-icons keyboard_arrow_down
    ul.dropdown-menu(aria-labelledby='dLabel')
      li
        router-link(:to="{'name': 'History'}")
          span {{ translations.user_link_1[defaultLang] | translate }}
      li
        router-link(:to="{'name': 'Settings'}")
          span {{ translations.user_link_2[defaultLang] | translate }}
      li
        router-link(:to="{'name': 'EventList'}")
          span(@click='logOut') {{ translations.user_link_3[defaultLang] | translate }}
</template>

<script>
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {
      logOut () {
        this.$store.commit(types.DELETE_TOKEN)
      }
    }
  }
</script>

<style lang="less" scoped>
.dropdown-menu{
  visibility: hidden;
  display: inline-block;
  opacity: 0;
  z-index: 2;
  padding: 10px 0;
  border-radius: 0;
  min-width: 152px;
  border: none;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  top: 32px;
  left: 0;
  right: 0;
  margin-left: auto;
  li{
    span{
      white-space: nowrap;
    }
    a{
      padding: 0 !important;
      text-decoration: none !important;
      text-transform: none;
    }
    span, a{
      letter-spacing: 0.5px;
      width: 100%;
      transition: 0.3s;
      display: block;
      margin: 0;
      text-align: right;
      padding: 10px;
      font-size: 14px;
      cursor: pointer;
      color: #292929;
      text-decoration: none;
      white-space: normal;
        &:hover{
        background-color: #f7f7f7;
        text-decoration: none;
      }
    }
  }
  &:after{
    z-index: -1;
    position: absolute;
    right: 16px;
    top: -7px;
    width: 16px;
    height: 16px;
    content: '';
    background-color: #fff;
    transform: rotate(135deg);
    box-shadow: -1px 1px 1px -1px rgba(0, 0, 0, 0.2);
  }
}
.account {
  display: inline-block;
  color: black;
  button{
    font-size: 14px;
    margin-left: 18px;
    color: #292929;
    border: none;
    background-color: transparent;
    padding: 0;
    outline: none !important;
    .material-icons{
      vertical-align: middle;
      font-size: 18px;
      padding-left: 8px;
    }
  }
}
.open > .dropdown-menu {
  visibility: visible;
  display: block;
  opacity:1;
  transition: all 0.1s linear;
  -webkit-transition: all 0.1s linear;
  top: 40px;
}
</style>



