<template lang="pug">
  section
    .content
      .container
        .create-content
          .create-body
            button(@click='goTo') close
              i.material-icons clear
            h3.title Create New
            .row
              .col-sm-4.col-sm-offset-2
                router-link(:to="{name: 'EventSetup'}")
                  .page-content
                    .page-body
                      img(src='static/event.svg')
                      h3 Event Page
                      p Schedule events and sell tickets
              // .col-sm-4
                  router-link(:to="{name: 'Privacy'}")
                    .page-content
                      .page-body
                        img(src='static/landing.svg')
                        h3 Landing Page
                        p Promote events and special offers
              .col-sm-4
                router-link(:to="{name: 'Subscription'}")
                  .page-content
                    .page-body
                      img(src='static/subscription.svg')
                      h3 Subscription
                      p Unite multiple events in a single product
</template>

<script>
  import store from '@/store'

  export default {
    methods: {
      goTo () {
        this.$router.push({name: 'CreateEvent'})
      }
    },
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    }
  }
</script>

<style lang="less" scoped>
  .content{
    min-height: 80vh;
    .create-content{
      display: table;
      width: 100%;
      height: 80vh;
      button{
        text-align: right;
        outline: 0;
        padding: 0;
        margin-bottom: 24px;
        text-decoration: none;
        text-transform: uppercase;
        border: none;
        background-color: transparent;
        .material-icons{
          font-size: 18px;
          vertical-align: text-bottom;
          padding-left: 4px;
        }
      }
      .create-body{
        text-align: right;
        display: table-cell;
        vertical-align: middle;
        a{
          text-transform: none;
          text-decoration: none;
        }
        .title{
          text-align: center;
          border-bottom: 1px solid #979797;
          margin-bottom: 62px;
          padding-bottom: 24px;
        }
        .page-content{
          display: table;
          width: 100%;
          transition: 0.3s;
          text-align: center;
          background-color: #fff;
          box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
          padding: 24px;
          height: 264px;
          &:hover{
            box-shadow: 0 8px 16px 0 rgba(0,0,0,0.3);
          }
          .page-body{
            display: table-cell;
            vertical-align: middle;
            p{
              color: #9a9a9a;
            }
          }
          h3{
            color: #292929;
            border: none;
          }
        }
      }
    } 
  }
</style>


