<template>
  <gmap-map
    :center="center"
    :zoom='parseInt(main_content.custom_fields.zoom)'
    map-type-id="roadmap"
    style="width: 100%; height: 376px"
  >
    <gmap-marker
      :key="index"
      v-for="(m, index) in markers"
      :position="m.position"
      :clickable="true"
      :draggable="true"
      @click="center=m.position"
    ></gmap-marker>
  </gmap-map>
</template>

<script>
  import * as VueGoogleMaps from 'vue2-google-maps'
  import Vue from 'vue'
  Vue.use(VueGoogleMaps, {
    load: {
      key: 'AIzaSyCILt4m-5ujba6NgTJM03899OmkRFV3NT0',
      v: 'OPTIONAL VERSION NUMBER'
    }
  })
  export default {
    props: [ 'main_content' ],
    data () {
      return {
        center: {lat: parseFloat(this.main_content.custom_fields.lat), lng: parseFloat(this.main_content.custom_fields.lng)},
        markers: [{
          position: {lat: parseFloat(this.main_content.custom_fields.lat), lng: parseFloat(this.main_content.custom_fields.lng)}
        }]
      }
    }
  }
</script> 

<style lang="less" scoped>
.vue-map-container{
  margin-bottom: 32px;
}
</style>



