<template lang="pug">
  section
    tk-header-admin
    .settings
      .container
        #settings
          .row
            .col-sm-4
              .settings-content
                .settings-body
                  h2 Main Settings
            .col-sm-8.text-right
              .settings-content
                .settings-body
                  tk-secondary-button(name='revert to saved')
                  tk-primary-button(name='save changes')
    .container
      .navigation
        .row
          .col-sm-12
            .menu-content
              .menu-body
                router-link.current(:to="{name: 'MainSettings'}") My Account
                router-link(:to="{name: 'ShopPreferences'}") Shop Preferences
                router-link(:to="{name: 'ManageTeam'}") Manage Team
                router-link(:to="{name: 'Notifications'}") Notifications
    .content
      .container
        .row
          .col-sm-4#js-scrollspy 
            nav#aside
              p My Account
              ul.js-scrollspy-nav
                li#personalLink(@click='personalSpy') 1. Account Information
                li#passwordLink(@click='passwordSpy') 2. Change Password
                li#newsletterLink(@click='newsletterSpy') 3. Newsletter Subscription
                li#deleteLink(@click='deleteSpy') 4. Delete Account
          .col-sm-8#internal-content
            div#personal-scroll
              h3 1. Account Information
              p All of the information we need to pre-fill the purchase details for you when you make a purchase.
              h4 Personal Info
              #personal.section.scrollspy
                .row
                  .col-sm-4.col-sm-offset-2
                    p.center Title
                  .col-sm-6
                    tk-custom-select-string(v-model='info.title', :options='title', style='margin-bottom: 24px;')
                  .col-sm-4.col-sm-offset-2
                    p.center First Name
                  .col-sm-6
                    tk-input-type-text(v-model='info.first_name')
                  .col-sm-4.col-sm-offset-2
                    p.center Last Name
                  .col-sm-6
                     tk-input-type-text(v-model='info.last_name')
                  .col-sm-4.col-sm-offset-2
                    p.center Email Address
                  .col-sm-6
                     tk-input-type-text(v-model='info.email').last
              h4 Billing address
              #billing.section.scrollspy
                .row
                  .col-sm-4.col-sm-offset-2
                    p.center Country
                  .col-sm-6
                    tk-custom-select-string(:options='countries ', style='margin-bottom: 24px;', v-model='info.country')
                  .col-sm-4.col-sm-offset-2
                    p.center Street, Address, Number
                  .col-sm-6
                    tk-input-type-text(v-model='info.street')
                  .col-sm-4.col-sm-offset-2
                    p.center City / Province
                  .col-sm-6
                     tk-input-type-text(v-model='info.city')
                  .col-sm-4.col-sm-offset-2
                    p.center ZIP / Postal Code
                  .col-sm-6
                     tk-input-type-text(v-model='info.zip').last
            div#password-scroll
              h3 2. Change Password
              p For safety reasons we recommend you change your password regularely.
              .row
                .col-sm-4.col-sm-offset-2
                  p.center Current Password
                .col-sm-6
                  tk-input-type-text-password
                .col-sm-4.col-sm-offset-2
                  p.center New Password
                .col-sm-6
                    tk-input-type-text-password
                .col-sm-4.col-sm-offset-2
                  p.center Repeat New Password
                .col-sm-6
                    tk-input-type-text-password
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                    tk-secondary-button.last(name='change password')
            div#subscription-scroll
              h3 3. Newsletter Subscription
              p We have news regarding your events, enter your email address to stay updated.
              .row
                .col-sm-4.col-sm-offset-2
                  p.center Email Address
                .col-sm-6
                  tk-input-type-text.last
                  // span Address saved
            div#delete-scroll
              h3 4. Delete Account
              p Delete all you account details and purchase history.
              .row
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                  tk-secondary-button.last(name='delete my account')
              p * The account details cannot be retraced after the account has been deleted, so only delete your account if you are sure no valuable information will be deleted.
            .row
              .col-sm-8
                tk-secondary-button.right(name='revert to saved')
                tk-primary-button(name='save changes')
    tk-footer-admin
</template>

<script>
  import store from '@/store'

  export default {
    data () {
      return {
        info: {
          title: '',
          first_name: '',
          last_name: '',
          email: '',
          billing_address: {
            country: '',
            street: '',
            city: '',
            zip: ''
          },
          password: {
            current: '',
            new: '',
            repeat: ''
          }
        },
        title: [
          'Mr.', 'Ms'
        ],
        countries: [
          'Austria', 'Belgium', 'Bulgaria', 'Croatia', 'Cyprus', 'Czech Republic', 'Denmark', 'Estonia',
          'Finland', 'France', 'Germany', 'Greece', 'Hungary', 'Ireland', 'Italy', 'Latvia',
          'Lithuania', 'Luxembourg', 'Malta', 'Netherlands', 'Poland', 'Portugal', 'Romania', 'Slovakia',
          'Slovenia', 'Spain', 'Sweden', 'United Kingdom'
        ],
        visibility: ['Public', 'Private'],
        language: ['English', 'Latvian'],
        currency: ['Euro', 'USD'],
        timezone: ['UTC+3', 'GMT']
      }
    },
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    },
    methods: {
      personalSpy () {
        document.getElementById('personal').scrollIntoView({behavior: 'smooth', block: 'end', inline: 'end'})
      },
      passwordSpy () {
        document.getElementById('password-scroll').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      newsletterSpy () {
        document.getElementById('subscription-scroll').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      deleteSpy () {
        document.getElementById('delete-scroll').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      }
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')

      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '350') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    }
  }
</script>

<style lang="less" scoped>
#personal, #billing, #password-scroll, #subscription-scroll, #delete-scroll{
  margin-bottom: 32px;
  border-bottom: 1px solid #979797;
}
.navigation{
  border-bottom: 1px solid #979797;
  margin-bottom: 32px;
  .menu-content{
    display: table;
    height: 88px;
    width: 100%;
    .menu-body{
      display: table-cell;
      vertical-align: middle;
      a{
        text-transform: none;
        position: relative;
        text-decoration: none;
        margin-right: 50px;
        color: #292929;
        font-size: 18px;
        .router-link-exact-active{
          position: relative;
          width: 10px;
          height: 2px;
          -webkit-text-stroke: 1px #292929;
          &:after{
            content: "";
            position: absolute;
            width: 10px;
            height: 1px;
            background-color: #292929;
            left: 0;
            right: 0;
            bottom: -10px;
            margin: 0 auto;
          }
        }
      }
    }
  }
}               
.settings{
  background-color: #f7f7f7;
  #settings{
    border-bottom: 1px solid #979797;
  }
  .settings-content{
    display: table;
    height: 116px;
    width: 100%;
    .settings-body{
      display: table-cell;
      vertical-align: middle;
      h2{
        margin: 0;
      }
      button{
        margin-left: 24px;
      }
      .back{
        text-transform: uppercase;
        padding: 0;
        border: none;
        outline: 0;
        text-decoration: underline;
        background-color: transparent;
      }
    }
  }        
}
.content{
  margin-bottom: 120px;
}
nav{
  padding: 24px;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
  p{
    color: #9a9a9a;
    text-transform: uppercase;
    margin-bottom: 24px;
  }
  ul{
    text-decoration: none;
    padding: 0;
    list-style-type: none;
    li{
      margin-bottom: 16px;
      cursor: pointer;
      &:last-child{
        margin: 0;
      }
    }
    a{
      cursor: pointer;
      text-transform: capitalize;
      text-decoration: none;
      &:hover{
        color: #292929;
      }
    }
  }
}
.active{
  font-weight: bold;
}
.center{
  margin: 0;
  margin-top: 12px;
}
.current{
  position: relative;
  -webkit-text-stroke: 1px #292929;
  &:after{
    position: absolute;
    content: '';
    width: 14px;
    height: 1px;
    background-color: #292929;
    bottom: -11px;
    left: 0;
    right: 0;
    margin: auto;
  }
}
.fixed{
  position: fixed;
  background-color: #f7f7f7;
  z-index: 1;
  width: 360px;
  top: 32px;
}
input[type='text']{
  margin-bottom: 24px;
}
.last{
  margin-bottom: 32px !important;
}
.right{
  margin-right: 32px;
}
h3{
  margin-top: 0;
  +p{
    margin-bottom: 32px;
  }
}
h4{
  margin-bottom: 24px;
}
</style>



