<template lang="pug">
  .modal-dialog
    .modal-close
      button(data-dismiss="modal") close
        //button(data-dismiss="modal") {{ translations.event_page_calendar_close[defaultLang] | translate }}
        i.material-icons clear
    .modal-content
      .modal-content-wrapper
        .modal-header
          slot(name="header")
        .modal-body
          slot
      .modal-footer(v-if='footer')
          slot(name="footer")
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: {
      footer: {
        type: Boolean
      },
      translations: {
        type: Object
      }
    },
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    }
  }
</script>

<style lang="less" scoped>
.modal-dialog{
  width: 770px;
}
.modal-header{
  text-align: center;
  border-bottom: 1px solid #979797;
  padding: 0;
  margin-bottom: 32px;
  h3{
    margin: 0;
    padding: 8px 0 24px 0;
  }
}
.content-wrapper{
  display: table;
  width: 100%;
  height: 80px;
}
.modal-body{
  width: 700px;
  text-align: left;
  margin: 0 auto;
  padding: 0;
}
.modal-content-wrapper{
  padding: 16px;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  .material-icons{
    font-size: 18px;
    vertical-align: text-bottom;
    padding-left: 4px;
  }
  button{
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    width: auto;
    border: none;
    background-color: transparent;
    outline: none !important;
  }
}
.modal-content{
  background-color: #f7f7f7;
  border-radius: 0;
  border: none;
  }
  .modal-footer{
    padding: 16px 32px;
    background-color: #fff;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  }
   @media(min-width:0px) and (max-width:767px){
     .modal-content-wrapper{
      min-height: 90vh;
    }
    .modal-content{
      height: 100vh;
      overflow-y: scroll;
      padding-bottom: 160px;
    }
    .modal-body{
       padding: 0;
       width: auto;
    }
    .modal-close{
      box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
      display: block;
      position: absolute;
      bottom: 0;
      z-index: 9999 !important;
      width: 100%;
      background-color: #fff;
      text-align: center;
      z-index: 1;
      padding: 20px;
      button{
        color: #292929;
        border: none;
        background-color: transparent;
        padding: 0;
        text-transform: uppercase;
      }
    }
    .modal-footer{
        position: fixed;
        bottom: 60px;
        width: 100%;
      .col-sm-6{
        text-align: center;
        button{
          width: 100%;
        }
      }
    }
    .modal-footer{
      padding: 18px;
    }
  }
</style>


