import Vue from 'vue'
import Router from 'vue-router'
import ticketyl from '@/components/admin/ticketyl-page/ticketyl'
import AdminEventSettings from '@/components/admin/event-settings-page/AdminEventSettings'
import CreateNew from '@/components/admin/create-new-page/CreateNew'
import EventSetup from '@/components/admin/event-setup-page/EventSetup'
import AdminEventList from '@/components/admin/eventlist-page/AdminEventList'
import LandingVisualEditor from '@/components/admin/visual-editor-page/LandingVisualEditor'
import BookingAndSales from '@/components/admin/booking-and-sales-page/BookingAndSales'
import TransactionManager from '@/components/admin/transaction-manager-page/TransactionManager'
import PageSetupEmpty from '@/components/admin/empty-setup-page/PageSetupEmpty'
import Subscription from '@/components/admin/subscription-page/Subscription'
import OnBoardingShop from '@/components/admin/onboarding-shop-page/OnBoardingShop'
import OnBoardingOrganization from '@/components/admin/onboarding-organization-page/OnBoardingOrganization'
import OnBoardingTeam from '@/components/admin/onboarding-team-page/OnBoardingTeam'
import MainSettings from '@/components/admin/main-settings-page/MainSettings'
import ShopPreferences from '@/components/admin/main-settings-page/ShopPreferences'
import ManageTeam from '@/components/admin/main-settings-page/ManageTeam'
import Notifications from '@/components/admin/main-settings-page/Notifications'
import NewMember from '@/components/admin/new-member-page/NewMember'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    { path: '/ticketyl', name: 'Ticketyl', component: ticketyl },
    { path: '/event-settings', component: AdminEventSettings },
    { path: '/create-new', name: 'CreateNew', component: CreateNew },
    { path: '/event-setup', name: 'EventSetup', component: EventSetup },
    { path: '/landing-visual-editor', name: 'VisualEditor', component: LandingVisualEditor },
    { path: '/booking-and-sales', component: BookingAndSales },
    { path: '/transaction-manager', name: 'Transaction', component: TransactionManager },
    { path: '/setup-empty', component: PageSetupEmpty },
    { path: '/subscription', name: 'Subscription', component: Subscription },
    { path: '/onboarding-organization', name: 'Organization', component: OnBoardingOrganization },
    { path: '/onboarding-shop', name: 'Shop', component: OnBoardingShop },
    { path: '/onboarding-team', name: 'Team', component: OnBoardingTeam },
    { path: '/main-settings', name: 'MainSettings', component: MainSettings },
    { path: '/shop-preferences', name: 'ShopPreferences', component: ShopPreferences },
    { path: '/manage-team', name: 'ManageTeam', component: ManageTeam },
    { path: '/notifications', name: 'Notifications', component: Notifications },
    { path: '/new-member/123/123', component: NewMember },
    { path: '/event-list', name: 'CreateEvent', component: AdminEventList }
  ],
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  }
})
