<template lang="pug">
  section
    header

      tk-payment-method.hidden-xs(:name="translations.checkout_footer_tickets_button | tktranslate(lang)", :translations='translations')


      #error_modal.modal.fade(role='dialog')
        .modal-dialog
          .modal-close
            button(data-dismiss="modal") Close &#10005;
          .modal-content
            .modal-body
              h2#code
              p#message
            .modal-footer
              tk-primary-button(@click='closeError', :name="'OK'", style='width: 100%;')

      .container
        .row
          .col-sm-3.col-md-3.col-lg-3.onTop
            .header-wrapper
              .header-body
                router-link(:to="{'name': 'EventList'}")
                  img(:src='translations.logo | tktranslate(selectedLang)', :alt='translations.logo_alt | tktranslate(selectedLang)').img-responsive
          .col-sm-4.col-md-5.col-lg-4.col-lg-offset-1.onTop
            .header-wrapper
              .header-body
                nav
                  //router-link(:to="{'name': 'EventList'}") {{ translations.events | tktranslate(selectedLang) }}
                  //router-link(:to="{'name': 'Specific'}") {{ 'Specific' | translate }}
          //.col-sm-5.col-md-4.col-lg-4
            .header-wrapper
              .header-body
                // tk-search-field
                tk-user(v-if="$store.getters.logInToken", :translations='translations')
                div(v-if="!$store.getters.logInToken", style="display: inline-block")
                  tk-currency#currency(:currency='currency') 
                  tk-language(:langs='lang', v-model='defaultLangs')
                  tk-log-in-modal(:translations='translations')
    .header-overlay.hidden-xs
      .container
        .row
          .col-sm-12
            .header-wrapper
              .header-body
                tk-search-field(:translations='translations')
                //tk-user(v-if="$store.getters.logInToken", :translations='translations')
                div(v-if="!$store.getters.logInToken", style="display: inline-block")
                  //tk-currency#currency(:currency='currency') 
                  tk-language(:langs='lang', v-model='defaultLangs', @input='changeLang')
                  tk-shopping-cart(:translations='translations')
                  //tk-log-in-modal(:translations='translations')
                //button.cart Cart
                  i.material-icons.md-24 shopping_cart
</template>

<script>
  import { mixin as onClickOutside } from 'vue-on-click-outside'
  import * as types from '@/store/mutation-types'
  import Vue from 'vue'
  import Nav from '@/components/header/Nav'
  import SearchField from '@/components/misc/SearchField'
  import Language from '@/components/header/Language'
  import Currency from '@/components/header/Currency'
  import User from '@/components/header/User'
  import LogInModal from '@/components/modal/LogInModal'
  import PaymentMethod from '@/components/modal/PaymentMethod'

  Vue.component('tk-nav', Nav)
  Vue.component('tk-search-field', SearchField)
  Vue.component('tk-language', Language)
  Vue.component('tk-currency', Currency)
  Vue.component('tk-user', User)
  Vue.component('tk-log-in-modal', LogInModal)

  import { mapGetters } from 'vuex'

  export default {
    mixins: [onClickOutside],
    name: 'Account',
    props: ['translations'],
    data () {
      return {
        currency: ['eur', 'czk'],
        defaultLangs: null,
        seen: false,
        mobile: false
      }
    },
    mounted () {
      window.jQuery(document.body).on('show.bs.modal,hidden.bs.modal', function () {
        window.jQuery('body').css('padding-right', '0')
      })
      const mq = window.matchMedia('(max-width: 767px)')
      if (mq.matches) {
        this.mobile = true
      }
    },
    computed: {
      ...mapGetters({
        lang: 'languages',
        selectedLang: 'selectedLanguage',
        defaultLang: 'defaultLanguage',
        error: 'error'
      })
    },
    methods: {
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      currentLang (a) {
        this.$emit('input', a)
      },
      closeError () {
        window.jQuery('#error_modal').modal('hide')
      },
      changeLang (lang) {
        this.$store.commit(types.SET_LANGUAGE, lang)
      }
    },
    watch: {
      /*
      defaultLangs (def) {
        this.$store.commit(types.SET_LANGUAGE, def)
      },
      */
      selectedLang (l) {
        if (this.defaultLangs === null) {
          this.defaultLangs = l
        }
      }
    },
    components: {
      tkPaymentMethod: PaymentMethod
    }
  }
</script>

<style lang="less" scoped>
  #error_modal {
    button{
      outline: 0;
      background-color: #292929 !important;
    }
    .modal-dialog{
      max-width: 402px;
    }
    .modal-close{
      padding: 10px 0;
      text-align: right;
      .material-icons{
        font-size: 18px;
        vertical-align: text-bottom;
        padding-left: 4px;
      }
      button{
        padding: 0;
        font-size: 14px;
        text-transform: uppercase;
        color: #fff;
        width: auto;
        border: none;
        background-color: #292929 !important;
        outline: none !important;
      }
    }
    .modal-content{
      padding: 24px;
      background-color: #f7f7f7;
      border-radius: 0;
      border: none;
      .modal-body{
        padding: 0;
        border-bottom: 1px solid #979797;
        padding-bottom: 24px;
        margin-bottom: 24px;
        p{
          margin: 0;
          font-size: 16px;
        }
        h2{
          color: red;
          font-size: 12px;
          margin-top: 0;
        }
      }
    }
    .modal-footer{
      padding: 0;
    }
    @media(min-width:0px) and (max-width:767px){
      .modal-content{
        min-height: 100vh;
      }
      .modal-close{
        box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
        display: block;
        position: absolute;
        bottom: 0;
        width: 100%;
        background-color: #fff;
        text-align: center;
        z-index: 1;
        padding: 20px;
        button{
          color: #292929;
          border: none;
          background-color: transparent;
          padding: 0;
          text-transform: uppercase;
        }
      }
    }
  }
  .onTop{
    z-index: 2;
  }
  .header-overlay{
    position: absolute;
    top: 0;
    width: 100%;
    text-align: right;
    .header-wrapper{
      display: table;
      height: 72px;
      width: 100%;
      .header-body{
        position: relative;
        display: table-cell;
        vertical-align: middle;
      }
    }
  }
  header{
    font-size: 14px;
    position: relative;
    .col-sm-5{
      text-align: right;
    }
    .col-sm-4{
      &:first-child{
        text-align: left;
      }
      &:nth-child(2){
        text-align: center;
      }
      &:last-child{
        text-align: center;
      }
    }
    .header-wrapper{
      display: table;
      height: 72px;
      width: 100%;
      .header-body{
        position: relative;
        display: table-cell;
        vertical-align: middle;
      }
    }
    button{
      text-transform: uppercase;
      background-color: transparent;
      border: none;
      margin-left: 16px;
      &:first-child{
        margin-left: 0;
      }
      .fa{
        padding-left: 8px;
      }
    }
    img{
      height: 64px;
      width: auto;
    }
    nav{
      a{
        cursor: pointer;
        text-transform: none;
        text-decoration: none;
        color: #292929;
        padding: 0 24px;
        border-right: 1px solid #292929;
        &:last-child{
          border-right: none;
        }
        &:hover{
          cursor: pointer;
          text-decoration: none;
        }
      }
    }
  }
  /* Media Queries */
  /* Extra Small */
   @media(min-width:0px) and (max-width:767px){
    header{
      display: none;
      height: 100vh;
      width: 100%;
      .container{
        display: table-cell;
        vertical-align: middle;
      }
    }
    .header-body{
      text-align: center;
    }
    nav{
      a{
        position: relative;
        text-transform: capitalize;
        text-decoration: none;
        display: block;
        margin-bottom: 56px;
        border-right: none !important;
        &:last-child{
          margin-bottom: 0;
          &:after{
            content: none;
          }
        }
        &:after{
          position: absolute;
          content: '';
          height: 1px;
          width: 16px;
          background-color: #292929;
          margin: 0 auto;
          left: 0;
          right: 0;
          bottom: -24px;
        }
      }
    }
  }
  .cart{
    border: 1px solid #292929;
    margin-left: 16px;
    outline: none;
    height: 40px;
    text-transform: uppercase;
    background-color: transparent;
    padding: 0 24px;
    .material-icons{
      padding-left: 8px;
      font-size: 16px;
      vertical-align: sub;
    }
  }
  /* Small */
  @media(min-width:768px) and (max-width:991px) {
    .col-sm-5{
      text-align: right;
    }
    header{
      button{
        font-size: 12px;
      }
      img{
        height: 40px;
      }
      nav{
        a{
          font-size: 12px;
          padding: 0 12px;
        }
      }
    }
  }
  /* Medium */
  @media(min-width:992px) and (max-width:1199px) {
    header{
      img{
        height: 60px;
      }
    }
  }
  /* Large */
  @media(min-width:1200px){

  }
</style>


