import * as types from '../mutation-types'

const state = {
  items: [],
  price: {
    'total': 0,
    'order_lines_total': 0,
    'applied_charges': []
  },
  tickets: 0,
  total: null,
  charge_groups: [],
  details: {},
  charge_group: function (code) {
    for (var i in this.charge_groups) {
      if (this.charge_groups[i].code === code) {
        return this.charge_groups[i]
      }
    }
  },
  update: function () {
    var currency = {
      'code': 'eur',
      'translations': {
        'symbol': { en: '€' }
      }
    }
    this.price['total'] = 0
    this.price['order_lines_total'] = 0
    this.price['applied_charges'] = []
    this.tickets = 0
    for (var i in this.items) {
      this.tickets += this.items[i].count
      this.price['order_lines_total'] += this.items[i].count * this.items[i].product.price.amount
    }
    this.price['total'] += this.price['order_lines_total']

    for (i in this.items) {
      for (var k in this.items[i].product.charges.must) {
        var group = this.charge_group(this.items[i].product.charges.must[k])
        var charge = this.charge_for(group, this.items[i])
        this.price['total'] += charge.amount
        this.price['applied_charges'].push({
          charge_group: group,
          applied: charge
        })
      }
    }
    var groups = []
    for (i in this.price['applied_charges']) {
      var index = null
      for (k in groups) {
        if (groups[k].charge_group.code === this.price['applied_charges'][i].charge_group.code) {
          index = k
        }
      }
      if (index === null) {
        groups.push(this.price['applied_charges'][i])
      } else {
        groups[index].applied.amount += this.price['applied_charges'][i].applied.amount
      }
    }
    this.price['applied_charges'] = groups
    this.total = {
      amount: this.price['total'],
      currency: currency
    }
  },
  charge_for (group, item) {
    var price = 0
    var currency = {
      'code': 'eur',
      'translations': {
        'symbol': { en: '€' }
      }
    }
    var ticketCount = 0

    for (var p in this.items) {
      var prod = this.items[p].product
      if (prod.charges.must.indexOf(group.code) !== -1) {
        ticketCount += this.items[p].count
      }
    }
    if (group.configuration.included === 'included') {
      return {
        amount: price,
        currency: currency
      }
    }
    for (var k in group.charges) {
      var charge = group.charges[k]
      var apply = true
      for (var r in charge.rules) {
        var rule = charge.rules[r]
        var a = null
        var b = parseInt(rule.value)

        if (rule.key === 'ticket_count') { a = ticketCount }
        if (rule.operator === '>' && a <= b) { apply = false }
        if (rule.operator === '<' && a >= b) { apply = false }
        if (rule.operator === '=' && a !== b) { apply = false }
      }
      var amount = charge.price.amount
      if (charge.price.currency.code === 'eur') {
        amount = amount / ticketCount * item.count
      }
      if (apply) {
        if (charge.direction > 0) {
          price += amount
        } else {
          price -= amount
        }
      }
    }
    return {
      amount: price,
      currency: currency
    }
  },
  price_for_charge (chargeGroup) {
    var price = 0
    var currency = {
      'code': 'eur',
      'translations': {
        'symbol': { en: '€' }
      }
    }
    var ticketCount = 0

    for (var p in this.items) {
      var prod = this.items[p].product
      if (prod.charges.deny.indexOf(chargeGroup.code) === -1) {
        ticketCount += this.items[p].count
      }
    }
    if (chargeGroup.configuration.included === 'included') {
      return {
        amount: price,
        currency: currency
      }
    }
    for (var k in chargeGroup.charges) {
      var charge = chargeGroup.charges[k]
      var apply = true
      for (var r in charge.rules) {
        var rule = charge.rules[r]
        var a = null
        var b = parseInt(rule.value)

        if (rule.key === 'ticket_count') { a = ticketCount }
        if (rule.operator === '>' && a <= b) { apply = false }
        if (rule.operator === '<' && a >= b) { apply = false }
        if (rule.operator === '=' && a !== b) { apply = false }
      }
      if (apply) {
        if (charge.direction > 0) {
          price += charge.price.amount
        } else {
          price -= charge.price.amount
        }
      }
    }
    return {
      amount: price,
      currency: currency
    }
  }
}
const mutations = {
  [types.ADD_TO_CART] (state, item) {
    state.items.push(item)
    state.update()
  },
  [types.REMOVE_FROM_CART] (state, index) {
    state.items.splice(index, 1)
    state.update()
  },
  [types.EMPTY_CART] (state, index) {
    state.items = []
    state.update()
  },
  [types.SHOP_CHARGES] (state, charges) {
    state.charge_groups = charges
    state.update()
  },
  [types.SET_ORDER_DETAILS] (state, details) {
    state.details = details
  }
}
const getters = {
  get_cart: state => {
    return state.items
  },
  details: state => {
    return state.details
  },
  get_basket: state => {
    return state
  }
}
export default {
  state,
  mutations,
  getters
}



