<template lang="pug">
  .div
    button(type='button', data-toggle='modal', data-target='#paymentCard') Payment Card
    #paymentCard.modal.fade(role='dialog')
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-content-wrapper
            .modal-header
              h3 {{ translations.checkout_payment_card_title[defaultLang] | translate }}
            .modal-body
              .row
                .col-sm-6.col-sm-offset-3
                  p {{ translations.checkout_payment_card_name[defaultLang] | translate }}
                  tk-InputTypeText(placeholder="FULL NAME")
                .col-sm-6.col-sm-offset-3
                  p {{ translations.checkout_payment_card_number[defaultLang] | translate }}
                  tk-InputTypeText(placeholder="*** *** *** ***")
                .col-sm-6.col-sm-offset-3
                  .row
                    .col-sm-7
                      p {{ translations.checkout_payment_card_expiry_date[defaultLang] | translate }}
                      tk-InputTypeText(placeholder="MM / GG")
                    .col-sm-5
                      p {{ translations.checkout_payment_card_security_code[defaultLang] | translate }}
                      tk-InputTypeText(placeholder="CVC")
                .col-sm-6.col-sm-offset-3
                  .row
                    .col-sm-6
                      img(src="../../assets/ssl.png")
                    .col-sm-6
                      img(src="../../assets/powered-by-stripe.png").pull-right
                  button {{ translations.checkout_payment_card_back_button[defaultLang] | translate }}
          .modal-footer
            .row
              .col-sm-4 
                .footer-wrapper
                  .footer-body
                    p {{ translations.checkout_payment_card_footer_tickets[defaultLang] | translate }}
                      strong 4
              .col-sm-4
                .footer-wrapper
                  .footer-body
                    p {{ translations.checkout_payment_card_footer_total[defaultLang] | translate }}
                      strong 206.00 EUR
              .col-sm-4
                .footer-wrapper
                  .footer-body
                    tk-primary-button(:name="translate(translations.checkout_payment_card_footer_button[defaultLang])")
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {
      translate (translate) {
        return this.$options.filters.translate(translate)
      }
    }
  }
</script>

<style lang="less" scoped>
.modal-dialog{
  width: 770px;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  button{
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    border: none;
    background-color: transparent;
  }
}
.modal-body{
  padding: 20px 0;
  p{
    margin-bottom: 8px;
  }
  img{
    height: 48px;
  }
  button{
    margin: 16px 0;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    text-decoration: underline;
  }
}
.modal-content-wrapper{
  padding: 0 25px;
}
.modal-header{
  text-align: center;
  border-bottom: 1px solid #979797;
  padding: 20px 0;
  h3{
    margin: 4px 0;
  }
}
.modal-content{
  background-color: #f7f7f7;
  border-radius: 0;
}
.modal-footer{
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  padding: 0 25px;
  strong{
    margin-left: 16px;
  }
  p{
    margin: 0;
  }
  .col-sm-4{
    text-align: center;
  }
  .col-sm-4:last-child{
    text-align: right;
  }
  .col-sm-4:first-child{
    text-align: left;
  }
  .footer-wrapper{
    display: table;
    height: 75px;
    width: 100%;
    .footer-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
}
</style>



