<template lang="pug">
  section
    tk-header-admin
    .events
      .container
        .row
          .col-sm-6
            .eventlist-content
              .eventlist-body
                h2 Events
          .col-sm-6.text-right
            .eventlist-content
              .eventlist-body
                tk-primary-button(name='create new', @click='goTo')
        nav
          .row
            .col-sm-7
              .menu-content
                .menu-body
                  router-link(:to="{name: 'Events'}") All
                  router-link(:to="{name: 'Events', query: {filter: 'upcoming'}}") Upcoming
                  router-link(:to="{name: 'Events', query: {filter: 'public'}}") Public
                  router-link(:to="{name: 'Events', query: {filter: 'draft'}}") Draft
                  router-link(:to="{name: 'Events', query: {filter: 'passed'}}") Passed
                  router-link(:to="{name: 'Events', query: {filter: 'archived'}}") Archived
            .col-sm-5.text-right
              .menu-content
                .menu-body
                  tk-search-admin
                  span.search search
        .empty(v-if='1 > 0')
          .empty-content
            .empty-body
              img(src='/static/calendar_emptystate.svg')
              h2 No Events Created
              p See that important-looking 
                strong black button?
              p 
                strong Click it!
        .row(v-if='1 < 0')
          .col-sm-4
            .event
              .event-content
                .event-body
                  h3 Dinner In The Sky Riga
              .events-date
                .events-date-body
                  .row
                    .col-sm-6.text-left
                      p JUL 16 - AUG 28
                    .col-sm-6.text-right
                      span draft
          .col-sm-4
            .event
              .event-content
                .event-body
                  h3 Dinner In The Sky Riga
              .events-date
                .events-date-body
                  .row
                    .col-sm-6.text-left
                      p JUL 16 - AUG 28
                    .col-sm-6.text-right
                      span draft
          .col-sm-4
            .event
              .event-content
                .event-body
                  h3 Dinner In The Sky Riga
              .events-date
                .events-date-body
                  .row
                    .col-sm-6.text-left
                      p JUL 16 - AUG 28
                    .col-sm-6.text-right
                      span draft
          .col-sm-4
            .event
              .event-content
                .event-body
                  h3 Dinner In The Sky Riga
              .events-date
                .events-date-body
                  .row
                    .col-sm-6.text-left
                      p JUL 16 - AUG 28
                    .col-sm-6.text-right
                      span draft
    tk-footer-admin
</template>

<script>
  import store from '@/store'

  export default {
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    },
    methods: {
      goTo () {
        this.$router.push({name: 'CreateNew'})
      }
    }
  }
</script>

<style lang="less" scoped>
.empty{
  text-align: center;
  .empty-content{
    display: table;
    width: 100%;
    height: 60vh;
    .empty-body{
      display: table-cell;
      vertical-align: middle;
      img{
        height: 171px;
        margin-bottom: 36px;
      }
    }
  }         
  p{
    font-size: 18px;
  }
}
.search{
  font-size: 18px;
  text-transform: uppercase;
  color: #9a9a9a;
  margin-left: 8px;
}
.event{
  margin-bottom: 32px;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
}
.events-date{
  background-color: #fff;
  display: table;
  width: 100%;
  height: 72px;
  .events-date-body{
    padding: 24px;
    display: table-cell;
    vertical-align: middle;
    p{
      margin: 0;
    }
    span{
      text-transform: uppercase;
      font-weight: bold;
      border: 1px solid #292929;
      padding: 4px;
    }
  }
}
.event-content{
  background-image: url('/static/dits.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  color: #fff;
  display: table;
  width: 100%;
  height: 192px;
  .event-body{
    display: table-cell;
    vertical-align: middle;
  }
}
  .events{
    min-height: 80vh;
    margin-bottom: 96px;
    .eventlist-content{
      margin-bottom: 24px;
      display: table;
      height: 100px;
      width: 100%;
      .eventlist-body{
        display: table-cell;
        vertical-align: bottom;
        h2{
          margin: 0;
        }
      }
    }
    nav{
      border-bottom: 1px solid #979797;
      margin-bottom: 62px;
      .menu-content{
        display: table;
        height: 88px;
        width: 100%;
        .menu-body{
          display: table-cell;
          vertical-align: middle;
          a{
            text-transform: none;
            position: relative;
            text-decoration: none;
            margin-right: 50px;
            color: #292929;
            font-size: 18px;
            .router-link-exact-active{
              position: relative;
              width: 10px;
              height: 2px;
              -webkit-text-stroke: 1px #292929;
              &:after{
                content: "";
                position: absolute;
                width: 10px;
                height: 1px;
                background-color: #292929;
                left: 0;
                right: 0;
                bottom: -10px;
                margin: 0 auto;
              }
            }
          }
        }
      }
    }               
  }
</style>



