<template lang="pug">
  modal-content(:translations='translations')
    div(slot='header')
      h2 {{ translations.log_in_title[defaultLang] | translate }}
      p {{ translations.log_in_paragraph[defaultLang] | translate }}
    div
      .error(v-if="wrong")
        p {{ translations.log_in_error[defaultLang] | translate }}
      tk-input-type-text(:placeholder='translate(translations.log_in_email[defaultLang])', v-model='user.email')
      tk-input-type-text(:placeholder='translate(translations.log_in_password[defaultLang])', v-model='user.password', type='password')
      tk-primary-button.signIn(@click='signIn', :name="translate(translations.log_in_sign_in[defaultLang])", v-if='user.email && user.password') {{ translations.log_in_sign_in[defaultLang] | translate }}
      tk-disabled-button.disabled(:name="translate(translations.log_in_sign_in[defaultLang])", v-else)
      p {{ translations.log_in_new_user[defaultLang] | translate }}
      button.create(@click="$emit('createAccount')") {{ translations.log_in_create[defaultLang] | translate }}
      //
        .social
          p Or
          button#facebook
            i.fa.fa-facebook(aria-hidden='true')
            | Log In With Facebook
          button#google
            i.fa.fa-google(aria-hidden='true')
            | Log In With Google
</template>

<script>
  import ModalContent from '@/components/modal/ModalContent'
  import Api from '@/services/ApiService'
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    data () {
      return {
        wrong: false,
        user: {
          email: '',
          password: '',
          expires: '2017-10-14 10:00:23'
        }
      }
    },
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      signIn () {
        new Api().postUserAuth({email: this.user.email, password: this.user.password}).then(response => {
          this.$store.commit(types.ADD_TOKEN, response.data.token)
          this.$store.commit(types.DELETE_ADMIN_TOKEN)
          this.wrong = false
          window.jQuery('#LogInXs').modal('hide')
        }).catch(response => { this.wrong = true })
      }
    },
    components: {
      'modal-content': ModalContent
    }
  }
</script>

<style lang="less" scoped>
.modal-header{
  padding: 40px !important;
}
.error{
  background-color: #fff;
  border: 1px solid #bfbfbf;
  font-weight: bold;
  color: #f71e1e;
  padding: 16px 8px;
  margin-bottom: 24px;
  p{
    margin: 0;
  }
}
#facebook{
  background-color: #3b5998;
}
#google{
  background-color: #db3236;
}
.modal{
  z-index: 10001 !important;
}
.create{
  text-transform: uppercase;
  text-decoration: underline;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none !important;
}
.div{
  display: inline-block;
  margin-left: 20px;
  button{
    padding: 10px 18px;
    text-transform: uppercase;
    background-color: transparent;
  }
}
.modal-dialog{
  z-index: 10001 !important;
  max-width: 402px;
  min-height: 100vh;
}
.modal-body{
  text-align: left;
  .create{
    text-transform: uppercase;
    border: none;
    text-decoration: underline;
    background-color: transparent;
    padding: 0;
    margin-bottom: 0;
  }
  padding: 20px 0;
  a{
    display: block;
    font-size: 14px;
    text-transform: none;
    text-decoration: none;
    margin-bottom: 10px;
  }
  input[type='text'], input[type='password']{
    margin-bottom: 24px !important;
  }
  .disabled{
    margin-bottom: 25px;
    width: 100%;
    background-color: #bfbfbf;
    color: #f7f7f7;
    cursor: disabled;
  }
  .signIn{
    display: block;
    width: 100%;
    margin-bottom: 25px;
    transition: 0.3s;
    background-color: #292929;
    text-transform: uppercase;
    font-size: 18px;
    color: #fff;
    padding: 10px 18px;
    border: none;
    &:hover{
      background-color: #424242;
    }
  }
}
.modal-content-wrapper{
  padding: 0 25px;
  .modal-content{
    height: 100vh !important;
  }
}
.modal-header{
  text-align: left;
  border-bottom: 1px solid #979797;
  margin-bottom: 30px;
  h2{
    font-size: 32px;
    font-weight: bold;
    margin: 0 0 8px 0;
    text-align: left;
  }
  p{
    font-size: 14px;
    text-align: left;
    margin-bottom: 24px;
  }
}
  .modal-close{
    padding: 10px 0;
    text-align: right;
    button{
      padding: 0;
      font-size: 14px;
      text-transform: uppercase;
      color: #fff;
      border: none;
      background-color: transparent;
    }
  }
  .footer-col-wrapper{
    display: table;
    height: 50px;
    width: 100%;
    .footer-col{
      display: table-cell;
      vertical-align: middle;
    }
  }
  .modal-footer{
    background-color: #fff;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
    .col-sm-6:first-child{
      text-align: left;
      button{
        text-transform: uppercase;
        text-decoration: underline;
        color: #292929;
        background-color: transparent;
      }
    }
  }
  .modal-content{
    background-color: #f7f7f7;
    border-radius: 0;
    height: 100vh !important;
    textarea{
      color: #9a9a9a;
      font-size: 18px;
      padding: 10px;
      text-transform: uppercase;
      width: 100%;
    }
  }
  .social{
    .fa{
      margin-right: 16px;
    }
    p{
      position: relative;
      text-transform: uppercase;
      text-align: center;
      color: #9a9a9a;
      margin-bottom: 24px;
      &:after{
        content: '';
        position: absolute;
        right: 0;
        top: 50%;
        background-color: #9a9a9a;
        height: 1px;
        width: 40%;
      }
       &:before{
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        background-color: #9a9a9a;
        height: 1px;
        width: 40%;
      }
    }
    button{
      transition: 0.3s;
      background-color: #9a9a9a;
      text-transform: uppercase;
      font-size: 16px;
      color: #fff;
      width: 100%;
      padding: 10px 18px;
      border: none;
      margin-bottom: 24px;
      &:hover{
        background-color: #a9a9a9;
      }
    }
  }
  @media(min-width:0px) and (max-width:767px){
    .modal-dialog{
      max-width: 100%;
      height: 100vh;
    }
  }
  /* Small */
  @media(min-width:768px) and (max-width:991px){
  }
</style>



