<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
// import ApiService from '@/services/ApiService'
// import EventList from '@/components/pages/EventList'
import Page from '@/components/pages/Page'
import { mapGetters, mapMutations } from 'vuex'
import * as types from '@/store/mutation-types'

import Api from '@/services/ApiService'
import shop from '@/services/Shop/Shop'
// import Vue from 'vue'

// import routes from '@/router/routes'
// const api = new ApiService()
export default {
  name: 'app',
  created () {
    /* window.shop = this.shop
    window.serie = this.serie
    window.package = this.package
    window.routes = this.routes */

    global.api = new Api()
    if (this.$route.query.lang) {
      this.setLanguage(this.$route.query.lang)
    }

    shop
      .code('danceworld.ticketyl.com')
      .load('routes').on('routes', data => {
        this.$store.commit(types.SET_ROUTES, data)
        const routes = data
          .filter(route => { return route.translations.hasOwnProperty('name') })
          .map(route => {
            return {
              path: route.url,
              name: route.translations.name.en,
              component: Page,
              props: {
                pageId: route.id
              }
            }
          })
        this.$router.addRoutes(routes)
      })
    shop.load('shop').on('shop', model => {
      this.$store.commit(types.SET_SHOP, model)
      this.$store.commit(types.SHOP_CHARGES, model.own_charges)
      this.$store.commit(types.SET_DEFAULT_LANG, model.configuration.default_language)
      this.$store.commit(types.SET_LANGUAGES, model.allowed_languages)
      if (!this.lang) {
        this.setLanguage(types.SET_LANGUAGE, model.configuration.default_language)
      }
      shop.load('series').on('series', series => {
        this.$store.commit(types.SET_SERIES, series)
      })
    })
  },
  computed: {
    ...mapGetters({
      lang: 'selectedLanguage',
      shop: 'shop',
      serie: 'serie',
      package: 'package',
      routes: 'routes'
    })
  },
  methods: {
    ...mapMutations({
      setLanguage: types.SET_LANGUAGE
    })
  }
}
</script>

<style>
</style>



