<template lang="pug">
  section
    button(@click="getEvents", type='button', data-toggle='modal', :data-target="'#tickets' + _uid").seeTickets {{ translations.aside_tickets_button | tktranslate(lang) }}
    div.modal(role='dialog', :id="'tickets' + _uid")
      tk-event-calendar(@seeTickets='showTickets', v-if='!seeTickets', :translations='translations')
      tk-ticket-list(v-else, @differentDate='showCalendar' @toCheckout='checkout', :translations='translations')
</template>

<script>
// import moment from 'moment'
import EventCalendar from '@/components/single-event-page/EventCalendar'
import TicketList from '@/components/single-event-page/TicketList'
import { mapGetters } from 'vuex'

export default {
  name: 'CalendarModal',
  props: [
    'translations'
  ],
  data () {
    return {
      seeTickets: false
    }
  },
  computed: {
    ...mapGetters({
      package: 'package',
      lang: 'selectedLanguage'
    })
  },
  methods: {
    getEvents () {
      this.$emit('closeBasket')
      window.jQuery('#shoppingCart' + this._uid).modal('hide')
      /* var seriesId = this.$route.params.id
      var packageId = this.packageId */
    },
    showTickets () {
      this.seeTickets = true
    },
    showCalendar () {
      this.seeTickets = false
    },
    checkout () {
      window.jQuery('#tickets' + this._uid).modal('hide')
      this.$router.push({name: 'Checkout'})
    }
  },
  components: {
    tkTicketList: TicketList,
    tkEventCalendar: EventCalendar
  }
}
</script>

<style lang="less" scoped>
section{
    .seeTickets{
    width: 100%;
    transition: 0.3s;
    background-color: #292929;
    text-transform: uppercase;
    font-size: 18px;
    color: #fff;
    padding: 10px 18px;
    border: none;
    outline: none !important;
    cursor: pointer;
     &:hover{
      background-color: #424242;
    }
  }
}
.modal-header{
  text-align: center;
  border-bottom: 1px solid #979797;
  h3{
    margin: 10px 0;
  }
}
.nav{
    padding: 10px 0;
  .col-sm-6:first-child{
    text-align: right;
  }
}
.info{
  text-align: center;
  padding: 10px 0;
  .col-sm-4:first-child{
    text-align: left;
  }
  .col-sm-4:last-child{
    text-align: right;
  }
}
.tickets{
  background-color: #fff;
  padding: 10px;
  margin-bottom: 16px;
  .col-sm-6:last-child{
    text-align: right;
  }
  .selected{
    padding: 10px 0;
    border-top: 1px solid #979797;
    .selected-wrapper{
      display: table;
      height: 60px;
      width: 100%;
      .selected-body{
        display: table-cell;
        vertical-align: middle;
      }
    }
  }
  .col-sm-6:last-child{
    text-align: right;
  }
  .content-wrapper{
    display: table;
    width: 100%;
    height: 80px;
    .content-body{
      display: table-cell;
      vertical-align: middle;
      .sold{
        border: 1px solid #292929;
        text-transform: uppercase;
        font-weight: bold;
        padding: 5px;
      }
      .count{
        text-align: right;
        span{
          font-size: 18px;
          padding: 11px 20px;
          background-color: #fff;
          color: #292929;
          border-top: 1px solid #9a9a9a;
          border-bottom: 1px solid #9a9a9a;
        }
        span:first-child{
          border: none;
          font-size: 18px;
          padding: 12px 20px;
          background-color: #9a9a9a;
          color: #fff;
        }
        span:last-child{
          border: none;
          font-size: 18px;
          padding: 12px 20px;
          background-color: #292929;
          color: #fff;
        }
      }
        h3{
        margin: 10px 0;
      }
    }
  }                
}
.div{
  display: inline-block;
  margin-left: 20px;
}
.modal-dialog{
  max-width: 770px;
}
.modal-body{
  text-align: left;
  .create{
    text-transform: uppercase;
    border: none;
    text-decoration: underline;
    background-color: transparent;
    padding: 16px !important;
  }
  padding: 20px 0;
  p:first-child{
    font-weight: bold;
  }
  a{
    display: block;
    font-size: 14px;
    text-transform: none;
    text-decoration: none;
    margin-bottom: 10px;
  }
  .signIn{
    display: block;
    width: 100%;
    margin-bottom: 25px;
    transition: 0.3s;
    background-color: #292929;
    text-transform: uppercase;
    font-size: 18px;
    color: #fff;
    padding: 10px 18px;
    border: none;
    &:hover{
      background-color: #424242;
    }
  }
}
.modal-content-wrapper{
  padding: 0;
}
  .modal-close{
    padding: 10px 0;
    text-align: right;
    button{
      padding: 0;
      font-size: 14px;
      text-transform: uppercase;
      color: #fff;
      border: none;
      background-color: transparent;
    }
  }
  .footer-col-wrapper{
    display: table;
    height: 50px;
    width: 100%;
    .footer-col{
      display: table-cell;
      vertical-align: middle;
    }
  }
  .modal-footer{
    background-color: #fff;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
    .col-sm-6:first-child{
      text-align: left;
      button{
        text-transform: uppercase;
        text-decoration: underline;
        color: #292929;
        background-color: transparent;
      }
    }
  }
  .modal-content{
    background-color: #f7f7f7;
    border-radius: 0;
    textarea{
      color: #9a9a9a;
      font-size: 18px;
      padding: 10px;
      text-transform: uppercase;
      width: 100%;
    }
  }
  @media(min-width:0px) and (max-width:767px){
    .modal-dialog{
      width: auto !important;
    }
  }
</style>



