<template lang="pug">
  section
    tk-carousel(:carousel='carousel')
    .container
      .row
        .col-sm-8.col-sm-offset-2
          h1 Spend This Christmas In The Sky
          p Santa in the Sky is the latest concept of Events in the Sky
          h4 Who hasn’t dreamed of flying away with Santa Claus and his reindeer in their sleigh when they were young?
          p A real fairy tale world where 16 young, and not so young, children get the chance to live out their dream. They get to sit next to Santa Claus in his magically lit up and gilded sleigh. They can drink hot chocolate with him and warm themselves with a delicious waffle or a pancake with sugar.  And they will sing Christmas carols together…
          p Santa in the Sky is the latest concept of Events in the Sky, a Belgian company that specialises in original events at 50 metres high. World premieres like Dinner in the Sky, with its gastronomical star dinners, Marriage in the Sky, for a memorably divinewedding, or Lounge in the Sky, with its surreal atmosphere.
          p.last Events in the Sky organises its concepts in 55 countries all over the world, including recently also in Malaysia, Mexico and Vietnam.
          tk-small-carousel(:data='data')
          .gifts
            tk-primary-button(name='Get christmas gifts now')
          tk-map
          tk-gift(:translations='translations')
</template>

<script>
import { mapGetters } from 'vuex'

import SmallCarousel from '@/components/single-event-page/smallCarouselSlide'
import Carousel from '@/components/single-event-page/Carousel'
import Gift from '@/components/single-event-page/LookingForGiftCard'
import map from '@/components/single-event-page/map'

export default {
  props: ['translations'],
  data () {
    return {
      data: [
        {
          img: 'santa.jpg'
        },
        {
          img: 'santa2.jpg'
        },
        {
          img: 'santa3.jpg'
        },
        {
          img: 'santa4.jpg'
        },
        {
          img: 'santa5.jpg'
        },
        {
          img: 'santa.jpg'
        },
        {
          img: 'santa2.jpg'
        },
        {
          img: 'santa3.jpg'
        },
        {
          img: 'santa4.jpg'
        },
        {
          img: 'santa5.jpg'
        }
      ],
      carousel: [
        {
          start_date: '',
          start_month: '',
          divider: '',
          end_date: '',
          end_month: '',
          img: 'santa3.jpg'
        },
        {
          start_date: '',
          start_month: '',
          divider: '',
          end_date: '',
          end_month: '',
          img: 'santa5.jpg'
        },
        {
          start_date: '',
          start_month: '',
          divider: '',
          end_date: '',
          end_month: '',
          img: 'santa2.jpg'
        },
        {
          start_date: '',
          start_month: '',
          divider: '',
          end_date: '',
          end_month: '',
          img: 'santa.jpg'
        }
      ]
    }
  },
  computed: {
    ...mapGetters({
      selectedLang: 'selectedLanguage'
    })
  },
  components: {
    tkSmallCarousel: SmallCarousel,
    tkCarousel: Carousel,
    tkGift: Gift,
    tkMap: map
  }
}
</script>

<style lang="less" scoped>
h1 + p{
  margin-bottom: 32px;
}
.last{
  margin-bottom: 40px;
}
.gifts{
  text-align: center;
  padding: 32px 0;
  border-top: 1px solid #979797;
  border-bottom: 1px solid #979797;
  margin-bottom: 32px;
}
@media(min-width:0px) and (max-width:767px){
  .swiper-container{
    margin-bottom: 0 !important;
  }
}
</style>


