import api from '@/services/TicketylOS/TicketylOS'

class Shop {
  listeners = [];
  models = {};
  loaded = {};
  constructor () {
    if (window.shop) {
      this.shop = window.shop
    } else { this.shop = null }
    this.models.shop = api.shop().get
    this.models.routes = api.shop().routes
    this.models.series = api.shop().series
    for (var model in this.models) {
      if (window[model]) {
        this.loaded[model] = true
        this.models[model] = window[model]
      }
    }
  }
  code (code) {
    api.default.shop = code
    return this
  }
  load (model, data) {
    var self = this
    if (this.loaded[model]) {
      self.fire(model, this.models[model])
      return this
    }
    this.models[model](data).then(function (response) {
      self.models[model] = response
      self.loaded[model] = true
      window[model] = response
      self.fire(model, response)
    })
    return this
  }
  fire (model, data) {
    if (this.listeners[model]) {
      for (var i in this.listeners[model]) {
        this.listeners[model][i](data)
      }
    }
  }
  on (model, callback) {
    if (!this.listeners[model]) { this.listeners[model] = [] }
    this.listeners[model].push(callback)
    return this
  }
}
export default new Shop()



