<template lang="pug">
  section.visible-xs
    transition(name="fade")
      .input-group#mobSearchField(v-if="showPopover", v-on-click-outside="close")
        tk-input-type-text(type='text', :placeholder='translate(translations.search[defaultLang])')
        span.input-group-btn
          button.btn.btn-secondary.input-black-button-arrow-right(type='button', @click='find')
            i.material-icons.md-24 search
    #nav-open
      .container
        .row
          .col-xs-2
            .nav-content
              .nav-body
                button#open(type='button', data-toggle='modal', data-target='#navigation', @click='mansOpenNav')
                  i.material-icons dehaze
          .col-xs-8
            .nav-content
              .nav-body
                router-link(:to="{'name': 'EventList'}")
                  img(:src='translations.logo[defaultLang] | translate', :alt='translations.logo_alt[defaultLang] | translate')
          .col-xs-2
            .nav-content
              .nav-body
                tk-shopping-cart-mobile(:translations='translations')
    .mans-navigation#mans-nav
      .mans-navigation-content
        .mans-navigation-body
          .find-mans-nav
            button(@click="open")#find
              i.material-icons.md-24 search
          .close-mans-nav
            button(data-dismiss="modal", @click='mansCloseNav') {{ translations.close[defaultLang] | translate }}
              i.material-icons clear
          nav
            router-link(:to="{'name': 'EventList'}") 
              //span(@click='goToMain') {{ translations.events[defaultLang] | translate }}
            //router-link(:to="{'name': 'Specific'}") Specific
            //#logged-in(v-if="!$store.getters.logInToken")
            //tk-log-in-modal-xs(:translations='translations')
            //#settings
            tk-language(:langs='lang', v-model='defaultLangs', @input='changeLang')
            //tk-currency(:currency='currency')
          //#logged-in-nav(v-if="$store.getters.logInToken")
            .line
            nav
              router-link(:to="{'name': 'History'}") {{ translations.user_link_1[defaultLang] | translate }}
              router-link(:to="{'name': 'Settings'}") {{ translations.user_link_2[defaultLang] | translate }}
              router-link(:to="{'name': 'Events'}")
                span(@click='logOut') {{ translations.user_link_3[defaultLang] | translate }}
            .mobile-nav.logged-in
              p {{ translations.user_logged_in[defaultLang] | translate }}
              p admin
    // .nav-bar
      .nav-bar-inner
        #navigation.modal.animated.slideInLeft(role='dialog')
          .modal-dialog
            .modal-content
              .modal-body
                .navigation-tools
                  .row
                    .col-xs-6
                      .navigation-tools-content
                        .navigation-tools-body
                          .close-modal
                            button(data-dismiss="modal") close
                              i.material-icons clear
                    .col-xs-6
                      .navigation-tools-content
                        .navigation-tools-body
                          button(@click="open")#find
                            i.material-icons.md-24 search
                nav
                  router-link(:to="{'name': 'EventList'}") Events
                  router-link(:to="{'name': 'Specific'}") Specific
                #logged-in(v-if="!$store.getters.logInToken")
                  tk-log-in-modal-xs(:translations='translations')
                  #settings
                    tk-language(:langs='lang', v-model='defaultLangs')
                    tk-currency(:currency='currency')
                #logged-in-nav(v-if="$store.getters.logInToken")
                  .line
                  nav
                    router-link(:to="{'name': 'History'}") {{ translations.user_link_1[defaultLang] | translate }}
                    router-link(:to="{'name': 'Settings'}") {{ translations.user_link_2[defaultLang] | translate }}
                    router-link(:to="{'name': 'Events'}")
                      span(@click='logOut') {{ translations.user_link_3[defaultLang] | translate }}
                  .mobile-nav.logged-in
                    p {{ translations.user_logged_in[defaultLang] | translate }}
                    p admin
</template>

<script>
  import Vue from 'vue'
  import LogInModalXs from '@/components/modal/LogInModalXs'
  import { mixin as onClickOutside } from 'vue-on-click-outside'
  Vue.component('tk-log-in-modal-xs', LogInModalXs)
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'

  export default {
    mixins: [onClickOutside],
    name: 'Account',
    props: [
      'langs',
      'translations'
    ],
    data () {
      return {
        show: false,
        currency: ['eur', 'czk'],
        defaultLangs: '',
        seen: false,
        showPopover: false
      }
    },
    computed: {
      ...mapGetters({
        lang: 'languages',
        selectedLang: 'selectedLanguage',
        defaultLang: 'defaultLanguage',
        getTickets: 'getTickets'
      })
    },
    /*
    mounted () {
      var last = 0
      var nav = window.jQuery('#nav-open')
      window.jQuery(window).scroll(function (event) {
        var Pos = window.jQuery(this).scrollTop()
        var scroll = window.jQuery(window).scrollTop()
        if (scroll >= '240' && Pos > last) {
          nav.removeClass('fixed')
          nav.addClass('fixed-show')
        } else {
          nav.addClass('fixed')
          nav.removeClass('fixed-show')
        }
        last = Pos
      })
    },
    */
    methods: {
      goToMain () {
        window.jQuery('#mans-nav').css('margin-left', '-85%')
        window.jQuery('body').css('overflow-y', 'auto')
      },
      mansOpenNav () {
        window.jQuery('#mans-nav').css('margin-left', '0')
        window.jQuery('body').css('overflow-y', 'hidden')
      },
      mansCloseNav () {
        window.jQuery('#mans-nav').css('margin-left', '-85%')
        window.jQuery('body').css('overflow-y', 'auto')
      },
      find () {
        if (this.searchinput !== '') {
          this.$router.push({name: 'Search'})
          this.searchinput = ''
          window.jQuery('#searchBox').css('visibility', 'hidden !important')
        }
      },
      navdrop () {
        if (this.show === false) {
          this.show = true
        } else {
          this.show = false
        }
        window.jQuery('.modal-backdrop').remove()
      },
      open () {
        this.showPopover = true
        window.jQuery('#mans-nav').css('margin-left', '-85%')
        window.jQuery('body').css('overflow-y', 'auto')
      },
      close () {
        this.showPopover = false
      },
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      logOut () {
        this.$store.commit(types.DELETE_TOKEN)
      },
      routerDataLogOut () {
        return {name: 'EventList'}
      },
      changeLang (lang) {
        this.$store.commit(types.SET_LANGUAGE, lang)
      }
    },
    watch: {
      /*
      defaultLangs (def) {
        this.$store.commit(types.SET_LANGUAGE, def)
      },
      */
      selectedLang (l) {
        if (this.defaultLangs === null) {
          this.defaultLangs = l
        }
      }
    }
  }
</script>

<style lang="less" scoped>
.find-mans-nav{
  position: absolute;
  top: 14px;
  right: 32px;
}
.close-mans-nav{
  position: absolute;
  top: 17px;
  left: 32px;
  button{
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #292929;
    width: auto;
    border: none;
    background-color: transparent;
    outline: none !important;
    .material-icons{
      font-size: 18px;
      vertical-align: text-bottom;
      padding-left: 4px;
    }
  }
}
#mans-nav{
  margin-left: -85%;
  position: fixed;
  z-index: 99999999;
  top: 0;
}
.mans-navigation{
  width: 80%;
  height: 100vh;
  transition: 0.3s;
  padding: 32px;
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  .mans-navigation-content{
    display: table;
    height: 100vh;
    width: 100%;
    .mans-navigation-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
}
      .navigation-tools{
        position: absolute !important;
        width: 100%;
        top: 0;
        .navigation-tools-content{
          display: table;
          height: 56px;
          width: 100%;
          .navigation-tools-body{
            display: table-cell;
            vertical-align: middle;
            .material-icons{
              vertical-align: middle;
            }
          }
        }            
      }
        .close-modal{
          button{
            padding: 0;
            font-size: 14px;
            text-transform: uppercase;
            color: #292929;
            width: auto;
            border: none;
            background-color: transparent;
            outline: none !important;
            .material-icons{
              font-size: 18px;
              vertical-align: text-bottom;
              padding-left: 4px;
            }
          }
        }
.line{
  margin-bottom: 36px !important;
}
#navigation{
  z-index: 10000 !important;
}
#nav-open{
  transition: 0.5s;
  z-index: 9;
  background-color: #fff;
  box-shadow: inset 0 -1px rgba(0,0,0,.06), 0 2px 5px 0 rgba(0,0,0,.06);
  width: 100%;
  text-align: center;
  .nav-content{
    display: table;
    height: 64px;
    width: 100%;
    .nav-body{
      display: table-cell;
      vertical-align: middle;
      .material-icons{
        vertical-align: middle;
      }
       img{
        height: 56px;
      }
    }
  }          
}
.mobile-nav.logged-in{
  background-color: #fff;
  padding: 10px;
  text-align: center;
  border: 1px solid #979797;
  p{
    margin: 0;
  }
  p:first-child{
    text-transform: uppercase;
    color: #979797;
    margin-bottom: 4px;
  }
}
.log-in{
  margin-bottom: 48px;
}
#find{
  border-radius: none;
  outline: none;
  padding: 0;
  border: 0;
  height: 32px;
  color: #292929;
  background-color: transparent;
  .material-icons{
    font-size: 20px;
    color: #292929;
    vertical-align: top;
  }
}
#open{
  padding: 0;
  height: 56px;
  font-size: 16px;
  outline: none;
  text-transform: uppercase;
  border: none;
  background-color: transparent;
  border: 0;
  .material-icons{
    vertical-align: middle;
  }
}
.nav-bar{
  display: table;
  width: 100%;
  .nav-bar-inner{
    text-align: center;
    display: table-cell;
    vertical-align: middle;
  }
}
.modal{
  padding: 0 !important;
}
  .modal-dialog{
    width: 80%;
    margin: 0;
  }
  .modal-content{
    display: table;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
    border-radius: 0;
    border: none;
    .modal-body{
      display: table-cell;
      vertical-align: middle;
      text-align: center;
      .material-icons{
        vertical-align: middle;
      }
    }
  }
  nav{
    .active{
      position: relative;
      font-weight: bold;
    }
    a{
      cursor: pointer;
      position: relative;
      display: block;
      font-size: 14px;
      text-decoration: none;
      text-transform: capitalize;
      &:last-child{
        margin-bottom: 48px;
        &:after{
          content: none;
        }
      }
      &:hover{
        color: #292929;
      }
    }
  }
  #log-in{
    margin-bottom: 48px;
    .log-in{
      margin: 0;
    }
  }
  #settings{
    button{
      margin: 0;
    }
    .account{
      margin-right: 16px;
      button{
        margin: 0;
      }
    }
  }
  .input-group{
  z-index: 10001;
  position: fixed;
  background-color: #fff;
  padding: 8px 15px;
  top: 0;
  input[type='text']{
    width: 100%;
    min-height: 48px;
    padding: 10px;
  }
  button{
    min-height: 48px;
    padding: 10px 14px;
    color: #fff;
    background-color: #292929;
    border: none;
    border-radius: 0;
    .material-icons{
      margin: 0;
      vertical-align: middle;
    }
  }
}
.fixed{
  position: fixed;
  top: 0 !important;
}
.fixed-show{
  position: fixed;
  top: -64px !important;
}
.pos-relative{
  position: relative !important;
}
.animated {
  animation-duration: .3s;
  animation-fill-mode: both;
}
@keyframes slideInUp {
  from {
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
.slideInUp {
  animation-name: slideInUp;
}
@media(min-width:0px) and (max-width:767px){
  .modal-content{
    .modal-body{
      text-align: left;
      padding-left: 56px;
      padding-right: 56px;
    }
  }
}
</style>


