<template lang="pug">
  section
    .container
      .result
        .row
          .col-sm-6.col-xs-12
            .result-wrapper
              .result-body
                h3 {{ translations.search_title[defaultLang] | translate }}
          .col-sm-6.col-xs-12
            .result-wrapper
              .result-body
                button.active {{ translations.search_sort_by_relevance[defaultLang] | translate }}
                button {{ translations.search_sort_by_date[defaultLang] | translate }}
      .search-content(v-for='d in getSearchResult')
        .row
          .col-sm-3
            .content-wrapper
              .content-body
                .image(:style="{ 'background-image': 'url(static/' + d.translations.logo.en + ')' }")
          .col-sm-9
            .content-wrapper
              .content-body
                span {{ d.acts_date_range.start.date | formatMonth }} {{ d.acts_date_range.start.date | formatDate }} - {{ d.acts_date_range.end.date | formatMonth }} {{ d.acts_date_range.end.date | formatDate }} | {{ (d.acts_price_range.min.amount / 100) | currency('') }} {{ d.acts_price_range.min.currency.code }}  - {{ (d.acts_price_range.max.amount / 100) | currency('')}} {{ d.acts_price_range.min.currency.code }}
                h1 {{ d.translations.name[defaultLang] | translate }}
                p {{ d.place }}
                p {{ d.description }}
</template>

<script>
  import { mapGetters } from 'vuex'
  import moment from 'moment'

  export default {
    data () {
      return {
        data: [
          {
            date: 'JUL 04 | 82.00€',
            title: 'Dinner In The Sky',
            place: 'Brīvības iela 154, Rīga, Latvija',
            description: 'An independed TED international this year in the theme of enterpreneurship and motivation.',
            image: 'La-Hulpe.jpg'
          },
          {
            date: 'JUL 04 | 82.00€',
            title: 'Sunset In The Sky',
            place: 'Brīvības iela 154, Rīga, Latvija',
            description: 'An independed TED international this year in the theme of enterpreneurship and motivation.',
            image: 'dits.jpg'
          },
          {
            date: 'JUL 04 | 82.00€',
            title: 'Brunch In The Sky',
            place: 'Brīvības iela 154, Rīga, Latvija',
            description: 'An independed TED international this year in the theme of enterpreneurship and motivation.',
            image: 'dits2.jpg'
          },
          {
            date: 'JUL 04 | 82.00€',
            title: 'Lunch In The Sky',
            place: 'Brīvības iela 154, Rīga, Latvija',
            description: 'An independed TED international this year in the theme of enterpreneurship and motivation.',
            image: 'dinner-in-the-sky-foto-parallax.jpg'
          }
        ]
      }
    },
    filters: {
      formatDate: function (value) {
        return moment(value).format('DD')
      },
      formatMonth: function (value) {
        return moment(value).format('MMM')
      }
    },
    mounted () {
      window.jQuery('#searchBox').css('visibility', 'hidden')
      window.jQuery('#mobSearchField').css('visibility', 'hidden')
      window.jQuery('.input-group-btn').css('background-color', '#f7f7f7')
      window.jQuery('#searchIcon').css('color', '#292929')
      window.jQuery('#searchField').css('z-index', '1')
    },
    props: ['translations'],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage',
        getSearchResult: 'getSearchResult'
      })
    }
  }
</script>

<style lang="less" scoped>
section{
  margin-bottom: 104px;
}
.result{
  border-bottom: 1px solid #979797;
  margin-bottom: 34px;
  .col-sm-6:last-child{
    text-align: right;
  }
  .active{
    position: relative;
    -webkit-text-stroke: 1px #292929;
    &:after{
      content: '';
      position: absolute;
      background-color: #292929;
      left: 0;
      right: 0;
      width: 10px;
      bottom: -10px;
      margin: 0 auto;
      height: 1px;;
    }
  }
  button{
    font-size: 18px;
    padding: 0;
    outline: none;
    background-color: transparent;
    border: none;
    &:first-child{
      margin-right: 64px;
    }
  }
  .result-wrapper{
    display: table;
    width: 100%;
    height: 100px;
    .result-body{
      display: table-cell;
      vertical-align: middle;
    }
  }     
}
.search-content{
  margin-bottom: 44px;
  .content-wrapper{
    display: table;
    width: 100%;
    height: 188px;
    .content-body{
      display: table-cell;
      vertical-align: middle;
      span{
        font-weight: bold;
      }
      h1{
        margin: 0 0 8px 0;
      }
      p:first-of-type{
        font-weight: bold;
      }
      .image{
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 100%;
      }
    }
  }
}
@media(min-width:0px) and (max-width:767px){
  section{
    margin-top: 16px;
    margin-bottom: 32px;
  }
  h3{
    margin-bottom: 32px;
  }
  .result .result-wrapper{
    display: block;
    height: auto;
  }
  .result .result-wrapper .result-body{
    display: block;
  }
  .result-body{
    text-align: left;
  }
  .result{
    padding-bottom: 24px;
  }
  .search-content{
    margin-bottom: 16px;
  }
}
</style>


