<template lang="pug">
  .div
    button(type='button', data-toggle='modal', data-target='#ticketDetailsBack') Ticket Details Back
    #ticketDetailsBack.modal.fade(role='dialog')
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-content-wrapper
            .modal-header
              h3 Ticket Details
            .modal-body
              .row
                .col-sm-6
                  h4 General Area
                  p Seat: Open Seating | Price 45.50€
                .col-sm-6
                  p Ticket Type *
                  tk-select(:select='ticket', v-model='ticketType')
              .row
                .col-sm-6
                  button.splan Seating Plan
                .col-sm-6
                  button.remove Remove Ticket
            .modal-body
              .row
                .col-sm-6
                  h4 General Area
                  p Seat: Open Seating | Price 45.50€
                .col-sm-6
                  p Ticket Type *
                  tk-select(:select='ticket', v-model='ticketType')
              .row
                .col-sm-6
                  button.splan Seating Plan
                .col-sm-6
                  button.remove Remove Ticket
            p.info * All entrants with any of the Special Ticket Types selected for their tickets need to have a document proving their eligibility when entering the venue.
          .modal-footer
            .row
              .col-sm-6
                .footer-wrapper
                  .footer-body
                    button.cancel &#10005; Cancel
              .col-sm-6
                .footer-wrapper
                  .footer-body
                    tk-PrimaryButton(name="Buy Now")
</template>

<script>
  export default {
    data () {
      return {
        ticket: [
          {name: 'None', value: 'n'},
          {name: 'Adult', value: 'a'},
          {name: 'Child', value: 'c'},
          {name: 'Family', value: 'f'}
        ]
      }
    }
  }
</script>

<style lang="less" scoped>
.modal-dialog{
  width: 770px;
}
.seating{
  color: #979797;
  text-transform: uppercase;
  margin: 0;
}
.info{
  margin: 48px 0;
}
.splan{
  margin: 24px 0;
  text-transform: uppercase;
  border: none;
  background-color: transparent;
  text-decoration: underline;
  padding: 0;
}
.remove{
  margin: 24px 0;
  text-transform: uppercase;
  border: none;
  color: #cb4a4a;
  background-color: transparent;
  text-decoration: underline;
  padding: 0;
  float: right;
}
.discount{
  text-align: left;
}
h1{
  text-align: center;
  margin: 0 0 24px 0;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  button{
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    border: none;
    background-color: transparent;
  }
}
.method-wrapper{
  border: 1px solid #979797;
  margin-bottom: 24px;
  transition: 0.3s;
  height: 137px;
  width: 100%;
  display: table;
  opacity: 0.6;
  .method-body{
    display: table-cell;
    vertical-align: middle;
  }
  cursor: pointer;
  &:hover{
    opacity: 1;
    background-color: #fff;
  }
  img{
    height: 100px;
    width: auto;
    margin: 0 auto;
  }
}
.modal-body{
  padding: 20px;
  background-color: #fff;
  margin: 24px 0;
}
.modal-content-wrapper{
  padding: 0 25px;
}
.modal-header{
  text-align: center;
  border-bottom: 1px solid #979797;
  padding: 20px 0;
  h3{
    margin: 4px 0;
  }
}
.modal-content{
  background-color: #f7f7f7;
  border-radius: 0;
}
.modal-footer{
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  padding: 0 25px;
  .cancel{
    background-color: transparent;
    border: none;
    text-transform: uppercase;
  }
  .col-sm-6:last-child{
    text-align: right;
  }
  .col-sm-6:first-child{
    text-align: left;
  }
  .footer-wrapper{
    display: table;
    height: 75px;
    width: 100%;
    .footer-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
}
</style>



