<template lang="pug">
  modal-content(:translations='translations')
    div(slot='header')
      h2 {{ translations.sign_up_title[defaultLang] | translate }}
      p {{ translations.sign_up_paragraph[defaultLang] | translate }}
    div
      tk-input-type-text(placeholder='First Name', v-model='user.name')
      tk-input-type-text(placeholder='Last Name', v-model='user.lname')
      tk-input-type-text#email(placeholder='Email', v-model='user.email')
      tk-input-type-text.password(placeholder='Password', v-model='user.password', type='password')
      .error(v-if="wrongEmail")
        p Wrong email address
      tk-primary-button.create(@click='signUp', :name="translate(translations.sign_up_log_in[defaultLang])", v-if='user.name && user.lname && user.email && user.password')
      tk-disabled-button.disabled(:name="translate(translations.sign_up_log_in[defaultLang])", v-else)
      p {{ translations.sign_up_account[defaultLang] | translate }}
      button.login(@click="$emit('logIn')") {{ translations.log_in_title[defaultLang] | translate }}
</template>

<script>
  import ModalContent from '@/components/modal/ModalContent'
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    data () {
      return {
        wrongEmail: false,
        user: {
          name: '',
          lname: '',
          email: '',
          password: ''
        }
      }
    },
    methods: {
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      signUp () {
        var mailformat = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i
        if (this.user.email.match(mailformat)) {
          document.getElementById('email').classList.remove('wrongInput')
          this.wrongEmail = false
          window.jQuery('#LogIn').modal('hide')
        } else {
          document.getElementById('email').classList.add('wrongInput')
          document.getElementById('email').focus()
          this.wrongEmail = true
        }
      }
    },
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    components: {
      'modal-content': ModalContent
    }
  }
</script>

<style lang="less" scoped>
    h2{
      font-size: 32px;
      font-weight: bold;
      margin: 0 0 8px 0;
      text-align: left;
    }
    p{
      font-size: 14px;
      text-align: left;
    }
  input[type='text']{
    margin-bottom: 24px;
  }
  .create{
    width: 100%;
    margin-bottom: 24px;
  }
  .login{
    text-transform: uppercase;
    text-decoration: underline;
    padding: 0;
    border: none;
    background-color: transparent;
    outline: none !important;
  }
  .disabled{
    width: 100%;
    margin-bottom: 24px;
  }
  .social{
    .fa{
      margin-right: 16px;
    }
    p{
      position: relative;
      text-transform: uppercase;
      text-align: center;
      color: #9a9a9a;
      margin-bottom: 24px;
      &:after{
        content: '';
        position: absolute;
        right: 0;
        top: 50%;
        background-color: #9a9a9a;
        height: 1px;
        width: 40%;
      }
       &:before{
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        background-color: #9a9a9a;
        height: 1px;
        width: 40%;
      }
    }
    button{
      transition: 0.3s;
      background-color: #9a9a9a;
      text-transform: uppercase;
      font-size: 16px;
      color: #fff;
      width: 100%;
      padding: 10px 18px;
      border: none;
      margin-bottom: 24px;
      &:hover{
        background-color: #a9a9a9;
      }
    }
  }
  .wrongInput{
    border: 3px solid #292929 !important;
  }
  .error{
    margin-top: -16px;
    margin-bottom: 14px;
    p{
      font-size: 12px;
      font-weight: bold;
      margin: 0;
    }
  }
  .password{
    margin-bottom: 24px;
  }
  @media(min-width:0px) and (max-width:767px){
    .modal-dialog{
      max-width: 100%;
    }
  }
</style>



