<template lang="pug">
  section
    .container
      .row
        .col-sm-8.col-sm-offset-2
          .image(v-bind:style = "{ 'background-image': 'url(' + translations.success_image.en + ')' }")
          h1 {{ translations.success_title | tktranslate(lang) }}
          p {{ translations.success_paragraph | tktranslate(lang) }}
          h3 {{ translations.success_order | tktranslate(lang) }}
          //tk-table-gift-cards.hidden-xs(v-if='giftCards', :translations='translations')
          tk-table.hidden-xs(:translations='translations')
          //tk-table-xs-gift-cards.hidden-sm.hidden-md.hidden-lg(v-if='giftCards', :translations='translations')
          tk-table-xs.hidden-sm.hidden-md.hidden-lg(:translations='translations')
          h3 {{ translations.success_tickets_title | tktranslate(lang) }}
          p {{ translations.success_tickets_paragraph | tktranslate(lang) }}
          template(v-if='order.documents')
            table#document_table
              tr(v-for='document in order.documents')
                td(valign="middle")
                  strong {{ document.configuration.name }}
                  span {{ document.configuration.type }}
                td(align="right", valign="middle"): a.btn(target='_blank', :href='document.path') {{ translations.success_tickets_button_pdf | tktranslate(lang) }}
          h4.generating(v-else) {{ translations.generating_documents | tktranslate(lang) }}
          // tk-primary-button(v-if='totalSelected', :name="translations.success_tickets_button_pdf | tktranslate(lang)")
          // tk-secondary-button(v-if='totalSelected', :name="translations.success_tickets_button_invoice | tktranslate(lang)")
          //tk-primary-button(v-if='totalGiftCardPrices', :name="translations.success_documents_button | tktranslate(lang)")
</template>

<script>
  import { mapGetters } from 'vuex'
  import api from '@/services/TicketylOS/TicketylOS'
  import * as types from '@/store/mutation-types'

  export default {
    props: ['translations'],
    computed: {
      ...mapGetters({
        lang: 'selectedLanguage',
        order: 'order'
      })
    },
    created () {
      api.order(this.$route.params.id).finalize().then(model => {
        this.$store.commit(types.SET_ORDER, model)
      })
    }
  }
</script>

<style lang="less" scoped>
#document_table {
  width: 100%;
  margin-bottom: 30px;
  tr {
    td {
      padding: 5px;
    }
  }
}
.generating{
  margin-bottom: 64px;
}
a{
  text-decoration: none;
}
.btn{
  margin: 0px !important;
  transition: 0.3s;
  background-color: #292929;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  min-height: 48px;
  padding: 10px 18px;
  border-radius: 0;
  border: 0;
  outline: none !important;
  &:hover{
    background-color: #424242;
    text-decoration: none;
  }
}
.invoice{
  transition: 0.3s;
  background-color: #9a9a9a;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  min-height: 48px;
  padding: 10px 18px;
  border-radius: 0;
  border: none;
  outline: none !important;
  &:hover{
    background-color: #a9a9a9;
    text-decoration: none;
  }
}
section{
  margin-top: 32px;
  header{
    margin-bottom: 8px;
  }
  h1 + p{
    text-transform: uppercase;
    margin-bottom: 24px;
  }
  a{
    display: inline-block;
    margin-bottom: 88px;
    margin-right: 32px;
  }
  .image{
    width: 100%;
    height: 176px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 32px;
  }
  h3 + p{
    margin-bottom: 16px;
  }
  h1 + p + h3{
    margin-bottom: 32px;
  }
  .ticket-category{
    margin-bottom: 22px;
  }
  .footer{
    z-index: 1;
    background-color: #fff;
    position: fixed;
    bottom: 0;
    width: 100%;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    .col-sm-6{
      text-align: right;
    }
    .footer-wrapper{
      display: table;
      height: 80px;
      width: 100%;
      .footer-body{
        display: table-cell;
        vertical-align: middle;
      }
    }
  }
}
@media(min-width:0px) and (max-width:767px){
  .generating{
    margin-bottom: 24px;
  }
  section{
    margin: 0;
    .image{
      margin-top: 16px;
      margin-bottom: 24px;
      height: 120px;
    }
    h1 + p + h3{
      margin: 24px 0;
    }
    h3{
      font-size: 20px;
    }
    h3 + p{
      margin-bottom: 24px;
    }
    a{
      display: inline-block;
      margin-bottom: 32px;
      width: 100%;
    }
  }
}
</style>



