<template lang="pug">
  .div
    button.cart(type='button', data-toggle='modal', :data-target="'#shoppingCart' + _uid") {{ translations.cart | tktranslate(lang) }}
      i.material-icons.md-24 shopping_cart
      span(v-if='cart.length > 0') {{ cart.length }}
    .modal(role='dialog', :id="'shoppingCart' + _uid")
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-content-wrapper
            .modal-header
              h3 {{ translations.shopping_cart_title | tktranslate(lang) }}
            .modal-body
              p.paragraph(v-if='cart.length > 0') Once you have the choices you wish in your basket, you are ready to continue to the checkout
              tk-table-basket.hidden-xs(v-if='cart.length > 0' :translations='translations')
              tk-table-basket-xs.hidden-sm.hidden-md.hidden-lg(v-if='cart.length > 0' :translations='translations')
              .empty(v-else)
                h4 Cart Empty
                p.info Select tickets an they will appear here
                //.tickets
                  tk-Modal(:translations='translations', @closeBasket='hideShoppingCart')
          .modal-footer
            .row
              .col-xs-12
                .footer-wrapper
                  .footer-body
                    tk-primary-button(v-if='this.$route.name === "Checkout"' name='continue shopping' data-dismiss="modal")
                    tk-primary-button.addToBasket(v-if='cart.length > 0 && this.$route.name !== "Checkout"' @click='toCheckout', :name="translations.shopping_cart_to_checkout | tktranslate(lang)")
                    tk-disabled-button.addToBasket(v-if='!cart.length > 0 && !this.$route.name !== "Checkout"' :name="translations.shopping_cart_to_checkout | tktranslate(lang)")
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage',
        lang: 'selectedLanguage',
        cart: 'get_cart'
      })
    },
    methods: {
      hideShoppingCart () {
        console.log('hide')
        window.jQuery('#shoppingCart' + this._uid).modal('hide')
      },
      toCheckout () {
        window.jQuery('#tickets' + this._uid).modal('hide')
        this.$router.push({name: 'Checkout'})
      },
      translate (translate) {
        return this.$options.filters.translate(translate)
      }
    }
  }
</script>

<style lang="less" scoped>
.info{
  padding-bottom: 16px;
}
.tickets{
  width: 160px;
  margin: 0 auto;
}
.empty{
  text-align: center;
  img{
    height: 120px;
    margin-bottom: 16px;
  }
}
.div{
  display: inline-block;
}
.cart{
  position: relative;
  font-size: 14px;
  outline: 0;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  min-height: 40px;
  margin-left: 24px;
  border: 1px solid #292929;
  padding: 0 16px;
  .material-icons{
    font-size: 16px;
    vertical-align: middle;
    margin-top: -4px;
    margin-left: 4px;
  }
  span{
    position: absolute;
    font-size: 10px;
    vertical-align: super;
    background-color: #292929;
    color: #fff;
    top: 3px;
    right: 8px;
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
  }
}
.modal-dialog{
  width: 770px;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  button{
    outline: 0;
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    border: none;
    background-color: transparent;
  }
}
.modal-body{
  padding: 40px 0;
  p{
    margin-bottom: 8px;
  }
}
.paragraph{
  text-align: left;
  padding-bottom: 16px;
}
.modal-content-wrapper{
  padding: 0 25px;
}
.modal-header{
  text-align: center;
  border-bottom: 1px solid #979797;
  padding: 20px 0;
  h3{
    margin: 4px 0;
  }
}
.modal-content{
  background-color: #f7f7f7;
  border-radius: 0;
}
.modal-footer{
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  padding: 0 25px;
  .footer-wrapper{
    display: table;
    height: 75px;
    width: 100%;
    .footer-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
}
</style>



