import * as types from '../mutation-types'

const state = {
  vouchers: [], // Discount Code
  tickets: [], // Selected Shop Seats (date, time)
  allSeats: [], // All available seats
  selectedPackageId: 0,
  packages: [] // Aside select
}
const mutations = {
  [types.ADD_TICKET] (state, t) {
    state.tickets = state.tickets.concat(t)
  },
  [types.SET_TICKETS] (state, t) {
    state.allSeats = t
  },
  [types.SET_SELECTEDPACKAGEID] (state, s) {
    state.selectedPackageId = s
  },
  [types.DELETE_SEATS] (state) {
    state.allSeats = []
    state.tickets = []
    state.vouchers = []
  },
  [types.DELETE_TICKETS] (state, id) {
    state.tickets = state.tickets.filter(function (ticket) {
      return ticket.id !== id
    })
  },
  [types.SET_VOUCHERS] (state, v) {
    state.vouchers = v
  },
  [types.SET_PACKAGES] (state, p) {
    state.packages = p
  }
}
const getters = {
  get_ticket_voucher_amount: state => {
    if (state.vouchers.length > 0) {
      return state.vouchers[0].discount.amount
    }
    return 0
  },
  get_ticket_voucher_code: state => {
    if (state.vouchers.length > 0) {
      return state.vouchers[0].code
    }
    return 0
  },
  getPackages: state => {
    return state.packages
  },
  getTickets: state => {
    return state.tickets
  },
  getCheckoutTickets: state => {
    var checkoutTickets = []
    for (var i = 0; i < state.tickets.length; i++) {
      checkoutTickets.push({id: state.tickets[i].id, amount: 1})
    }
    return checkoutTickets
  },
  getTicketsSelected: state => {
    return state.tickets.length
  },
  getSelectedPackageId: state => {
    return state.selectedPackageId
  },
  getTotalPrice: state => {
    var totalTickets = 0
    for (var i = 0; i < state.tickets.length; i++) {
      totalTickets += state.tickets[i].price.amount
    }
    return totalTickets
  },
  getAllSeats: state => {
    return state.allSeats
  },
  getDefaultPackage: state => {
    if (state.packages.length === 0) {
      return 0
    }
    return state.packages[0].id
  },
  selectedTickets: state => {
    return state.tickets.filter(ticket => ticket.no_selected > 0)
  }
}
export default {
  state,
  mutations,
  getters
}



