<template lang="pug">
  section
    tk-header-admin
    .settings
      .container
        #settings
          .row
            .col-sm-4
              .settings-content
                .settings-body
                  h2 Main Settings
            .col-sm-8.text-right
              .settings-content
                .settings-body
                  tk-secondary-button(name='revert to saved')
                  tk-primary-button(name='save changes')
    .container
      .navigation
        .row
          .col-sm-12
            .menu-content
              .menu-body
                router-link(:to="{name: 'Events'}") My Account
                router-link(:to="{name: 'Events'}") Shop Preferences
                router-link(:to="{name: 'Events'}") Manage Team
                router-link.current(:to="{name: 'Events'}") Notifications
    .content
      .container
        .row
          .col-sm-4#js-scrollspy 
            nav#aside
              p Notifications
              ul.js-scrollspy-nav
                li#websiteLink(@click='websiteSpy') 1. Website Notifications
                li#emailLink(@click='emailSpy') 2. Email Notifications
          .col-sm-8#internal-content
            div#website-scroll
              h3 1. Website Notifications
              p Select when you want to be alerted directly at the website
              #website.section.scrollspy
                .row
                  .col-sm-4.col-sm-offset-2
                    p Permissions
                  .col-sm-6
                    form(action='')
                      input(type='checkbox', name='permissions', id="web_new_transaction", value='web_new_transaction', v-model='info.web_notifications')
                      label(for="web_new_transaction") New Transaction
                      input(type='checkbox', name='permissions', id="web_changes", value='web_changes', v-model='info.web_notifications')
                      label(for="web_changes") Changes In Transaction  
                      input(type='checkbox', name='permissions', id="web_new_event", value='web_new_event', v-model='info.web_notifications')
                      label(for="web_new_event") New Event Published
                      input(type='checkbox', name='permissions', id="web_withdrawal_request", value='web_withdrawal_request', v-model='info.web_notifications')
                      label(for="web_withdrawal_request") Withdrawal Requested
                      input(type='checkbox', name='permissions', id="web_event_cancelled", value='web_event_cancelled', v-model='info.web_notifications')
                      label(for="web_event_cancelled") Event Cancelled
            #email.section.scrollspy
              h3 2. Email Notifications
              p Pick and choose the events when you wish to have an email sent to you
              .row
                .col-sm-4.col-sm-offset-2
                  p Active Notifications
                .col-sm-6
                  form(action='')
                    input(type='checkbox', name='card', id="email_new_transaction", value='email_new_transaction', v-model='info.email_notifications')
                    label(for="email_new_transaction") New Transaction
                    input(type='checkbox', name='card', id="email_changes", value='email_changes', v-model='info.email_notifications')
                    label(for="email_changes") Changes In Transaction
                    input(type='checkbox', name='card', id="email_new_event", value='email_new_event', v-model='info.email_notifications')
                    label(for="email_new_event") New Event Published
                    input(type='checkbox', name='card', id="email_withdrawal_request", value='email_withdrawal_request', v-model='info.email_notifications')
                    label(for="email_withdrawal_request") Withdrawal Requested
                    input(type='checkbox', name='card', id="email_event_cancelled", value='email_event_cancelled',v-model='info.email_notifications')
                    label(for="email_event_cancelled") Event Cancelled
            .row
              .col-sm-8
                tk-secondary-button.right(name='revert to saved')
                tk-primary-button(name='save changes')
    tk-footer-admin
</template>

<script>
  import store from '@/store'

  export default {
    data () {
      return {
        info: {
          web_notifications: [],
          email_notifications: []
        },
        visibility: ['Public', 'Private'],
        language: ['English', 'Latvian'],
        currency: ['Euro', 'USD'],
        timezone: ['UTC+3', 'GMT']
      }
    },
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    },
    methods: {
      websiteSpy () {
        document.getElementById('website').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      emailSpy () {
        document.getElementById('email').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      }
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')

      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '350') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    }
  }
</script>

<style lang="less" scoped>
#website, #email{
  margin-bottom: 32px;
  border-bottom: 1px solid #979797;
}
.navigation{
  border-bottom: 1px solid #979797;
  margin-bottom: 32px;
  .menu-content{
    display: table;
    height: 88px;
    width: 100%;
    .menu-body{
      display: table-cell;
      vertical-align: middle;
      a{
        text-transform: none;
        position: relative;
        text-decoration: none;
        margin-right: 50px;
        color: #292929;
        font-size: 18px;
        .router-link-exact-active{
          position: relative;
          width: 10px;
          height: 2px;
          -webkit-text-stroke: 1px #292929;
          &:after{
            content: "";
            position: absolute;
            width: 10px;
            height: 1px;
            background-color: #292929;
            left: 0;
            right: 0;
            bottom: -10px;
            margin: 0 auto;
          }
        }
      }
    }
  }
}               
.settings{
  background-color: #f7f7f7;
  #settings{
    border-bottom: 1px solid #979797;
  }
  .settings-content{
    display: table;
    height: 116px;
    width: 100%;
    .settings-body{
      display: table-cell;
      vertical-align: middle;
      h2{
        margin: 0;
      }
      button{
        margin-left: 24px;
      }
      .back{
        text-transform: uppercase;
        padding: 0;
        border: none;
        outline: 0;
        text-decoration: underline;
        background-color: transparent;
      }
    }
  }        
}
.content{
  margin-bottom: 120px;
}
nav{
  padding: 24px;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
  p{
    color: #9a9a9a;
    text-transform: uppercase;
    margin-bottom: 24px;
  }
  ul{
    text-decoration: none;
    padding: 0;
    list-style-type: none;
    li{
      margin-bottom: 16px;
      cursor: pointer;
      &:last-child{
        margin: 0;
      }
    }
    a{
      cursor: pointer;
      text-transform: capitalize;
      text-decoration: none;
      &:hover{
        color: #292929;
      }
    }
  }
}
.active{
  font-weight: bold;
}
.center{
  margin: 0;
  margin-top: 12px;
}
.current{
  position: relative;
  -webkit-text-stroke: 1px #292929;
  &:after{
    position: absolute;
    content: '';
    width: 14px;
    height: 1px;
    background-color: #292929;
    bottom: -11px;
    left: 0;
    right: 0;
    margin: auto;
  }
}
.fixed{
  position: fixed;
  background-color: #f7f7f7;
  z-index: 1;
  width: 360px;
  top: 32px;
}
input[type='text']{
  margin-bottom: 24px;
}
.margin-bottom{
  margin-bottom: 24px;
}
.last{
  margin-bottom: 32px !important;
}
.action{
  outline: 0;
  text-transform: uppercase;
  padding: 0;
  background-color: transparent;
  text-decoration: underline;
  border: none;
}
.right{
  margin-right: 32px;
}
h3{
  margin-top: 0;
  +p{
    margin-bottom: 32px;
  }
}
h4{
  margin-bottom: 24px;
}
form{
  margin-bottom: 32px;
  img{
    height: 22px;
    width: 22px;
    margin-right: 8px;
  }
}
.gift-card-content{
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0.2) 100%), url('/static/giftcard_bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: table;
  height: 184px;
  width: 100%;
  margin-bottom: 32px;
  .gift-card-body{
    color: #fff;
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    p{
      margin-bottom: 16px;
    }
  }
}
</style>



