<template lang="pug">
  section.checkout
    .loading-screen(v-if='this.$route.name !== "Checkout"')
      .container
        .loading
          .spinner
            .rect1
            .rect2
            .rect3
            .rect4
            .rect5
    .section-content(v-if='this.$route.name === "Checkout"')
      // tk-warning(:translations='translations')
      // tk-warning-checkbox(:translations='translations')
      tk-reservation-released(:translations='translations')
      .container
        .row
          .col-sm-8
            .image(v-bind:style="{ 'background-image': 'url(/static/' + translations.checkout_image.en + ')' }")
            h1 {{ serie.translations.name | tktranslate(lang) }}
            tk-timer(@finished='showReleased', :translations='translations').hidden-sm.hidden-md.hidden-lg
            h3 {{ translations.checkout_title_1 | tktranslate(lang) }}
            p {{ translations.checkout_title_1_paragraph | tktranslate(lang) }}
            //tk-table-gift-cards.hidden-xs(v-if='giftCards.length > 0 || customGift', :translations='translations')
            tk-table-basket.hidden-xs(v-if='cart.length > 0' :translations='translations')
            tk-table-basket-xs.hidden-sm.hidden-md.hidden-lg(v-if='cart.length > 0' :translations='translations')
            //tk-table-xs-gift-cards.hidden-sm.hidden-md.hidden-lg(v-if='giftCards.length > 0 || customGift', :translations='translations')
            //tk-table-xs.hidden-sm.hidden-md.hidden-lg(v-if='totalSelected', :translations='translations')
            p {{ translations.checkout_info | tktranslate(lang) }}
            //tk-discount-input(:voucher='voucher', v-if='giftCards.length > 0 || customGift', :translations='translations', placeholder='Discount code' @click='applyDiscount', v-model='discount_code').hidden-sm.hidden-md.hidden-lg
            //tk-discount-input(:voucher='voucher', v-if='tickets.length > 0', :translations='translations', placeholder='Discount code' @click='applyDiscountTicket', v-model='discount_code').hidden-sm.hidden-md.hidden-lg
            //.faster-checkout
              .row
                .col-sm-7
                  .faster-checkout-wrapper
                    .faster-checkout-body
                      h3 {{ translations.checkout_log_in_title | tktranslate(lang) }}
                      p {{ translations.checkout_log_in_paragraph | tktranslate(lang) }}
                .col-sm-5
                  .faster-checkout-wrapper
                    .faster-checkout-body
                      tk-checkout-login
            h3 {{ translations.checkout_title_2 | tktranslate(lang) }}
            p {{ translations.checkout_title_2_paragraph | tktranslate(lang) }}
            #buyer-info
              h4 {{ translations.checkout_form_buyer_info | tktranslate(lang) }}
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_title | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(v-model='orderDetails.buyerInfo.title')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_first_name | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='name', v-model='orderDetails.buyerInfo.fname', v-validate="'required|alpha_spaces'", :has-error="errors.has('name')", data-vv-value-path='text', data-vv-validate-on="none")
                  p(v-if="errors.has('name')").error {{ translations.checkout_form_buyer_first_name_required | tktranslate(lang) }}
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_last_name | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='lname', v-model='orderDetails.buyerInfo.lname', v-validate="'required|alpha_spaces'", :has-error="errors.has('lname')", data-vv-value-path='text', data-vv-validate-on="none")
                  p(v-if="errors.has('lname')").error {{ translations.checkout_form_buyer_last_name_required | tktranslate(lang) }}
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_email_address | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='email', v-model='orderDetails.buyerInfo.email', v-validate="'required|email'", :has-error="errors.has('email')", data-vv-value-path='text', data-vv-validate-on="none")
                  p(v-if="errors.has('email')").error {{ translations.checkout_form_buyer_email_address_required | tktranslate(lang) }}
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_phone | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='phone', v-model='orderDetails.buyerInfo.phone', v-validate="'required|numeric'", :has-error="errors.has('phone')", data-vv-value-path='text', data-vv-validate-on="none", v-on:keypress="isNumber")
                  p(v-if="errors.has('phone')").error {{ translations.checkout_form_buyer_phone_required | tktranslate(lang) }}
            #buyer-address
              h4 {{ translations.checkout_form_buyer_address | tktranslate(lang) }}
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_country | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='country', v-model='orderDetails.buyerAddress.country')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_street | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='street', v-model='orderDetails.buyerAddress.street', v-validate="'required:true'", :has-error="errors.has('street')", data-vv-value-path='text', data-vv-validate-on="none")
                  p(v-if="errors.has('street')").error {{ translations.checkout_form_buyer_street_required | tktranslate(lang) }}
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_city | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='city', v-model='orderDetails.buyerAddress.city', v-validate="'required:true'", :has-error="errors.has('city')", data-vv-value-path='text', data-vv-validate-on="none")
                  p(v-if="errors.has('city')").error {{ translations.checkout_form_buyer_city_required | tktranslate(lang) }}
                .col-sm-4.col-sm-offset-2
                  p {{ translations.checkout_form_buyer_zip | tktranslate(lang) }}
                .col-sm-6
                  tk-input-type-text(name='zip', v-model='orderDetails.buyerAddress.zip', v-validate="'required:true'", :has-error="errors.has('zip')", data-vv-value-path='text', data-vv-validate-on="none")
                  p(v-if="errors.has('zip')").error {{ translations.checkout_form_buyer_zip_required | tktranslate(lang) }}
                .col-sm-6
                .col-sm-6
                  button(@click='showBusiness', v-if='!businessActive').businessperson {{ translations.checkout_form_buyer_business_button | tktranslate(lang) }}
                  button(@click='showBusiness', v-if='businessActive').businessperson {{ translations.checkout_form_buyer_person_button | tktranslate(lang) }}
            transition(name='slide-fade')
              #business(v-if='businessActive')
                h4 {{ translations.checkout_form_business_details_title | tktranslate(lang) }}
                .row
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_details_company | tktranslate(lang) }}
                  .col-sm-6
                    tk-input-type-text(v-model='orderDetails.businessDetails.company')
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_details_registration_no | tktranslate(lang) }}
                  .col-sm-6
                    tk-input-type-text(v-model='orderDetails.businessDetails.reg')
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_details_tax | tktranslate(lang) }}
                  .col-sm-6
                    tk-input-type-text(v-model='orderDetails.businessDetails.tax')
                h4 {{ translations.checkout_form_business_address_title | tktranslate(lang) }}
                .row
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_address_country | tktranslate(lang) }}
                  .col-sm-6
                    tk-custom-select-string(v-model='orderDetails.businessAddress.country', :options='countries')
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_address_street | tktranslate(lang) }}
                  .col-sm-6
                    tk-input-type-text(v-model='orderDetails.businessAddress.street')
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_address_city | tktranslate(lang) }}
                  .col-sm-6
                    tk-input-type-text(v-model='orderDetails.businessAddress.city')
                  .col-sm-4.col-sm-offset-2
                    p {{ translations.checkout_form_business_address_zip | tktranslate(lang) }}
                  .col-sm-6
                    tk-input-type-text(v-model='orderDetails.businessAddress.zip')
            h4 {{ translations.checkout_terms | tktranslate(lang) }}
            .row
              .col-sm-6
                p(v-if="errors.has('terms')").terms-error {{ translations.checkout_terms_warning | tktranslate(lang) }}
              .col-sm-6
                input(type='checkbox', name='terms', id="terms", v-validate="'required'", data-vv-value-path='text', :has-error="errors.has('terms')", data-vv-validate-on="none")
                label#termsLabel(for="terms", style='display: inline-block;') {{ translations.checkout_terms_accept | tktranslate(lang) }}
                tk-terms-checkout(:translations='translations')
          .col-sm-4
            #aside
              //tk-discount-input(v-if='giftCards.length > 0 || customGift', :translations='translations', placeholder='Discount code' @click='applyDiscount', v-model='discount_code', :voucher='voucher').hidden-xs
              //tk-discount-input(v-if='tickets.length > 0', :translations='translations', placeholder='Discount code' @click='applyDiscountTicket', v-model='discount_code', :voucher='voucher').hidden-xs
              tk-total(@validate='validateBeforeSubmit("aside")', :translations='translations', :order='order', :show='showaside', :business='business')
              tk-Modal.hidden-xs(:translations='translations', style='margin-bottom: 16px;')
              tk-timer(@finished='showReleased', :translations='translations').hidden-xs
      .footer.hidden-xs
        .container
          .row
            .col-sm-4.col-xs-6
              .footer-wrapper
                .footer-body
                  .ticket-prices
                    p {{ translations.checkout_footer_tickets_selected | tktranslate(lang) }}
                      strong {{ basket.tickets }}
            .col-sm-4.col-xs-6
              .footer-wrapper
                .footer-body
                  .ticket-prices
                    p {{ translations.checkout_footer_tickets_total_price | tktranslate(lang) }}
                      strong {{ basket.total | tkprice }}
            .col-sm-4.col-xs-12
              .footer-wrapper
                .footer-body
                  button#pay(type='button', data-toggle='modal', @click='pay') {{ translations.checkout_footer_tickets_button | tktranslate(lang) }}

</template>

<script>
  import { mapGetters } from 'vuex'
  import api from '@/services/TicketylOS/TicketylOS'
  import modal from '@/services/Modal/Modal'
  import * as types from '@/store/mutation-types'
  import DiscountInput from '@/components/single-event-page/DiscountInput'
  import Total from '@/components/single-event-page/Total'
  import PaymentMethod from '@/components/modal/PaymentMethod'
  import Warning from '@/components/misc/Warning'
  import WarningCheckbox from '@/components/misc/WarningCheckbox'

  export default {
    props: ['translations'],
    data () {
      return {
        discount_code: '',
        mobile: false,
        showaside: false,
        totalSelectedgiftCards: null,
        totalSelectedWithCustom: null,
        voucher: null,
        validate: false,
        countries: [
          '', 'Austria', 'Belgium', 'Bulgaria', 'Croatia', 'Cyprus', 'Czech Republic', 'Denmark', 'Estonia',
          'Finland', 'France', 'Germany', 'Greece', 'Hungary', 'Ireland', 'Italy', 'Latvia',
          'Lithuania', 'Luxembourg', 'Malta', 'Netherlands', 'Poland', 'Portugal', 'Romania', 'Slovakia',
          'Slovenia', 'Spain', 'Sweden', 'United Kingdom'
        ],
        mrms: [
          'Mr', 'Ms'
        ],
        subscribe: {
          title: 'Sign up to event newsletter',
          placeholder: 'Your email address',
          success: 'Thank you for subcribing',
          wrong: ''
        },
        business: true,
        businessActive: false,
        orderDetails: {
          buyerInfo: {
            title: '',
            fname: '',
            lname: '',
            email: '',
            phone: ''
          },
          buyerAddress: {
            country: '',
            street: '',
            city: '',
            zip: ''
          },
          businessDetails: {
            company: '',
            reg: '',
            tax: ''
          },
          businessAddress: {
            country: '',
            street: '',
            city: '',
            zip: ''
          }
        }
      }
    },
    computed: {
      ...mapGetters({
        cart: 'get_cart',
        lang: 'selectedLanguage',
        basket: 'get_basket',
        serie: 'serie',
        shop: 'shop'
      })
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')
      if (mq.matches) {
        this.mobile = true
      }
    },
    methods: {
      showReleased () {
        window.jQuery('#reservation').modal('show')
      },
      pay () {
        this.$validator.validateAll().then((result) => {
          console.log('result', result)
          if (result) {
            this.makeOrder()
            return
          }
        })
      },
      makeOrder () {
        var products = []
        for (var i in this.cart) {
          products.push({
            id: this.cart[i].product.id,
            amount: this.cart[i].count
          })
        }
        var data = {
          'products': products,
          'buyer': {
            'title': this.orderDetails.buyerInfo.title,
            'first_name': this.orderDetails.buyerInfo.fname,
            'last_name': this.orderDetails.buyerInfo.lname,
            'email': this.orderDetails.buyerInfo.email,
            'phone': this.orderDetails.buyerInfo.phone,
            'address': {
              'country': this.orderDetails.buyerAddress.country,
              'address': this.orderDetails.buyerAddress.street,
              'city': this.orderDetails.buyerAddress.city,
              'zip': this.orderDetails.buyerAddress.zip
            }
          },
          'language': this.$store.getters.selectedLanguage,
          'needs_invoice': false
        }
        if (this.business === true) {
          data.needs_invoice = true
          data.company = {
            'name': this.orderDetails.businessDetails.company,
            'registration_number': this.orderDetails.businessDetails.reg,
            'tax_number': this.orderDetails.businessDetails.reg,
            'address': {
              'country': this.orderDetails.businessAddress.country,
              'address': this.orderDetails.businessAddress.street,
              'city': this.orderDetails.businessAddress.city,
              'zip': this.orderDetails.businessAddress.zip
            }
          }
        }
        this.$store.commit(types.SET_ORDER_DETAILS, data)
        api.order().reserve(data).then(response => {
          this.$store.commit(types.SET_ORDER, response)
          this.$store.commit(types.EMPTY_CART, response)
          modal.show('#pay')
        })
      },
      resetPaymentMethod () {
        this.validate = false
        this.showaside = false
      },
      isNumber (e) {
        var iKeyCode = (e.which) ? e.which : e.keyCode
        if (iKeyCode !== 46 && iKeyCode > 31 && (iKeyCode < 48 || iKeyCode > 57)) {
          e.preventDefault()
          return false
        }
      },
      showBusiness () {
        if (this.businessActive === false) {
          this.businessActive = true
        } else {
          this.businessActive = false
        }
      }
    },
    components: {
      tkDiscountInput: DiscountInput,
      tkTotal: Total,
      tkPaymentMethod: PaymentMethod,
      tkWarning: Warning,
      tkWarningCheckbox: WarningCheckbox
    }
  }
</script>

<style lang="less" scoped>
button#pay{
  transition: 0.3s;
  background-color: #292929;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  padding: 10px 18px;
  border: none;
  outline: none !important;
  &:hover{
    background-color: #424242;
  }
}
.dropdown{
  margin-bottom: 24px;
}
.checkout{
  margin-top: 32px;
}
#buyer-info, #business, #buyer-address{
  border-bottom: 1px solid #9a9a9a;
  padding-bottom: 12px;
  margin-bottom: 36px;
}
.col-sm-8{
  margin-bottom: 32px;
}
.businessperson{
  text-transform: uppercase;
  border: none;
  text-decoration: underline;
  background-color: transparent;
  padding: 0;
  margin-bottom: 24px;
  outline: none !important;
}
#title, #country, #business-country{
  margin-bottom: 24px;
}
.col-sm-offset-2 p{
  margin-top: 14px;
}
input[type='text']{
  margin-bottom: 24px;
}
.ticket-category{
  margin-bottom: 30px;
}
.ticket-category + p{
  margin-bottom: 25px;
}
h1 + p {
  margin-bottom: 40px;
}
h3 + p{
  margin-bottom: 30px;
}
h4{
  margin-bottom: 23px;
}
.faster-checkout{
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
  padding: 16px;
  margin-bottom: 32px;
  margin-top: 32px;
  .col-sm-5{
    text-align: right;
  }
    .faster-checkout-wrapper{
    display: table;
    height: 88px;
    width: 100%;
    .faster-checkout-body{
      display: table-cell;
      vertical-align: middle;
    }
  }
} 
strong{
  margin-left: 10px;
}
.image{
  width: 100%;
  height: 176px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 32px;
}
  .footer{
    text-transform: uppercase;
    z-index: 1;
    background-color: #fff;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    .col-sm-6{
      text-align: right;
    }
    .col-sm-4:last-child{
      text-align: right;
    }
    button{
      text-decoration: none;
    }
    .footer-wrapper{
      display: table;
      height: 80px;
      width: 100%;
      .footer-body{
        display: table-cell;
        vertical-align: middle;
        p{
          margin: 0;
        }
      }
    }
  }
#termsForm label{
  margin: 0;
  padding-bottom: 0;
}
.active{
  font-weight: bold;
  letter-spacing: -0.5px;
}
.error{
  font-size: 12px;
  margin-top: -24px !important;
  font-weight: bold;
}
.false{
  border: 3px solid #292929 !important;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s ease;
}
.slide-fade-enter, .slide-fade-leave-to{
  transform: translateX(10px);
  opacity: 0;
}
/* Media Queries */
/* Extra Small */
@media(min-width:0px) and (max-width:767px){
  #termsLabel{
    margin-bottom: 0;
  }
  .col-sm-8{
    margin-bottom: 24px;
  }
  h1{
    margin-top: 0;
    margin-bottom: 24px;
  }
  h1 + p{
    margin-bottom: 24px;
  }
  h3{
    font-size: 20px;
  }
  h3 + p{
    margin-bottom: 16px !important;
  }
  .image{
    height: 120px;
    margin-top: 16px;
    margin-bottom: 24px;
  }
  .faster-checkout{
    h3{
      margin-bottom: 0 !important;
    }
    .faster-checkout-wrapper{
      height: 88px;
      h3 + p{
        margin-bottom: 0 !important;
      }
    }
    margin-left: -15px;
    margin-right: -15px;
    button{
      width: 100%;
    }
  }
  .footer{
    display: none;
    button{
      width: 100%;
    }
    .footer-wrapper{
      height: 56px;
    }
  }
  .checkout{
    margin: 0;
    padding-bottom: 0;
  }
  form{
    position: relative;
    margin-bottom: 0;
    label{
      display: inline-block;
      margin-bottom: 0;
    }
  }
}
.faster-checkout-body{
  p{
    margin: 0;
  }
  h3{
    margin: 0;
    margin-bottom: 8px;
  }
}
.terms-error{
  font-size: 12px;
  font-weight: bold;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0
}
/* Small */
@media(min-width:768px) and (max-width:991px){
  .faster-checkout{
    .faster-checkout-wrapper{
      height: 193px;
    }
  }
}
/* Medium */
@media(min-width:992px) and (max-width:1199px){
  .faster-checkout{
    .faster-checkout-wrapper{
      height: 193px;
    }
  }
}
/* Loading Screen */
.loading{
    position: relative;
    background-color: #f7f7f7;
    height: 80vh;
    width: 100%;
    margin: auto;
    .spinner{
      position: absolute;
      top: 15%;
      display: table-cell;
      vertical-align: middle;
    }
    img{
      height: 32px;
      position: absolute;
      bottom: 40px;
      left: 0;
    }
  }
  .spinner {
    position: relative;
    margin: 100px auto;
    width: 100%;
    height: 80px;
    text-align: center;
    font-size: 10px;
    img{
      height: 40px;
      position: absolute;
      left: 40px;
      bottom: 40px;
    }
  }
.spinner > div {
  background-color: #444;
  height: 100%;
  width: 8px;
  margin-right: 4px;
  display: inline-block;
  -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
  animation: sk-stretchdelay 1.2s infinite ease-in-out;
}
.spinner .rect2 {
  -webkit-animation-delay: -1.1s;
  animation-delay: -1.1s;
}
.spinner .rect3 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
}
.spinner .rect4 {
  -webkit-animation-delay: -0.9s;
  animation-delay: -0.9s;
}
.spinner .rect5 {
  -webkit-animation-delay: -0.8s;
  animation-delay: -0.8s;
}
@-webkit-keyframes sk-stretchdelay {
  0%, 40%, 100% { -webkit-transform: scaleY(0.4) }  
  20% { -webkit-transform: scaleY(1.0) }
}
@keyframes sk-stretchdelay {
  0%, 40%, 100% { 
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4);
  }  20% { 
    transform: scaleY(1.0);
    -webkit-transform: scaleY(1.0);
  }
}
/* Loading Screen end */
</style>



