<template lang="pug">
  section
    .container
      .row
        .col-xs-12.col-sm-4.col-sm-push-8
          #aside
            nav
              p Other documents
              ul
                li
                  router-link(:to="{'name': 'Privacy'}") Privacy Policy
                li
                  router-link(:to="{'name': 'Imprint'}") Imprint
                li 
                  router-link(:to="{'name': 'Contact'}") Contacts
                li 
                  router-link(:to="{'name': 'Terms'}") Terms And Conditions
                li 
                  router-link(:to="{'name': 'Faq'}").active FAQ
                li
                  router-link(:to="{'name': 'About'}") About
        .col-xs-12.col-sm-8.col-sm-pull-4
          h1 FAQ
          h5 Important Terms
          ul
            li Minimum height of the guest must be at least 1.50 m
            li An adult must accompany guest who is under age of 18 years old
            li Experience is possible for wheelchair users and people with mild disabilities
            li No alcohol will be served to minors
          h5 Good to know
          ul
            li Please inquire vegetarian menu and report any food allergies in advance
            li Guests who appear to be intoxicated will not be permitted to board the Dinner in the Sky restaurant due to safety reasons
            li Unaccompanied children will not be permitted to participate
            li Please arrive at least 30 minutes before the indicated time of your lift
            li If you arrive late, your reservation will be canceled with no refund
            li Don’t forget to take your booking confirmation with you
          h5 What to bring along
          ul
            li Weather appropriate clothing
            li Please remember that the wind at the height of 50 m is slightly stronger, temperatures can be slightly lower
            li Please take your phones, photo and video cameras along to shoot the breathtaking moments of your dining experience in the sky
          h5 Group Size
          ul
            li Maximum group size for one lift is 22 persons
          h5 Weather
          ul
            li Lifts will be made and meals served during rainy weather conditions
            li Lifts will be delayed or canceled in case of wind stronger than 15 m/s and/or thunderstorm
            li Lifts can be canceled or delayed shortly before your scheduled lift time due to weather conditions
          h5 Safety
          p Dinner in the Sky® is a product developed by Events in the Sky according to two key words: Exclusivity and Safety. We want to provide our clients with the most exclusive experiences ever dreamt of in a completely safe way. All our concepts are designed accordingly to the German norm DIN 4112. Products are built in Belgium under the control of Rheinland TÜV. This obsession for safety is probably one of the reasons why Dinner in the Sky® is now operated in more than 40 countries and got the official authorizations from the toughest countries such as US, Canada, Australia, South-Africa etc.
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    props: ['translations'],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')
      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '72') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    }
  }
</script>

<style lang="less" scoped>
.router-link-exact-active{
  font-weight: bold !important;
}
.active{
  font-weight: bold !important;
}
section{
  margin-top: 32px;
  h1{
    margin-bottom: 32px;
  }
  h4{
    margin-bottom: 24px;
    text-transform: uppercase;
  }
  p:last-child{
    margin-bottom: 80px;
  }
  nav{
    background-color: #f7f7f7;
    padding: 24px 16px;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    p{
      text-transform: uppercase;
      color: #9a9a9a;
      margin-bottom: 24px;
    }
    ul{
      text-decoration: none;
      padding: 0;
      list-style-type: none;
      li{
        margin-bottom: 16px;
        &:last-child{
          margin: 0;
        }
      }
      a{
        cursor: pointer;
        text-transform: capitalize;
        text-decoration: none;
        &:hover{
          color: #292929;
        }
      }
    }
  }
}
.fixed{
  z-index: 9999;
  position: fixed;
  width: 360px;
  top: 32px;
}
@media(min-width:0px) and (max-width:767px){
  section{
    margin-top: 16px;
    nav{
      margin-bottom: 16px;
    }
    h1{
      margin-bottom: 16px;
    }
  }
}
</style>



