<template lang="pug">
  section
    .container
      .row
        .col-sm-4#js-scrollspy 
          nav#aside
            p {{ translations.settings_aside_title[defaultLang] | translate }}
            ul.js-scrollspy-nav
              li#personalLink(@click='personalSpy') {{ translations.settings_aside_link_1[defaultLang] | translate }}
              li#passwordLink(@click='passwordSpy') {{ translations.settings_aside_link_2[defaultLang] | translate }}
              li#subLink(@click='subSpy') {{ translations.settings_aside_link_3[defaultLang] | translate }}
              li#shopLink(@click='shopSpy') {{ translations.settings_aside_link_4[defaultLang] | translate }}
              li#deleteLink(@click='deleteSpy') {{ translations.settings_aside_link_5[defaultLang] | translate }}
        .col-sm-8#internal-content
          div#personalSpy
            h3 {{ translations.settings_title[defaultLang] | translate }}
            p {{ translations.settings_paragraph[defaultLang] | translate }}
            #personal.section.scrollspy
              h4 {{ translations.settings_form_personal_info[defaultLang] | translate }}
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_personal_info_title[defaultLang] | translate }}
                .col-sm-6
                  tk-custom-select-uni#mrms(:options='mrms', v-model='accountSettings.personalInfo.title')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_personal_info_first_name[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text(v-model='accountSettings.personalInfo.first_name')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_personal_info_last_name[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text(v-model='accountSettings.personalInfo.last_name')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_personal_info_email_address[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text#emails(v-model='accountSettings.personalInfo.email_address')
                  .error(v-if='wrongEmail')
                    p Wrong email address
          div
            #billing
              h4 {{ translations.settings_form_billing_address[defaultLang] | translate }}
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_billing_address_country[defaultLang] | translate }}
                .col-sm-6
                  tk-custom-select-uni#countries(:options='countries', v-model='accountSettings.billingAddress.country')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_billing_address_street[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text(v-model='accountSettings.billingAddress.street')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_billing_address_city[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text(v-model='accountSettings.billingAddress.city')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_form_billing_address_zip[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text(v-model='accountSettings.billingAddress.zip')
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                  button(@click='showBusiness', v-if='!business').businessperson add business details
                  button(@click='hideBusiness', v-if='business').businessperson hide business details
          transition(name='slide-fade')
            .BUSINESS(v-if='business')
              #business
                h4 Business details
                .row
                  .col-sm-4.col-sm-offset-2
                    p Company Name
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.company')
                  .col-sm-4.col-sm-offset-2
                    p Registration No.
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.reg')
                  .col-sm-4.col-sm-offset-2
                    p Tax Number
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.tax')
                h4 Business address
                .row
                  .col-sm-4.col-sm-offset-2
                    p Country
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.country')
                  .col-sm-4.col-sm-offset-2
                    p Street, Address, Number
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.street')
                  .col-sm-4.col-sm-offset-2
                    p City / Province
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.city')
                  .col-sm-4.col-sm-offset-2
                    p ZIP / Postal Code
                  .col-sm-6
                    tk-input-type-text(v-model='accountSettings.businessDetails.zip')
          div#passwordSpy
            h3 {{ translations.settings_password_title[defaultLang] | translate }}
            p {{ translations.settings_password_paragraph[defaultLang] | translate }}
            #password.section.scrollspy
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_password_current[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text-password(v-model='accountSettings.password.current')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_password_new[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text-password#new(v-model='accountSettings.password.new')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_password_repeat[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text-password#newRepeat(v-model='accountSettings.password.new_repeat')
                  .error(v-if='!passwordMatch')
                    p Passwords did not match!
                  .success(v-if='changed')
                    p Password saved
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                  tk-secondary-button(v-if='!accountSettings.password.current || !accountSettings.password.new || !accountSettings.password.new_repeat', name='Change Password')
                  tk-primary-button(v-if='accountSettings.password.current && accountSettings.password.new && accountSettings.password.new_repeat', name='Change Password', @click='savePassword')
          div#subscriptionSpy
            h3 {{ translations.settings_newsletter_title[defaultLang] | translate }}
            p {{ translations.settings_newsletter_paragraph[defaultLang] | translate }}
            #subscription.section.scrollspy
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_newsletter_email[defaultLang] | translate }}
                .col-sm-6
                  tk-input-type-text#subEmail(v-model='accountSettings.subscription.email')
                  h5(v-if='wrongEmailSub') Wrong email address
                  h5(v-if='!emptyEmailSub') Address saved
          div#shopSpy
            h3 {{ translations.settings_shop_title[defaultLang] | translate }}
            p {{ translations.settings_shop_paragraph[defaultLang] | translate }}
            #shop.section.scrollspy
              .row
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_shop_language[defaultLang] | translate }}
                .col-sm-6
                  tk-custom-select-uni#languages(:options='languages', v-model='accountSettings.shop.language')
                .col-sm-4.col-sm-offset-2
                  p {{ translations.settings_shop_currency[defaultLang] | translate }}
                .col-sm-6
                  tk-custom-select-uni#currencies(:options='currencies', v-model='accountSettings.shop.currency')
          div#deleteSpy
            h3 {{ translations.settings_delete_title[defaultLang] | translate }}
            p {{ translations.settings_delete_paragraph[defaultLang] | translate }}
            #delete.section.scrollspy
              .row
                .col-sm-4.col-sm-offset-2
                .col-sm-6
                  tk-secondary-button(name='Delete My Account')
              p {{ translations.settings_delete_info[defaultLang] | translate }}
            .row
              .col-sm-5
                tk-secondary-button(name='Revert To Saved')
              .col-sm-7
                tk-primary-button(name='Save Changes', @click='save')
</template>

<script>
  import { mapGetters } from 'vuex'
  export default {
    props: ['translations'],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {
      savePassword () {
        if (this.accountSettings.password.new !== this.accountSettings.password.new_repeat) {
          this.passwordMatch = false
          document.getElementById('new').classList.add('wrongInput')
          document.getElementById('newRepeat').classList.add('wrongInput')
        } else {
          document.getElementById('new').classList.remove('wrongInput')
          document.getElementById('newRepeat').classList.remove('wrongInput')
          this.passwordMatch = true
          this.changed = true
        }
      },
      save () {
        var mailformat = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i
        if (this.accountSettings.personalInfo.email_address.match(mailformat) || this.accountSettings.personalInfo.email_address === '') {
          document.getElementById('emails').classList.remove('wrongInput')
          this.wrongEmail = false
        } else {
          document.getElementById('emails').classList.add('wrongInput')
          document.getElementById('emails').focus()
          this.wrongEmail = true
        }
        if (this.accountSettings.subscription.email.match(mailformat)) {
          document.getElementById('subEmail').classList.remove('wrongInput')
          this.emptyEmailSub = false
          this.wrongEmailSub = false
        } else {
          document.getElementById('subEmail').classList.add('wrongInput')
          document.getElementById('subEmail').focus()
          this.emptyEmailSub = true
          this.wrongEmailSub = true
        }
      },
      deleteSpy () {
        document.getElementById('deleteSpy').scrollIntoView({behavior: 'smooth', block: 'start', inline: 'center'})
      },
      shopSpy () {
        document.getElementById('shopSpy').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      subSpy () {
        document.getElementById('subscriptionSpy').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      passwordSpy () {
        document.getElementById('passwordSpy').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      personalSpy () {
        document.getElementById('personalSpy').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      showBusiness () {
        this.business = true
      },
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      hideBusiness () {
        this.business = false
      }
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')

      window.jQuery(document).on('scroll', function () {
        if (window.jQuery(this).scrollTop() >= window.jQuery('#subscriptionSpy').position().top) {
          // document.getElementById('subLink').classList.toggle('active')
        }
        if (window.jQuery(this).scrollTop() >= window.jQuery('#shopSpy').position().top) {
          // document.getElementById('shopLink').classList.toggle('active')
        }
        if (window.jQuery(this).scrollTop() >= window.jQuery('#deleteSpy').position().top) {
          // document.getElementById('deleteLink').classList.toggle('active')
        }
        if (window.jQuery(this).scrollTop() >= window.jQuery('#passwordSpy').position().top) {
          // document.getElementById('passwordLink').classList.toggle('active')
        }
        if (window.jQuery(this).scrollTop() >= window.jQuery('#personalSpy').position().top) {
          // document.getElementById('personalLink').classList.toggle('active')
        }
      })
      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '72') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    },
    data () {
      return {
        business: false,
        wrongEmail: false,
        emptyEmailSub: true,
        wrongEmailSub: false,
        passwordMatch: true,
        changed: false,
        countries: [
          'Austria', 'Belgium', 'Bulgaria', 'Croatia', 'Cyprus', 'Czech Republic', 'Denmark', 'Estonia',
          'Finland', 'France', 'Germany', 'Greece', 'Hungary', 'Ireland', 'Italy', 'Latvia',
          'Lithuania', 'Luxembourg', 'Malta', 'Netherlands', 'Poland', 'Portugal', 'Romania', 'Slovakia',
          'Slovenia', 'Spain', 'Sweden', 'United Kingdom'
        ],
        mrms: [
          'Mr', 'Ms'
        ],
        currencies: [
          'EUR', 'USD', 'CZK'
        ],
        languages: [
          'English', 'German', 'Latvian', 'Lithuanian', 'German', 'Latvian'
        ],
        accountSettings: {
          personalInfo: {
            title: '',
            first_name: '',
            last_name: '',
            email_address: ''
          },
          billingAddress: {
            country: '',
            street: '',
            city: '',
            zip: ''
          },
          businessDetails: {
            company: '',
            reg: '',
            tax: ''
          },
          businessAddress: {
            country: '',
            street: '',
            city: '',
            zip: ''
          },
          password: {
            current: '',
            new: '',
            new_repeat: ''
          },
          subscription: {
            email: ''
          },
          shop: {
            language: '',
            currency: ''
          }
        }
      }
    }
  }
</script>

<style lang="less" scoped>
#countries, #currencies, #languages, #mrms{
  margin-bottom: 24px;
}
#personal{
  margin-bottom: 35px;
  padding-bottom: 20px;
  border-bottom: 1px solid #979797;
}
#billing{
  margin-bottom: 32px;
  padding-bottom: 32px;
  border-bottom: 1px solid #979797;
}
#business{
  margin-bottom: 32px;
  padding-bottom: 20px;
  border-bottom: 1px solid #979797;
}
#password{
  margin-bottom: 32px;
  padding-bottom: 32px;
  border-bottom: 1px solid #979797;
}
#subscription{
  margin-bottom: 32px;
  padding-bottom: 20px;
  border-bottom: 1px solid #979797;
  h5{
    margin-top: -12px;
    font-size: 12px;
  }
}
#shop{
  margin-bottom: 32px;
  padding-bottom: 20px;
  border-bottom: 1px solid #979797;
}
#delete{
  margin-bottom: 32px;
  padding-bottom: 20px;
  border-bottom: 1px solid #979797;
  button{
    margin-bottom: 32px;
  }
}
.businessperson{
  text-transform: uppercase;
  border: none;
  text-decoration: underline;
  background-color: transparent;
  padding: 0;
  outline: none !important;
}
section{
  position: relative;
  margin-top: 32px;
  margin-bottom: 64px;
  nav{
    padding: 24px;
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    p{
      color: #9a9a9a;
      text-transform: uppercase;
      margin-bottom: 24px;
    }
    ul{
      text-decoration: none;
      padding: 0;
      list-style-type: none;
      li{
        margin-bottom: 16px;
        cursor: pointer;
        &:last-child{
          margin: 0;
        }
      }
      a{
        cursor: pointer;
        text-transform: capitalize;
        text-decoration: none;
        &:hover{
          color: #292929;
        }
      }
    }
  }
  .col-sm-offset-2{
    p{
      margin-top: 14px;
    }
  }
  h3{
    margin-top: 0;
  }
  h3 + p{
    margin-bottom: 21px;
  }
  h4{
    margin-bottom: 24px;
  }
  input[type='text'], select{
    margin-bottom: 24px;
  }
  .line{
    margin-bottom: 24px;
  }
  .business{
    padding: 0;
    border: none;
    background-color: transparent;
    text-transform: uppercase;
    text-decoration: underline;
    margin-bottom: 24px;
  }
  .account{
    margin: 32px 0;
  }
}
.error, .success{
  text-align: left;
  margin-top: -16px;
  margin-bottom: 14px;
  p{
    font-size: 12px;
    font-weight: bold;
    margin: 0;
  }
}
.wrongInput{
  border: 3px solid #292929 !important;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s ease;
}
.slide-fade-enter, .slide-fade-leave-to{
  transform: translateX(10px);
  opacity: 0;
}
/* Media Queries */
/* Extra Small */
@media(min-width:0px) and (max-width:767px){
  section{
    margin-top: 0;
    margin-bottom: 40px;
    nav{
      margin-bottom: 28px;
      margin-left: -15px;
      margin-right: -15px;
      padding: 16px;
    }
    input[type='text'], select{
      margin-bottom: 15px;
    }
    h4{
      margin-bottom: 10px;
    }
    h3 + p{
      margin-bottom: 14px;
    }
    .col-sm-offset-2 p{
      margin-top: 0;
    }
  }
  #personal{
    padding-bottom: 9px;
    margin-bottom: 24px;
  }
  #business{
    padding-bottom: 17px;
  }
  .businessperson{
    text-align: left;
  }
  #billing{
    padding-bottom: 20px;
  }
  #password{
    padding-bottom: 32px;
  }
  .business{
    text-align: left;
  }
  button{
    width: 100%;
  }
  .col-sm-5, .col-sm-7{
    button{
      width: 100%;
      margin-bottom: 16px;
    }
  }
}
.active{
  font-weight: bold;
}
.inactive{
  font-weight: normal;
}
.fixed{
  position: fixed;
  background-color: #f7f7f7;
  z-index: 9999;
  width: 360px;
  top: 32px;
}
</style>



