<template lang="pug">
  .calendar
    button#tickets(type='button', data-toggle='modal', :data-target="'#event-tickets' + _uid") See tickets
    .modal.fade.animated.fadeInUpBig(role='dialog', :id="'event-tickets' + _uid")
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-header
            h3 {{ translations.event_page_ticket_list[defaultLang] | translate }}
          .modal-body
            nav
              button(@click='showTickets()', v-bind:class="{ active: !seating }") {{ translations.event_page_ticket_list_menu[defaultLang] | translate }}
              button(@click='showSeating()', v-bind:class="{ active: seating }") {{ translations.event_page_ticket_list_menu_2[defaultLang] | translate }}
            #info
              .row
                .col-sm-4.types
                  p 
                    strong {{ allSeats.length }}
                    | {{ translations.event_page_ticket_list_info[defaultLang] | translate }}
                .col-sm-4.date
                  p  kaut kads date
                .col-sm-4.different
                  button {{ translations.event_page_ticket_list_info_2[defaultLang] | translate }}
            #seating(v-if='seating')
              img(src='/static/seating.png')
            #tickets-list(v-if='!seating')
              .tickets(v-for='(t, tindex) in allSeats')
                .row
                  .col-sm-8.ticket-type
                    .tickets-wrapper
                      .tickets-body
                        h3(:class='{ soldOut: (t.availability.available === 0)}') {{ translations.event_page_ticket_list_ticket[defaultLang] | translate }} {{ tindex + 1 }}
                        p(:class='{ soldOut: (t.availability.available === 0)}') {{ translations.event_page_ticket_list_price[defaultLang] | translate }}: 
                          strong {{ t.price.amount / 100 | currency('') }}€
                  .col-sm-4.ticket-amount
                    .tickets-wrapper
                      .tickets-body
                        tk-spinner(@input="addTicket(t, $event)", v-if='(t.availability.available > 0)', :maxTickets='t.availability.available')
                        span.sold(v-if='(t.availability.available === 0)') Sold Out
                .tickets-selected(v-for='(n, index) in getTicketsById(t.id)')
                  .row
                    .col-sm-6.tickets-selected-count
                      p {{ translations.event_page_ticket_list_ticket[defaultLang] | translate }} {{ index + 1 }}
                      p {{ translations.event_page_ticket_list_price[defaultLang] | translate }}:
                        strong {{ t.price.amount / 100 | currency('') }}€
                    .col-sm-6
                      tk-custom-select-uni(:options='seats_type')
              #discount
                .row
                  .col-sm-8
                    .tickets-wrapper
                      .tickets-body
                        .discount-accepted
                          p Discount code (GC_DITS_01) of value 20% has been accepted! Your discount amount has automatically assigned to total price.
                  .col-sm-4
                    .tickets-wrapper
                      .tickets-body
                        tk-discount-input-tickets
          .modal-footer
            .row
              .col-sm-4.tickets-selected-footer
                .footer-wrapper
                  .footer-body
                    p {{ translations.event_page_ticket_list_footer_tickets[defaultLang] | translate }}
                      strong {{ totalSelected }}
              .col-sm-4.tickets-selected-footer
                .footer-wrapper
                  .footer-body
                    p {{ translations.event_page_ticket_list_footer_price[defaultLang] | translate }}
                      strong {{ (totalPrice / 100) + (discount / 100) | currency('') }} &#8364;
              .col-sm-4
                .footer-wrapper
                  .footer-body
                    tk-primary-button(:name="translate(translations.event_page_ticket_list_button[defaultLang])", @click='toCheckout', v-if='totalPrice')
                    tk-disabled-button(:name="translate(translations.event_page_ticket_list_button[defaultLang])", v-else)
</template>

<script>
import * as types from '@/store/mutation-types'
import Api from '@/services/ApiService'
import { mapGetters } from 'vuex'

export default {
  props: ['translations'],
  data () {
    return {
      discount_code: '',
      voucher: null,
      seating: false,
      seats_type: [
        'Child ticket',
        'Elderly',
        'Adult'
      ]
    }
  },
  created () {
    this.$store.dispatch('fetch_seats', this.eventId)
  },
  computed: {
    ...mapGetters({
      eventId: 'getSelectedPackageId',
      allSeats: 'getAllSeats',
      totalSelected: 'getTicketsSelected',
      tickets: 'getTickets',
      totalPrice: 'getTotalPrice',
      discount: 'totalDiscount',
      defaultLang: 'defaultLanguage'
    })
  },
  methods: {
    showSeating () {
      this.seating = true
    },
    showTickets () {
      this.seating = false
    },
    translate (translate) {
      return this.$options.filters.translate(translate)
    },
    getTicketsById (id) {
      var tickets = []
      for (var i = 0; i < this.tickets.length; i++) {
        if (this.tickets[i].id === id) {
          tickets.push(this.tickets[i])
        }
      }
      return tickets
    },
    addTicket (ticket, amount) {
      var selectedTickets = []
      for (var i = 0; i < amount; i++) {
        var singleTicket = {}
        singleTicket.id = ticket.id
        singleTicket.price = ticket.price
        selectedTickets.push(singleTicket)
      }
      this.$store.commit(types.DELETE_TICKETS, ticket.id)
      this.$store.commit(types.ADD_TICKET, selectedTickets)
    },
    applyDiscount () {
      new Api().getShopVoucher({code: this.discount_code}).then(response => {
        this.$store.commit(types.SET_VOUCHERS, response.data)
        if (response.data.length > 0) {
          this.voucher = true
          this.totalPrice = this.totalPrice - response.data[0].discount.amount
        } else {
          this.voucher = false
        }
      })
    },
    toCheckout () {
      window.jQuery('#event-tickets' + this._uid).modal('hide')
      this.$emit('toCheckout')
    }
  }
}
</script>

<style lang="less" scoped>
#tickets{
  transition: 0.3s;
  background-color: #292929;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  min-height: 48px;
  padding: 10px 18px;
  border: none;
  outline: none !important;
  &:hover{
    background-color: #424242;
  }
}
button{
  outline: 0;
}
.modal-dialog{
  width: 770px;
}
.modal-content{
  background-color: #f7f7f7;
  border: none;
  padding: 0  16px;
  border-radius: 0;
}
.modal-close{
  text-align: right;
  padding: 8px 0;
  button{
    border: none;
    background-color: transparent;
    color: #fff;
    text-transform: uppercase;
    padding: 0;
  }
}
.modal-header{
  text-align: center;
  padding: 24px 16px;
  margin-bottom: 24px;
  border-bottom: 1px solid #979797;
  h3{
    margin: 0;
  }
}
.modal-body{
  padding: 0;
  #session{
    margin-bottom: 24px;
  }
  h5{
    color: #9a9a9a;
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 24px;
  }
}
label{
  margin-bottom: 32px;
  text-align: left;
  &:last-child{
    margin-bottom: 50px;
  }
  strong{
    padding: 0 4px;
  }
}
.modal-footer{
  padding: 0 32px;
  margin-left: -16px;
  margin-right: -16px;
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
}
.tickets{
  background-color: #fff;
  margin-bottom: 16px;
  padding: 16px;
  .tickets-wrapper{
    display: table;
    height: 55px;
    width: -webkit-fill-available;
  }
  .tickets-body{
    display: table-cell;
    vertical-align: middle;
  }
  .ticket-type{
    text-align: left;
  }
  .ticket-amount{
    text-align: right;
  }
}
nav{
  text-align: center;
  margin-bottom: 34px;
  .active{
    position: relative;
    font-weight: bold;
    &:after{
      position: absolute;
      content: '';
      background-color: #292929;
      width: 16px;
      height: 1px;
      left: 0;
      right: 0;
      bottom: -8px;
      margin: auto;
    }
  }
  button{
    font-size: 18px;
    border: none;
    padding: 0;
    background-color: transparent;
    &:first-child{
      margin-right: 24px;
    }
    &:last-child{
      margin-left: 24px;
    }
  }
}
#info{
  padding: 0 16px;
  strong{
    padding-right: 8px;
  }
  .types{
    text-transform: uppercase;
    text-align: left;
  }
  .date{
    font-weight: bold;
    text-align: center;
  }
  .different{
    text-align: right;
    margin-bottom: 16px;
    button{
      text-transform: uppercase;
      border: none;
      background-color: transparent;
      padding: 0;
      text-decoration: underline;
    }
  }
}
.tickets{
  strong{
    padding-right: 4px;
  }
  h3{
    margin: 0 0 8px 0;
  }
  p{
    margin: 0;
  }
}
#discount{
  padding: 0 16px;
  margin: 32px 0;
  p{
    color: #0e4d11;
    text-align: left;
    font-weight: bold;
    text-decoration: underline;
    margin: 0;
  }
}
#seating{
  text-align: center;
  background-color: #fff;
  margin-bottom: 32px;
}
.tickets-selected{
  padding-top: 24px;
  padding-bottom: 8px;
  margin-top: 24px;
  border-top: 1px solid #979797;
  strong{
    padding-left: 4px;
  }
  .tickets-selected-count{
    text-align: left;
  }
}
.tickets-selected-footer{
  text-align: left;
}
.footer-wrapper{
  display: table;
  height: 88px;
  width: 100%;
  .footer-body{
    display: table-cell;
    vertical-align: middle;
    strong{
      padding-left: 8px;
    }
  }
}           
.soldOut{
  color: #9a9a9a;
}
.sold{
  border: 1px solid #292929;
  text-transform: uppercase;
  font-weight: bold;
  padding: 5px;
}
@media(min-width:0px) and (max-width:767px){
  #tickets{
    width: 100%;
  }
}
</style>



