<template lang="pug">
  table
    caption All event overview
    thead
      tr.header
        th(scope='col') Event
        th(scope='col') Start
        th(scope='col') End
        th(scope='col') Tickets
        th(scope='col') Prices
        th(scope='col') Exp. Revenue
        th(scope='col') 
    tbody
      tr(v-for='(e, index) in all_events')
        td(data-label='Event') {{ e.title }}
        td(data-label='Start date') {{e.date_start | date }}
        td(data-label='End date') {{ e.date_end | date }}
        td(data-label='Tickets') -
        td(data-label='Prices') -
        td(data-label='Exp revenue') -
        td 
          tk-tools-second(@duplicateEvent='duplicateEvent(index)', @deleteEvent='deleteEvent(index)')
      tr.footer
        td(data-label='Total', colspan='7', style='text-align: right;')
          span TOTAL EVENTS: 
            strong(v-if="all_events") {{ all_events.length }}
          span TOTAL TICKETS:
            strong 450
          span TOTAL EXP. REVENUE:
            strong 14 900.00 $
</template>

<script>
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'
  import moment from 'moment'

  export default {
    computed: {
      ...mapGetters({
        all_events: 'all_events',
        all_tickets: 'all_tickets',
        subevents: 'subevents'
      })
    },
    methods: {
      deleteEvent (i) {
        this.$store.commit(types.DELETE_ADMIN_EVENT, i)
      },
      duplicateEvent (i) {
        this.$store.commit(types.DUPLICATE_ADMIN_EVENT, i)
      }
    },
    filters: {
      date (value) {
        return moment(value, 'YYYY-MM-DD HH-mm').format('DD.MM.YYYY (HH:mm)')
      }
    }
  }
</script>

<style lang="less" scoped>
table {
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  margin-bottom: -24px;
  tbody{
    font-size: 18px;
  }
}
table caption {
  color: #292929;
  font-size: 18px;
  margin-bottom: 18px;
}
.header{
  color: #fff;
  text-transform: none;
  background-color: #9a9a9a;
  &:hover{
    background-color: #9a9a9a !important;
  }
}
table tr {
  background: #f7f7f7;
  border-bottom: 1px solid #979797;
  &:hover{
    background-color: #e4e4e4;
  }
}
table th{
  padding: 16px;
}
table td {
  padding: 26px 16px;
  text-align: left;
}
table th {
  font-size: 14px;
}
.footer{
  font-size: 14px;
  border-bottom: none;
  span{
    margin-left: 40px;
    strong{
      margin-left: 8px;
    }
  }
}
@media screen and (max-width: 600px) {
  table {
    border: 0;
  }
  table caption {
    font-size: 1.3em;
  }
  table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
  }
  table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: .625em;
  }
  table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: .8em;
    text-align: right;
  }
  table td:before {
    /*
    * aria-label has no advantage, it won't be read inside a table
    content: attr(aria-label);
    */
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase;
  }
  table td:last-child {
    border-bottom: 0;
  }
}
</style>



