<template lang="pug">
  header
    .container
      .row
        .col-sm-6
          .header-content
            .header-body
              img(src='static/ticketyl-crop.png')
        .col-sm-6.text-right
          .header-content
            .header-body
              tk-ticketyl-lang(:currency='currency')
              tk-modal-log-in
              tk-modal-sign-up
</template>

<script>
  export default {
    data () {
      return {
        currency: [ 'ENG', 'LAT' ]
      }
    }
  }
</script>

<style lang="less" scoped>
  header{
    background-color: #f7f7f7;
    .header-content{
      display: table;
      height: 144px;
      width: 100%;
      .header-body{
        display: table-cell;
        vertical-align: middle;
        img{
          height: 42px;
          vertical-align: top;
        }
      }
    }        
  }
</style>



