<template lang="pug">
  section
    tk-header-admin
    .settings
      .container
        #settings
          .row
            .col-sm-4
              .settings-content
                .settings-body
                  h2 Main Settings
            .col-sm-8.text-right
              .settings-content
                .settings-body
                  tk-secondary-button(name='revert to saved')
                  tk-primary-button(name='save changes')
    .container
      .navigation
        .row
          .col-sm-12
            .menu-content
              .menu-body
                router-link(:to="{name: 'Events'}") My Account
                router-link(:to="{name: 'Events'}") Shop Preferences
                router-link.current(:to="{name: 'Events'}") Manage Team
                router-link(:to="{name: 'Events'}") Notifications
    .content
      .container
        .row
          .col-sm-4#js-scrollspy 
            nav#aside
              p Manage Team
              ul.js-scrollspy-nav
                li#accountLink(@click='accountSpy') 1. Your Account Information
                li#teamLink(@click='teamSpy') 2. Team Members
          .col-sm-8#internal-content
            div#account-scroll
              h3 1. Your Account Information
              p All of the information we need about you
              #account.section.scrollspy
                .row
                  .col-sm-4.col-sm-offset-2
                    p.center * Title
                  .col-sm-6
                    tk-custom-select-string(v-model='merchant_update.title').margin-bottom
                  .col-sm-4.col-sm-offset-2
                    p.center * First Name
                  .col-sm-6
                    tk-input-type-text(v-model='merchant_update.first_name')
                  .col-sm-4.col-sm-offset-2
                    p.center * Last Name
                  .col-sm-6
                    tk-input-type-text(v-model='merchant_update.last_name')
                  .col-sm-4.col-sm-offset-2
                    p.center * Email Address
                  .col-sm-6
                    tk-input-type-text(v-model='merchant_update.email').last
            #team.section.scrollspy
              h3 2. Team Members
              p Add team members and control their permissions
              .row
                .col-sm-4.col-sm-offset-2
                  p.center Team Member Email
                .col-sm-6
                  tk-input-type-text.last(placeholder="email@email.com", v-model='info.email')
                .col-sm-4.col-sm-offset-2
                  p.center Permissions
                .col-sm-6
                  form(action='')
                    input(type='checkbox', name='card', id="visa", v-model='info.permissions')
                    label(for="visa") Manage Shops
                    input(type='checkbox', name='card', id="american", v-model='info.permissions')
                    label(for="american") Manage Events
                    input(type='checkbox', name='card', id="paypal", v-model='info.permissions')
                    label(for="paypal") Manage Team Members
                    input(type='checkbox', name='card', id="sofort", v-model='info.permissions')
                    label(for="sofort") Check In
                    input(type='checkbox', name='card', id="bitcoin", v-model='info.permissions')
                    label(for="bitcoin") Promotion
                .row.last
                  .col-sm-4.col-sm-offset-2
                    p.center Shops
                  .col-sm-6
                    button.action Only Dinner in the Sky
                .row
                  .col-sm-4.col-sm-offset-2
                  .col-sm-6
                    tk-secondary-button.last(name='add team member')
            .row
              .col-sm-8
                tk-secondary-button.right(name='revert to saved')
                tk-primary-button(@click='save', name='save changes')
    tk-footer-admin
</template>

<script>
  import * as types from '@/store/mutation-types'
  import store from '@/store'

  export default {
    data () {
      return {
        visibility: ['Public', 'Private'],
        language: ['English', 'Latvian'],
        currency: ['Euro', 'USD'],
        timezone: ['UTC+3', 'GMT'],
        merchant_update: {
          'title': 'Mr',
          'first_name': '',
          'last_name': '',
          'email': ''
        },
        info: {
          'email': '',
          'permissions': [],
          'shops': []
        }
      }
    },
    beforeRouteEnter (to, from, next) {
      if (store.getters.logInAdminToken === '') {
        next('/ticketyl')
      }
      next()
    },
    methods: {
      save () {
        this.$store.commit(types.ADD_TEAM, this.info)
      },
      accountSpy () {
        document.getElementById('account').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      },
      teamSpy () {
        document.getElementById('team').scrollIntoView({behavior: 'smooth', block: 'center', inline: 'center'})
      }
    },
    mounted () {
      const mq = window.matchMedia('(max-width: 767px)')

      var aside = window.jQuery('#aside')
      window.jQuery(window).scroll(function () {
        var scroll = window.jQuery(window).scrollTop()
        if (mq.matches) {
          return
        }
        if (scroll >= '350') {
          aside.addClass('fixed')
        } else {
          aside.removeClass('fixed')
        }
      })
    }
  }
</script>

<style lang="less" scoped>
#account, #team{
  margin-bottom: 32px;
  border-bottom: 1px solid #979797;
}
.navigation{
  border-bottom: 1px solid #979797;
  margin-bottom: 32px;
  .menu-content{
    display: table;
    height: 88px;
    width: 100%;
    .menu-body{
      display: table-cell;
      vertical-align: middle;
      a{
        text-transform: none;
        position: relative;
        text-decoration: none;
        margin-right: 50px;
        color: #292929;
        font-size: 18px;
        .router-link-exact-active{
          position: relative;
          width: 10px;
          height: 2px;
          -webkit-text-stroke: 1px #292929;
          &:after{
            content: "";
            position: absolute;
            width: 10px;
            height: 1px;
            background-color: #292929;
            left: 0;
            right: 0;
            bottom: -10px;
            margin: 0 auto;
          }
        }
      }
    }
  }
}               
.settings{
  background-color: #f7f7f7;
  #settings{
    border-bottom: 1px solid #979797;
  }
  .settings-content{
    display: table;
    height: 116px;
    width: 100%;
    .settings-body{
      display: table-cell;
      vertical-align: middle;
      h2{
        margin: 0;
      }
      button{
        margin-left: 24px;
      }
      .back{
        text-transform: uppercase;
        padding: 0;
        border: none;
        outline: 0;
        text-decoration: underline;
        background-color: transparent;
      }
    }
  }        
}
.content{
  margin-bottom: 120px;
}
nav{
  padding: 24px;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
  p{
    color: #9a9a9a;
    text-transform: uppercase;
    margin-bottom: 24px;
  }
  ul{
    text-decoration: none;
    padding: 0;
    list-style-type: none;
    li{
      margin-bottom: 16px;
      cursor: pointer;
      &:last-child{
        margin: 0;
      }
    }
    a{
      cursor: pointer;
      text-transform: capitalize;
      text-decoration: none;
      &:hover{
        color: #292929;
      }
    }
  }
}
.active{
  font-weight: bold;
}
.center{
  margin: 0;
  margin-top: 12px;
}
.current{
  position: relative;
  -webkit-text-stroke: 1px #292929;
  &:after{
    position: absolute;
    content: '';
    width: 14px;
    height: 1px;
    background-color: #292929;
    bottom: -11px;
    left: 0;
    right: 0;
    margin: auto;
  }
}
.fixed{
  position: fixed;
  background-color: #f7f7f7;
  z-index: 1;
  width: 360px;
  top: 32px;
}
input[type='text']{
  margin-bottom: 24px;
}
.margin-bottom{
  margin-bottom: 24px;
}
.last{
  margin-bottom: 32px !important;
}
.action{
  outline: 0;
  text-transform: uppercase;
  padding: 0;
  background-color: transparent;
  text-decoration: underline;
  border: none;
}
.right{
  margin-right: 32px;
}
h3{
  margin-top: 0;
  +p{
    margin-bottom: 32px;
  }
}
h4{
  margin-bottom: 24px;
}
form{
  margin-bottom: 32px;
  img{
    height: 22px;
    width: 22px;
    margin-right: 8px;
  }
}
.gift-card-content{
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2) 0%,rgba(0,0,0,0.2) 100%), url('/static/giftcard_bg.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: table;
  height: 184px;
  width: 100%;
  margin-bottom: 32px;
  .gift-card-body{
    color: #fff;
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    p{
      margin-bottom: 16px;
    }
  }
}
</style>



