<template lang="pug">
  div
    button.logIn(type='button', data-toggle='modal', data-target="#checkoutLogin") Log in to your account
    #checkoutLogin.modal.fade(role='dialog')
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close
            i.material-icons clear
        .modal-content
          .modal-content-wrapper
            .modal-header
              h3 Log In
              p Use your saved info and preferences
            .modal-body
              tk-input-type-text#emails(placeholder='name', v-model='user.email')
              .error(v-if="wrongEmail")
                p Wrong email address
              tk-input-type-text-forgot(placeholder='password', v-model='user.password')
              .error(v-if="wrong")
                p There was an error with your E-Mail/Password combination. Please try again.
              tk-primary-button.signIn(v-if='user.email && user.password', @click='signIn', name='Sign in')
              tk-disabled-button.disabled(name='Sign in', v-else)
</template>

<script>
  import Api from '@/services/ApiService'
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    data () {
      return {
        emailValidated: false,
        wrong: false,
        wrongEmail: false,
        user: {
          email: '',
          password: '',
          expires: ''
        }
      }
    },
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      signIn () {
        var mailformat = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i
        if (this.user.email.match(mailformat)) {
          this.wrongEmail = false
          this.wrong = false
          document.getElementById('emails').classList.remove('wrongInput')
          new Api().postUserAuth({email: this.user.email, password: this.user.password}).then(response => {
            this.$store.commit(types.ADD_TOKEN, response.data.token)
            window.jQuery('#checkoutLogin').modal('hide')
          }).catch(response => { this.wrong = true })
        } else {
          this.wrongEmail = true
          document.getElementById('emails').classList.add('wrongInput')
          document.getElementById('emails').focus()
        }
      }
    }
  }
</script>

<style lang="less" scoped>
.logIn{
  transition: 0.3s;
  background-color: #292929;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  width: 100%;
  min-height: 48px;
  padding: 10px 18px;
  border-radius: 0;
  border: 0;
  outline: none !important;
  &:hover{
    background-color: #424242;
  }
}
.modal-dialog{
  width: 402px;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  button{
    padding: 0;
    outline: none !important;
    border: none;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    border: none;
    background-color: transparent;
    .material-icons{
      font-size: 18px;
      vertical-align: text-bottom;
      padding-left: 4px;
    }
  }
}
.modal-header{
  text-align: left;
  border-bottom: 1px solid #979797;
  padding: 0;
  margin-bottom: 30px;
  h3{
    margin: 0;
    margin-bottom: 8px;
  }
  h3 + p{
    margin-bottom: 24px;
  }
}
.modal-content{
  background-color: #f7f7f7;
  border-radius: 0;
}
.modal-content-wrapper{
  padding: 22px 32px;
}
.modal-body{
  padding: 0;
  text-align: left;
  .wrongInput{
    border: 3px solid #292929 !important;
  }
}
input[type='text']{
  margin-bottom: 24px;
}
.signIn, .disabled{
  width: 100%;
  margin-bottom: 24px;
}
.create{
  text-transform: uppercase;
  text-decoration: underline;
  border: none;
  background-color: transparent;
  padding: 0;
  margin-bottom: 24px;
}
.wrongInput{
  border: 3px solid #292929 !important;
}
.error{
  margin-top: -16px;
  margin-bottom: 14px;
  p{
    font-size: 12px;
    font-weight: bold;
    margin: 0;
  }
}
</style>



