<template lang="pug">
  .calendar
    button#events(type='button', data-toggle='modal', :data-target="'#event-calendar' + _uid") Tickets
    .modal.fade.animated.fadeInUpBig(role='dialog', :id="'event-calendar' + _uid")
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-header
            h3 {{ translations.event_page_calendar[defaultLang] | translate }}
          .modal-body
            tk-custom-select#session(:options='packages', @input='setSelectedPackageId($event)')
            p {{ translations.event_page_calendar_paragraph[defaultLang] | translate }}
            .row
              .col-sm-6
                tk-calendar(v-model='selectedDate', :enabledDates='eventDates')
              .col-sm-6
                h5 {{ translations.event_page_calendar_events_date[defaultLang] | translate }}
                form  
                  template(v-for='(e, index) in eventsOnDate')
                    input(type="radio" name="time" :id="'t' + index" v-model='selectedEventTime' v-bind:value='e') 
                    label(v-bind:for="'t' + index") 
                      span {{ translations.event_page_calendar_events_time[defaultLang] | translate }}:
                        strong {{ getTime(e.datetime.date) }} 
                        | | Prices: 
                        strong {{ e.price_range.min.amount / 100 | currency('') }} {{ e.price_range.min.currency.code / 100| currency('') }} 
                        | - 
                        strong {{ e.price_range.max.amount / 100| currency('') }} {{ e.price_range.max.currency.code / 100| currency('') }}
          .modal-footer
            tk-mans-ticket-modal(:translations='translations', @click='hides')
</template>

<script>
import Calendar from '@/components/single-event-page/Calendar'
import moment from 'moment'
import * as types from '@/store/mutation-types'
import { mapGetters } from 'vuex'

export default {
  name: 'EventCalendar',
  props: [
    'translations'
  ],
  data () {
    return {
      selectedEvent: null,
      selectedDate: null,
      selectedEventTime: null,
      enabledDates: [],
      eventsOnDate: []
    }
  },
  computed: {
    ...mapGetters({
      packages: 'getPackages',
      selectedPackageId: 'selectedPackageId',
      eventDates: 'getEventDates',
      events: 'getEvents',
      defaultLang: 'defaultLanguage'
    })
  },
  methods: {
    hides () {
      window.jQuery('#event-calendar' + this._uid).modal('hide')
    },
    translate (translate) {
      return this.$options.filters.translate(translate)
    },
    seeTicketsClicked () {
      if (this.selectedEventTime != null) {
        this.$emit('seeTickets')
      }
    },
    setSelectedPackageId (packageId) {
      var seriesId = this.$store.getters.getSeriesId
      this.$store.commit(types.SET_SELECTEDPACKAGEID, packageId)
      this.$store.commit(types.DELETE_SEATS)
      this.$store.dispatch('fetch_events', {seriesId, packageId})
    },
    getTime (a) {
      return moment(a).format('hh:mm')
    }
  },
  watch: {
    selectedEventTime (event) {
      this.$emit('input', event)
      this.$store.commit(types.GET_SELECTEDEVENT, event)
    },
    selectedDate (date) {
      this.eventsOnDate = []
      this.selectedEventTime = null
      var mDate = moment(date)
      for (var i = 0; i < this.events.length; i++) {
        mDate.isSame(this.events[i].datetime.date, 'day')
        if (mDate.isSame(this.events[i].datetime.date, 'day')) {
          this.eventsOnDate.push(this.events[i])
        }
      }
    }
  },
  created () {
    // get event list from backend
    // this.events = axios.get...
    // this.selectedEvent = this.events[0]
    // get available dates for event
    // this.enabledDates = axios.get...
    /*
    this.enabledDates = [
      moment().add(1, 'days'),
      moment().add(2, 'days'),
      moment().subtract(3, 'days')
    ]
    */
  },
  components: {
    tkCalendar: Calendar
  }
}
</script>

<style lang="less" scoped>
#events{
  width: 100%;
  transition: 0.3s;
  background-color: #292929;
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  min-height: 48px;
  padding: 10px 18px;
  border: none;
  outline: none !important;
  &:hover{
    background-color: #424242;
  }
}
button{
  outline: 0;
}
.modal-dialog{
  width: 770px;
}
.modal-content{
  background-color: #f7f7f7;
  border: none;
  padding: 0 16px;
  border-radius: 0;
}
.modal-close{
  text-align: right;
  padding: 8px 0;
  button{
    border: none;
    background-color: transparent;
    color: #fff;
    text-transform: uppercase;
    padding: 0;
  }
}
.modal-header{
  text-align: center;
  padding: 24px 16px;
  margin-bottom: 24px;
  border-bottom: 1px solid #979797;
  h3{
    margin: 0;
  }
}
.modal-body{
  width: 570px;
  margin: 0 auto;
  padding: 0;
  #session{
    margin-bottom: 24px;
  }
  h5{
    color: #9a9a9a;
    text-transform: uppercase;
    text-align: left;
    margin-bottom: 24px;
  }
  p{
    text-align: center;
  }
}
label{
  margin-bottom: 32px;
  text-align: left;
  &:last-child{
    margin-bottom: 50px;
  }
  strong{
    padding: 0 4px;
  }
}
.modal-footer{
  padding: 20px 32px;
  margin-left: -16px;
  margin-right: -16px;
  background-color: #fff;
  box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
}
@media(min-width:0px) and (max-width:767px){
  .modal-close{
    border-top: 1px solid #979797;
    display: block;
    position: absolute;
    bottom: 0;
    width: 90%;
    margin-left: 5%;
    background-color: #fff;
    text-align: center;
    z-index: 1;
    padding: 20px;
    button{
      color: #292929;
      border: none;
      background-color: transparent;
      padding: 0;
      text-transform: uppercase;
    }
  }
  .modal-dialog{
    width: auto;
  }
  .modal-content{
    padding: 8px 8px 0 8px;
  }
  .modal-header{
    padding: 16px;
  }
  .modal-body{
    width: auto;
  }
  .modal-footer{
    padding: 16px;
    padding-bottom: 80px;
  }
  label, label:last-child{
    margin-bottom: 27px;
  }
}
</style>



