<template lang="pug">
  .div
    #reservation.modal.fade(role='dialog')
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") Close &#10005;
        .modal-content
          .modal-body
            h2 {{ translations.checkout_released[defaultLang] | translate }}
            p {{ translations.checkout_released_paragraph[defaultLang] | translate }}
          .modal-footer
            tk-primary-button(@click='goTo', :name="translate(translations.checkout_released_button[defaultLang])", style='width: 100%;')
</template>

<script>
  import * as types from '@/store/mutation-types'
  import { mapGetters } from 'vuex'

  export default {
    props: [ 'translations' ],
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    methods: {
      translate (translate) {
        return this.$options.filters.translate(translate)
      },
      goTo () {
        this.$router.push({name: 'EventList'})
        this.$store.commit(types.DELETE_SEATS)
      }
    },
    mounted () {
      window.jQuery('#reservation').on('hidden.bs.modal', () => {
        this.$router.push({name: 'EventList'})
        this.$store.commit(types.DELETE_SEATS)
      })
    }
  }
</script>

<style lang="less" scoped>
button{
  outline: 0;
}
.modal-dialog{
  max-width: 402px;
}
.modal-close{
  padding: 10px 0;
  text-align: right;
  .material-icons{
    font-size: 18px;
    vertical-align: text-bottom;
    padding-left: 4px;
  }
  button{
    padding: 0;
    font-size: 14px;
    text-transform: uppercase;
    color: #fff;
    width: auto;
    border: none;
    background-color: transparent;
    outline: none !important;
  }
}
.modal-content{
  padding: 24px;
  background-color: #f7f7f7;
  border-radius: 0;
  border: none;
  .modal-body{
    padding: 0;
    border-bottom: 1px solid #979797;
    padding-bottom: 24px;
    margin-bottom: 24px;
    p{
      margin: 0;
    }
    h2{
      margin-top: 0;
    }
  }
}
.modal-footer{
  padding: 0;
}
@media(min-width:0px) and (max-width:767px){
  .modal-content{
    min-height: 100vh;
  }
  .modal-close{
    box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
    display: block;
    position: absolute;
    bottom: 0;
    width: 100%;
    background-color: #fff;
    text-align: center;
    z-index: 1;
    padding: 20px;
    button{
      color: #292929;
      border: none;
      background-color: transparent;
      padding: 0;
      text-transform: uppercase;
    }
  }
}
</style>


