import * as types from '../mutation-types'

const state = {
  error: null
}
const mutations = {
  [types.SET_ERROR] (state, instance) {
    console.log('x')
    state.error = instance
  }
}
const getters = {
  error: state => {
    return state.error
  }
}
export default {
  state,
  mutations,
  getters
}



