<template lang="pug">
  span
    input.tk-calendar.calendar(v-bind:value='date', :id="'tkcalendar' + _uid", v-on:input='dateSelected($event.target.value)')
</template>

<script>
  import 'eonasdan-bootstrap-datetimepicker'
  import moment from 'moment'
  import locale from '../../locale/en-gb'

  export default {
    props: ['enabledDates'],
    watch: {
      enabledDates (acts) {
        var enabled = []
        for (var i in acts) {
          var formated = moment(acts[i].datetime.date).format('YYYY-MM-DD')
          enabled.push(formated)
        }
        if (!enabled.length) {
          enabled = ['1970-01-01']
        }
        this.datetimepicker.data('DateTimePicker').enabledDates(enabled)
      }
    },
    methods: {
      dateSelected (e) {
        this.$emit('input', e.date)
      }
    },
    data () {
      return {
        datetimepicker: null,
        date: ''
      }
    },
    created () {
      moment.defineLocale('en-gb', locale['en-gb'])
    },
    mounted () {
      if (this.datetimepicker) return
      this.datetimepicker = window.jQuery(this.$el).datetimepicker({
        enabledDates: this.enabledDates.length > 0 ? this.enabledDates : ['1970-01-01'],
        inline: true,
        format: 'YYYY-MM-DD',
        useCurrent: true,
        viewMode: 'days',
        locale: 'en-gb'
      })
      this.datetimepicker.show()
      window.jQuery('th[data-action="pickerSwitch"]').on('click', e => false)
      this.datetimepicker.on('dp.change', this.dateSelected)
    }
  }
</script>

<style lang="less" scoped>
  div{
    .calendar{
      display: none !important;
    }
  }
</style>



