<template lang="pug">
  .log-in
    button(type='button', data-toggle='modal', data-target='#LogInXs', @click='closeNav').seeTickets Log In
    #LogInXs.modal.animated.fadeInUpBig(role='dialog')
      transition(name="component-fade" mode="out-in")
        component(v-bind:is="view", @createAccount='showSignUp', @logIn='showLogIn', :translations='translations')
</template>

<script>
  import LogInXs from '@/components/modal/LogInXs'
  import SignUpXs from '@/components/modal/SignUpXs'
  import { mapGetters } from 'vuex'

  export default {
    props: [
      'translations'
    ],
    data () {
      return {
        logIn: true,
        view: 'v-a'
      }
    },
    computed: {
      ...mapGetters({
        defaultLang: 'defaultLanguage'
      })
    },
    components: {
      'v-a': LogInXs,
      'v-b': SignUpXs
    },
    methods: {
      closeNav () {
        window.jQuery('#mans-nav').css('margin-left', '-85%')
      },
      showSignUp () {
        this.view = 'v-b'
      },
      showLogIn () {
        this.view = 'v-a'
      }
    }
  }
</script>

<style lang="less" scoped>
   button{
    width: 116px;
    padding: 10px 24px;
    border: 1px solid #292929;
    text-align: center;
    text-transform: uppercase;
    transition: .3s;
    border-radius: 0;
    background-color: transparent;
    outline: none !important;
    &:hover {
      cursor: pointer;
    }
  }
  .modal-dialog{
    max-width: 402px;
    height: 100vh;
  }
  .modal-content{
    height: 100vh !important;
  }
  .modal-body{
    text-align: left;
  }
  .component-fade-enter-active, .component-fade-leave-active {
    transition: opacity .3s ease;
  }
  .component-fade-enter, .component-fade-leave-to
  /* .component-fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
   /* Small */
  @media(min-width:768px) and (max-width:991px){
    button{
      font-size: 12px;
      padding: 10px 22px;
    }
  }
</style>



