<template lang="pug">
  div
    .modal.fade(role='dialog', :id="'pay'")
      .modal-dialog
        .modal-close
          button(data-dismiss="modal") {{ translations.checkout_payment_close | tktranslate(lang) }}
            i.material-icons clear
        .modal-content
          .modal-content-wrapper
            .modal-header
              h3 {{ translations.checkout_payment_title | tktranslate(lang) }}
            .modal-body
              .row(v-if='order')
                .col-sm-4.col-xs-6(v-for='gateway in order.allowed_gateways')
                  .method-wrapper
                    .method-body
                      a(@click='pay(gateway)')
                        img(:src="'static/' + gateway.translations.logo.en").img-responsive
                      //p.fee +7.35 € fee
              p {{ translations.checkout_payment_paragraph | tktranslate(lang) }}
</template>

<script>
  import api from '@/services/TicketylOS/TicketylOS'
  import { mapGetters } from 'vuex'
  export default {
    props: ['translations', 'name'],
    computed: {
      ...mapGetters({
        lang: 'selectedLanguage',
        order: 'order'
      })
    },
    mounted () {
      /* window.jQuery('#paymentMethod' + this._uid).on('hidden.bs.modal', () => {
        this.$emit('paymentMethodClose')
      }) */
    },
    methods: {
      pay (gateway) {
        var params = {
          gateway: gateway.code,
          currency: 'eur',
          success_url: api.front_url + '/booking/' + this.order.code + '/success',
          cancel_url: api.front_url + '/booking/' + this.order.code + '/cancel'
        }
        api.gateway().pay(this.order.code, params)
      }
    }
  }
</script>

<style lang="less" scoped>
  .modal-close-mob{
    display: none;
  }
  .modal-dialog{
    width: 770px;
  }
  .modal-close{
    padding: 10px 0;
    text-align: right;
    button{
      padding: 0;
      font-size: 14px;
      text-transform: uppercase;
      color: #fff;
      border: none;
      outline: 0;
      background-color: transparent;
      .material-icons{
        font-size: 18px;
        vertical-align: text-bottom;
        padding-left: 4px;
      }
    }
  }
  h3{
    text-transform: capitalize;
  }
  p{
    text-transform: initial;
    text-align: center;
  }
  .method-wrapper{
    border: 1px solid #979797;
    margin-bottom: 24px;
    height: 137px;
    width: 100%;
    display: table;
    .method-body{
      position: relative;
      display: table-cell;
      vertical-align: middle;
    }
    cursor: pointer;
    &:hover{
      opacity: 1;
      border: 2px solid #292929;
      background-color: #fff;
    }
    img{
      height: 56px;
      width: auto;
      margin: 0 auto;
    }
  }
  .modal-body{
    padding: 20px 0;
  }
  .modal-content-wrapper{
    padding: 0 25px;
  }
  .modal-header{
    text-align: center;
    border-bottom: 1px solid #979797;
    padding: 24px 0;
    h3{
      margin: 0;
    }
  }
  .modal-content{
    background-color: #f7f7f7;
    border: none;
    border-radius: 0;
  }
  .fee{
    font-size: 12px;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    bottom: 12px;
  }
  @media(min-width:0px) and (max-width:767px){
    .modal-content{
      min-height: 100vh;
    }
    .modal-dialog{
      width: 100%;
    }
    .modal-body{
      padding-bottom: 64px;
    }
    .modal-close{
      box-shadow: 0 2px 10px 0 rgba(0,0,0,0.3);
      display: block;
      position: absolute;
      bottom: 0;
      width: 100%;
      background-color: #fff;
      text-align: center;
      z-index: 1;
      padding: 20px;
      button{
        color: #292929;
        border: none;
        background-color: transparent;
        padding: 0;
        text-transform: uppercase;
      }
    }
    .modal-content-wrapper{
      padding: 10px;
    }
    h3{
      margin: 0;
    }
    p{
      margin-bottom: 50px;
    }
  }
</style>



