<template lang="pug">
  .dropdown.account
    button#dLabels(type='button', data-toggle='dropdown', aria-haspopup='true', aria-expanded='false') {{$store.getters.selectedLanguage}}
      i.material-icons keyboard_arrow_down
    ul.dropdown-menu(aria-labelledby='dLabels')
      li
        span(v-for='l in langs', @click='currentLang(l)') {{l.code}}
</template>

<script>
  export default {
    name: 'Account',
    props: [
      'langs'
    ],
    methods: {
      currentLang (a) {
        this.$emit('input', a.code)
      }
    }
  }
</script>

<style lang="less" scoped>
.dropdown-menu{
  visibility: hidden;
  display: inline-block;
  opacity: 0;
  z-index: 2;
  padding: 10px 0;
  border-radius: 0;
  margin: 0;
  border: none;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
  min-width: 152px;
  top: 32px;
  left: 0;
  right: 0;
  margin-left: auto;
    li{
      span{
        text-transform: uppercase;
        letter-spacing: 0.5px;
        width: 100%;
        transition: 0.3s;
        display: block;
        text-align: right;
        padding: 10px;
        font-size: 14px;
        cursor: pointer;
        color: #292929;
        text-decoration: none;
          &:hover{
          background-color: #f7f7f7;
          text-decoration: none;
        }
      }
    }
  &:after{
    z-index: -1;
    position: absolute;
    right: 16px;
    top: -7px;
    width: 16px;
    height: 16px;
    content: '';
    background-color: #fff;
    transform: rotate(135deg);
    box-shadow: -1px 1px 1px -1px rgba(0, 0, 0, 0.2);
  }
}
.account {
  display: inline-block;
  color: black;
  button{
    text-transform: uppercase;
    font-size: 14px;
    margin-left: 16px;
    color: #292929;
    border: none;
    background-color: transparent;
    padding: 0;
    outline: none !important;
  }
  .material-icons{
    vertical-align: middle;
    font-size: 18px;
    padding-left: 8px;
  }
}
.open > .dropdown-menu {
  visibility: visible;
  display: block;
  opacity: 1;
  transition: all 0.1s linear;
  -webkit-transition: all 0.1s linear;
  top: 40px;
  z-index: 999999;
}
@media(min-width:0px) and (max-width:767px){
  .dropdown-menu{
    min-width: 120px !important;
    margin-left: 0;
    &:after{
      left: 16px;
    }
    li{
      span{
        text-align: left;
      }
    }
  }
  .account{
    button{
      margin-left: 0;
    }
  }
}
</style>



